import { Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
    BookingInput,
    BookingSolutionCancellationRS,
    DialogOptions,
    ExtraBooking,
    ExtrasXTypeBooking,
} from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { ExtraSelectModalComponent } from './extra-select-modal/extra-select-modal.component';

@Component({
  selector: 'app-extra-select',
  templateUrl: './extra-select.component.html',
  styleUrls: ['./extra-select.component.scss']
})
export class ExtraSelectComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() scroll: Subject<void>;
  @Input() set _prenotazioneDetails(value: BookingInput) {
    if (value != null && value.camerePax != null && value.extraSelected != null && value.tariffeTipiCamera != null && value.sistemazioni != null && value.vociAddebito != null) {
      this.prenotazioneDetails = value;
      this.selectableExtras = [];

      for (let camera of this.prenotazioneDetails.extraSelected) {
        for (let voceAddebito of camera) {
          let voceAddebitoIndex = this.selectableExtras.findIndex(x => ((x.voceAddebito == null && (voceAddebito == null || voceAddebito.voceAddebito == null))) || (x.voceAddebito != null && voceAddebito != null && voceAddebito.voceAddebito != null && x.voceAddebito.debitEntryId == voceAddebito.voceAddebito.debitEntryId));

          if (voceAddebitoIndex < 0) {
            this.selectableExtras.push(voceAddebito);
          } else {
            for (let extra of voceAddebito.extras) {
              if (this.selectableExtras[voceAddebitoIndex].extras.find(x => x.extra.extraId == extra.extra.extraId) == null) {
                this.selectableExtras[voceAddebitoIndex].extras.push(extra);
              }
            }
          }
        }
      }
    }
  }

  @Input() condizioniPrenotazione: string;
  @Input() cancellazioni: BookingSolutionCancellationRS[];
  @Input() from: string;
  @Input() to: string;
  @Input() nights: number;
  @Input() numeroPax: number;
  @Input() dayUse: boolean;
  @Input() prenotaSubject: Subject<void>;
  @Input() bulletPoints: string[];
  @Input() valuta: string;

  @Output() back: EventEmitter<any> = new EventEmitter();
  @Output() continue: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();

  @ViewChild('container') container: ElementRef;

  public prenotazioneDetails: BookingInput = new BookingInput();
  public selectableExtras: ExtrasXTypeBooking[] = [];
  public voceAddebitoOid: number = -1;
  public noExtras: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;

  constructor(public utility: UtilityService,
    public containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    if (this.prenotaSubject != null) {
      this.sub = this.prenotaSubject.subscribe(() => {
        this.voceAddebitoOid = -1;
        this.noExtras = false;
      });
    }

    this.sub1 = this.scroll.subscribe(() => {
      if (this.containerService.dimension.width < 992) {
        setTimeout(() => {
          if (this.container != null) {
            this.container.nativeElement.scrollIntoView({ behavior: 'smooth' });
          }
        }, 500);
      } else {
        this.containerService.scrollToTop();
      }
    });
  }

  onExtraAdd(extra: ExtraBooking, quantity: number) {
    if (this.prenotazioneDetails != null && this.prenotazioneDetails.camerePax != null && this.prenotazioneDetails.tariffeTipiCamera != null) {
      if (this.prenotazioneDetails.camerePax.length == 1) {
        extra.quantita = quantity;
        this.onChange.emit();
      } else if (this.prenotazioneDetails.camerePax.length > 1) {
        if (this.prenotazioneDetails.extraSelected != null && this.prenotazioneDetails.extraSelected.length > 0) {
          let extras: ExtraBooking[] = null;
          let voceAddebitoIndex = 0;
          let extraIndex = 0;

          let i = 0;

          for (let voceAddebito of this.selectableExtras) {

            let i2 = 0;

            for (let e of voceAddebito.extras) {

              if (e.extra.extraId == extra.extra.extraId) {
                extras = [];

                for (let camera of this.prenotazioneDetails.extraSelected) {
                  extras.push(camera[i] != null ? JSON.parse(JSON.stringify(camera[i].extras[i2])) : null);
                }

                voceAddebitoIndex = i;
                extraIndex = i2;

                break;
              }

              i2++;
            }

            if (extras != null) {
              break;
            }

            i++;
          }

          if (extras != null) {
            let dialogOptions = new DialogOptions();
            dialogOptions.panelClass = 'booking-dialog';

            if (this.containerService.dimension.width >= 992) {
              dialogOptions.width = "50vw";
            } else {
              dialogOptions.width = "80vw";
            }

            let ref = this.showModalDialog(ExtraSelectModalComponent, dialogOptions);
            let component = <ExtraSelectModalComponent>ref.componentInstance;

            component.extras = extras;
            component.tariffeTipiCamera = this.prenotazioneDetails.tariffeTipiCamera;
            component.nights = this.nights;
            component.camerePax = this.prenotazioneDetails.camerePax;
            component.valuta = this.valuta;
            component.dialogRef = ref;

            ref.afterClosed().subscribe((x: ExtraBooking[]) => {
              if (x != null && x.length > 0) {

                let ind = 0;

                for (let cameraExtra of x) {

                  if (cameraExtra != null) {
                    this.prenotazioneDetails.extraSelected[ind][voceAddebitoIndex].extras[extraIndex].quantita = cameraExtra.quantita;
                    this.prenotazioneDetails.extraSelected[ind][voceAddebitoIndex].extras[extraIndex].extra.giorni = cameraExtra.extra.giorni;
                  }

                  ind++;
                }

                this.onChange.emit();
              }
            });
          }
        }
      }
    }
  }

  onGiornoSelected(extra: ExtraBooking) {
    if (extra.quantita > 0) {
      this.onChange.emit();
    }
  }

  onFilter() {
    if (this.voceAddebitoOid != -1) {
      this.noExtras = true;

      let i = 0;

      for (let extrasXType of this.selectableExtras) {

        if (extrasXType.voceAddebito.debitEntryId == this.voceAddebitoOid) {
          this.noExtras = false;
          break;
        }

        i++;
      }
    } else {
      this.noExtras = false;
    }
  }

  ngOnDestroy() {
    if (this.sub != null) {
      this.sub.unsubscribe();
    }

    this.sub1.unsubscribe();
  }
}