import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { RichiesteAddComponent } from 'src/app/interfaces/richieste-add/richieste-add.component';
import { Cliente } from 'src/app/shared/models/bo.model';
import {
    DialogOptions,
    DisponibilitaCamereRS,
    GetCalendarioDisponibilitaCamereRQ,
    GetCalendarioDisponibilitaCamereRS,
} from 'src/app/shared/models/dto';
import { TipoVisualizzazioneGrigliaDisponibilita } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-room-calendar',
  templateUrl: './booking-room-calendar.component.html',
  styleUrls: ['./booking-room-calendar.component.scss']
})
export class BookingRoomCalendarComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() from: string;
  @Input() to: string;
  @Input() notti: number;
  @Input() oidAlbergo: number;
  @Input() oidPortale: number;
  @Input() lingua: string;
  @Input() dayUse: boolean;
  @Input() tipoVisualizzazioneGrigliaDisponibilita: TipoVisualizzazioneGrigliaDisponibilita;
  @Input() cellulare: string;
  @Input() email: string;
  @Input() valuta: string;

  @Output() onInitialized: EventEmitter<any> = new EventEmitter();

  @ViewChild('calendar') calendar: ElementRef;

  public disponibilita: DisponibilitaCamereRS[] = [];
  public tooltips: string[][] = [];
  public initialized: boolean = false;
  public rowsCount: number = 1;
  public dayCount: number = 30;
  public maxDayCount: number = 30;
  public dayCountInRow: number = 30;
  public calendarWidth: number;
  public sub: Subscription;
  public enumTipoVisualizzazioneGrigliaDisponibilita = TipoVisualizzazioneGrigliaDisponibilita;

  constructor(private apiService: ApiService,
    private translationService: LocalizationService,
    public utility: UtilityService,
    private containerService: ContainerService,
    private sessionService: SessionService) {
    super();
  }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      this.resizeCalendar();

      setTimeout(() => {
        if (this.calendar != null) {
          this.calendarWidth = this.calendar.nativeElement.offsetWidth;
        }
      }, 200);
    });

    this.getDisponibilita();
  }

  ngAfterViewInit() {
    this.resizeCalendar();
  }

  resizeCalendar() {
    if (this.containerService.dimension.width < 900) {
      this.rowsCount = 3;
      this.dayCount = 18;
    } else if (this.containerService.dimension.width < 1100) {
      this.rowsCount = 3;
      this.dayCount = 21;
    } else if (this.containerService.dimension.width < 1350) {
      this.rowsCount = 3;
      this.dayCount = 30;
    } else {
      this.rowsCount = 2;
      this.dayCount = 30;
    }

    this.dayCountInRow = Math.floor(this.dayCount / this.rowsCount);
  }

  getDisponibilita() {
    this.setIsLoading(true);
    this.initialized = false;
    let rq = new GetCalendarioDisponibilitaCamereRQ();
    rq.dal = this.getDate(this.from);
    rq.al = this.getDate(moment(this.from).add(this.maxDayCount - 1, 'day'));
    rq.oidPortale = this.oidPortale;
    rq.dayUse = this.dayUse;
    rq.lingua = this.lingua;

    this.apiService.getCalendarioDisponibilitaCamere(rq).subscribe((res: GetCalendarioDisponibilitaCamereRS) => {
      this.disponibilita = res.data.disponibilita;

      this.tooltips = [];

      let i = 0;

      for (let camera of this.disponibilita) {
        this.tooltips.push([]);

        for (let day of camera.disponibilita) {
          if (day.cssClass == 'limitations') {
            let ret: string = '';

            if (day.durataMinimaSoggiorno > 0) {
              ret = this.translationService.getTranslationSync('booking.durataMinimaSoggiorno', { value: day.durataMinimaSoggiorno });
            }

            if (day.durataMassimaSoggiorno > 0) {
              let durataMassimaString = this.translationService.getTranslationSync('booking.durataMassimaSoggiorno', { value: day.durataMassimaSoggiorno });
              
              if (!ret) {
                ret = durataMassimaString;
              } else {
                ret += '\n' + durataMassimaString;
              }
            }

            this.tooltips[i].push(ret);
          } else {
            this.tooltips[i].push(null);
          }
        }

        i++;
      }

      this.initialized = true;
      this.onInitialized.emit();

      setTimeout(() => {
        if (this.calendar != null) {
          this.calendarWidth = this.calendar.nativeElement.offsetWidth;
        }
      }, 200);

      this.setIsLoading(false);
    });
  }

  isMonthVisible(index: number, data: string): boolean {
    let date = moment(data);
    let day = date.date();
    let daysInMonth = date.daysInMonth();

    if (day == 1 && index < this.dayCountInRow - 3) {
      return true;
    }

    if (index == 0 && day < daysInMonth - 3 && index < this.dayCountInRow - 3) {
      return true;
    }

    return false;
  }

  inviaRichiesta() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = '80vw';
    } else {
      dialogOptions.width = '95vw';
    }

    dialogOptions.minHeight = '80vh';
    dialogOptions.maxHeight = '95vh';
    dialogOptions.panelClass = 'booking-dialog-no-padding';

    let ref = this.showModalDialog(RichiesteAddComponent, dialogOptions);
    let component = <RichiesteAddComponent>ref.componentInstance;
    component.dialogRef = ref;
    component.oidAlbergo = this.oidAlbergo;
    component.oidPortale = this.oidPortale;
    component.language = this.lingua;
    component.booking = true;

    if (this.sessionService.sessione != null && this.sessionService.sessione.clienteMySelf != null) {
      let cliente = new Cliente();
      cliente.nome = this.sessionService.sessione.clienteMySelf.nome;
      cliente.cognome = this.sessionService.sessione.clienteMySelf.cognome;
      cliente.email = this.sessionService.sessione.clienteMySelf.email;
      cliente.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
      component.cliente = cliente;
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}