import { SocialAuthService } from '@abacritt/angularx-social-login';
import { SocialUser } from '@abacritt/angularx-social-login/entities/social-user';
import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, Comune, Nazione } from 'src/app/shared/models/bo.model';
import {
    AutocompleteDataLoader,
    BookingExtraInput,
    BookingSolutionPaymentRS,
    CreatePrenotazioneExtraRQ,
    GetNazioniTradotteRQ,
    PaymentModel,
    VerificaCodicePromozioneRQ,
    VerificaCodicePromozioneRS,
    WebApiRequestFilter,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-extra-select-pagamento',
  templateUrl: './booking-extra-select-pagamento.component.html',
  styleUrls: ['./booking-extra-select-pagamento.component.scss']
})
export class BookingExtraSelectPagamentoComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() scroll: Subject<void>;
  @Input() bookingExtraInput: BookingExtraInput;
  @Input() modalitaPagamento: BookingSolutionPaymentRS[];
  @Input() lingua: string;
  @Input() oidAlbergo: number;
  @Input() oidPortale: number;
  @Input() uid: string;
  @Input() condizioniPrenotazione: string;
  @Input() descrizioneFinale: string;
  @Input() loginSubject: Subject<void>;
  @Input() recaptchaAttiva: boolean;
  @Input() isVisualizzaCondizioniPrenotazioneStep2: boolean;
  @Input() isAccettaContrattoPrenotazioneStep2: boolean;
  @Input() isVerificaCartaCredito: boolean;
  @Input() isVisualizzaCRO: boolean;
  @Input() condizioniContratto: string;
  @Input() isVisualizzaBottoniSocialLoginStep2: boolean;
  @Input() isCellulareObbligatorio: boolean;
  @Input() isRichiesteObbligatorie: boolean;
  @Input() verificaCodicePromozionaleSubject: Subject<void>;
  @Input() valuta: string;

  @Input() set _isAttivaSpedizioneBookingExtra(value: boolean) {
    if (value != null) {
      this.isAttivaSpedizioneBookingExtra = value;

      if (this.sessionService.isLoggedInMySelf()) {
        if (this.isAttivaSpedizioneBookingExtra) {
          this.indirizzo = this.sessionService.sessione.clienteMySelf.indirizzo;
          this.citta = this.sessionService.sessione.clienteMySelf.comuneString;

          if (this.sessionService.sessione.clienteMySelf.comune != null) {
            this.selectedComune = Object.assign({}, this.sessionService.sessione.clienteMySelf.comune);
          } else {
            let comune = new Comune();
            comune.nome = this.citta;
            this.selectedComune = comune;
          }

          this.cap = this.sessionService.sessione.clienteMySelf.cap;
          this.provincia = this.sessionService.sessione.clienteMySelf.provincia;
          this.nazione = this.sessionService.sessione.clienteMySelf.nazioneString;
          this.selectedNazione = this.sessionService.getNazioneTradotta(this.lingua);
          this.checkFormValidity();
        }
      }
    }
  };

  @Output() back: EventEmitter<any> = new EventEmitter();
  @Output() loginEvent: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() onTotaleChange: EventEmitter<number> = new EventEmitter();
  @Output() onPrenotazioneCompleted: EventEmitter<any> = new EventEmitter();

  @ViewChild('container') container: ElementRef;

  public cliente: Cliente = new Cliente();
  public formValid: boolean = false;
  public errorMessage: string = 'globals.messages.inserireDati';
  public noPaymentMethod: boolean = false;
  public totale: number;
  public buttonsDisabled: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public sub3: Subscription;
  public emailConferma: string;
  public codicePromozionale: string;
  public oidCodicePromozione: number;
  public verificaCodicePromozionaleSubscription: Subscription;
  public indirizzo: string;
  public citta: string;
  public cap: string;
  public nazione: string;
  public provincia: string;
  public selectedComune: Comune = new Comune();
  public dataLoaderComune: AutocompleteDataLoader;
  public selectedNazione: Nazione = new Nazione();
  public dataLoaderNazione: AutocompleteDataLoader;
  public isAttivaSpedizioneBookingExtra: boolean;
  public richieste: string;

  constructor(public utility: UtilityService,
    private apiService: ApiService,
    public containerService: ContainerService,
    private authService: SocialAuthService,
    public sessionService: SessionService) {
    super();
  }

  ngOnInit() {
    this.sub = this.authService.authState.subscribe((user: SocialUser) => {
      if (user != null && this.cliente != null) {
        this.cliente.nome = user.firstName;
        this.cliente.cognome = user.lastName;
        this.cliente.email = user.email;
        this.checkFormValidity();
      }
    });

    this.sub1 = this.loginSubject.subscribe(() => {
      if (this.sessionService.isLoggedInMySelf()) {
        if (this.cliente != null) {
          this.cliente.nome = this.sessionService.sessione.clienteMySelf.nome;
          this.cliente.cognome = this.sessionService.sessione.clienteMySelf.cognome;
          this.cliente.email = this.sessionService.sessione.clienteMySelf.email;
          this.cliente.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
          this.cliente.privacy = this.sessionService.sessione.clienteMySelf.privacy;
          this.cliente.newsletter = this.sessionService.sessione.clienteMySelf.newsletter;
        }

        if (this.isAttivaSpedizioneBookingExtra) {
          this.indirizzo = this.sessionService.sessione.clienteMySelf.indirizzo;
          this.citta = this.sessionService.sessione.clienteMySelf.comuneString;

          if (this.sessionService.sessione.clienteMySelf.comune != null) {
            this.selectedComune = Object.assign({}, this.sessionService.sessione.clienteMySelf.comune);
          } else {
            let comune = new Comune();
            comune.nome = this.citta;
            this.selectedComune = comune;
          }

          this.cap = this.sessionService.sessione.clienteMySelf.cap;
          this.provincia = this.sessionService.sessione.clienteMySelf.provincia;
          this.nazione = this.sessionService.sessione.clienteMySelf.nazioneString;
          this.selectedNazione = this.sessionService.getNazioneTradotta(this.lingua);
        }

        this.checkFormValidity();
      } else {
        this.cliente = new Cliente();
        this.indirizzo = null;
        this.citta = null;
        this.cap = null;
        this.provincia = null;
        this.nazione = null;
        this.selectedComune = new Comune();
        this.selectedNazione = new Nazione();
        this.checkFormValidity();
      }
    });

    this.sub3 = this.scroll.subscribe(() => {
      if (this.containerService.dimension.width < 992) {
        setTimeout(() => {
          if (this.container != null) {
            this.container.nativeElement.scrollIntoView({ behavior: 'smooth' });
          }
        }, 500);
      } else {
        this.containerService.scrollToTop();
      }
    });

    this.sub2 = this.verificaCodicePromozionaleSubject.subscribe(() => {
      if (this.oidCodicePromozione != null) {
        this.verificaCodicePromozionale(true);
      }
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.sessionService.isLoggedInMySelf()) {
        if (this.cliente != null) {
          this.cliente.nome = this.sessionService.sessione.clienteMySelf.nome;
          this.cliente.cognome = this.sessionService.sessione.clienteMySelf.cognome;
          this.cliente.email = this.sessionService.sessione.clienteMySelf.email;
          this.cliente.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
          this.cliente.privacy = this.sessionService.sessione.clienteMySelf.privacy;
          this.cliente.newsletter = this.sessionService.sessione.clienteMySelf.newsletter;
          this.checkFormValidity();
        }
      } else {
        this.cliente = new Cliente();
        this.indirizzo = null;
        this.citta = null;
        this.cap = null;
        this.provincia = null;
        this.nazione = null;
        this.selectedComune = new Comune();
        this.selectedNazione = new Nazione();
        this.checkFormValidity();
      }
    }, 200);
  }

  prenota(pagamento: PaymentModel) {
    this.setIsLoading(true);

    let rq = new CreatePrenotazioneExtraRQ();
    rq.oidPortale = this.oidPortale;
    rq.extra = this.bookingExtraInput.extraSelected;
    rq.cliente = this.cliente;
    rq.pagamento = pagamento;
    rq.lingua = this.lingua;
    rq.oidCodicePromozionale = this.oidCodicePromozione;
    rq.indirizzo = this.indirizzo;
    rq.citta = this.citta;
    rq.cap = this.cap;
    rq.nazione = this.nazione;
    rq.provincia = this.provincia;
    rq.totale = this.totale;
    rq.uid = this.uid;
    rq.richieste = this.richieste;

    this.apiService.createPrenotazioneExtra(rq).subscribe(() => {
      this.setIsLoading(false);

      this.showInfo('globals.messages.prenotazioneCompletata').then(() => {
        this.buttonsDisabled = true;
        this.onPrenotazioneCompleted.emit();
        this.containerService.scrollToTop();
      });
    });
  }

  checkFormValidity() {
    if (!!this.cliente.nome && !!this.cliente.cognome && !!this.cliente.email && (!this.isCellulareObbligatorio || !!this.cliente.cellulare) && (!this.isRichiesteObbligatorie || !!this.richieste) && (!this.isAttivaSpedizioneBookingExtra || (!!this.indirizzo && !!this.citta && !!this.cap && !!this.provincia && !!this.nazione))) {
      if (this.utility.validateEmail(this.cliente.email)) {
        if (this.cliente.email == this.emailConferma) {
          this.formValid = true;
        } else {
          this.errorMessage = 'booking.emailDiverse';
          this.formValid = false;
        }
      } else {
        this.errorMessage = 'globals.messages.emailNonValida';
        this.formValid = false;
      }
    } else {
      this.errorMessage = 'globals.messages.inserireDati';
      this.formValid = false;
    }
  }

  verificaCodicePromozionale(articoliChanged: boolean = false) {
    this.setIsLoading(true);

    if (this.verificaCodicePromozionaleSubscription != null) {
      this.verificaCodicePromozionaleSubscription.unsubscribe();
    }

    this.bookingExtraInput.extraSelected.forEach(x => x.extra.prezzoScontato = null);

    let rq = new VerificaCodicePromozioneRQ();
    rq.oidAlbergo = this.oidAlbergo;
    rq.oidPortale = this.oidPortale;
    rq.codice = this.codicePromozionale;
    rq.extras = this.bookingExtraInput.extraSelected;

    this.verificaCodicePromozionaleSubscription = this.apiService.verificaCodicePromozionale(rq).subscribe((res: VerificaCodicePromozioneRS) => {
      if (res.data.oidCodicePromozione != null && res.data.oidCodicePromozione > 0) {
        this.oidCodicePromozione = res.data.oidCodicePromozione;

        let i = 0;

        for (let extra of this.bookingExtraInput.extraSelected) {
          extra.extra.prezzoScontato = res.data.extras[i].extra.prezzoScontato;
          i++;
        }

        this.onChange.emit();

        if (!articoliChanged) {
          this.showInfo('booking.codiceApplicato', { value: res.data.sconto + ' ' + this.utility.getValuta(this.valuta, false) });
        }
      } else {
        this.resetCodicePromozionale();
        this.showError('booking.erroreCodice');
      }

      this.setIsLoading(false);
    });
  }

  resetCodicePromozionale() {
    this.codicePromozionale = null;
    this.oidCodicePromozione = null;
    this.bookingExtraInput.extraSelected.forEach(x => x.extra.prezzoScontato = null);
    this.onChange.emit();
  }

  onChangeEvent() {
    if (this.oidCodicePromozione != null) {
      this.verificaCodicePromozionale(true);
    } else {
      this.onChange.emit();
    }
  }

  onUpdateOptionsComune(rq: WebApiRequestFilter) {
    this.dataLoaderComune = new AutocompleteDataLoader(this.apiService.getComuniFilter(rq));
  }

  onComuneSelected(comune: Comune) {
    if (comune != null && comune.oid > 0) {
      this.citta = comune.nome;
      this.cap = comune.cap;
      this.provincia = comune.provincia;
      this.checkFormValidity();
    }
  }

  onUpdateOptionsNazione(nome: string) {
    let rq = new GetNazioniTradotteRQ();
    rq.lingua = this.lingua;
    rq.nome = nome;
    rq.addEmptyItem = false;

    this.dataLoaderNazione = new AutocompleteDataLoader(this.apiService.getNazioniTradotte(rq));
  }

  onNazioneSelected(nazione: Nazione) {
    if (nazione != null && nazione.oid > 0) {
      this.nazione = nazione.nomeItaliano;
      this.checkFormValidity();
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
    this.sub3.unsubscribe();
  }
}