import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Lingua } from 'src/app/shared/models/bo.model';
import { BookingReviewConfigRS, CheckPortaleRS, GetRecensioniRQ, WebApiResponse } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-blocco-recensioni',
  templateUrl: './blocco-recensioni.component.html',
  styleUrls: ['./blocco-recensioni.component.scss']
})
export class BloccoRecensioniComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: Subscription;
  public recensioni: BookingReviewConfigRS[] = [];
  public initialized: boolean = false;
  public lingua: string;

  constructor(private apiService: ApiService,
    private utility: UtilityService,
    private route: ActivatedRoute) {
    super();
  }

  ngOnInit() {
    this.sub = this.route.queryParamMap.subscribe(x => {
      let oidPortale = x.get('oidPortale');

      if (!!oidPortale && !isNaN(+oidPortale)) {
        this.apiService.checkPortale(+oidPortale).subscribe((res: CheckPortaleRS) => {
          if (res.data.success) {
            this.utility.setLingua(x.get('lingua')).then((lingua: Lingua) => {
              this.lingua = lingua?.codice;

              let rq = new GetRecensioniRQ();
              rq.oidPortale = +oidPortale;
              rq.lingua = this.lingua;

              this.apiService.getRecensioniPortale(rq).subscribe((rs: WebApiResponse) => {
                this.recensioni = rs.data;
                this.initialized = true;
              });
            });
          }
        });
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}