import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { TagCrm, TagCrmXLingua } from 'src/app/shared/models/bo.model';
import { GetMyPageRQ, GetMyPageRS, WebApiResponse } from 'src/app/shared/models/dto';
import { TestoMyPage } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-mypage-testo-edit',
  templateUrl: './mypage-testo-edit.component.html',
  styleUrls: ['./mypage-testo-edit.component.scss']
})
export class MypageTestoEditComponent extends BaseComponent implements OnInit {

  @Input() getMyPageRQ: GetMyPageRQ;
  @Input() oidPreventivo: number;
  @Input() oidTagCrm: number;
  @Input() testoMypage: TestoMyPage;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: TagCrmXLingua[] = [];
  public enumTestoMyPage = TestoMyPage;

  constructor(private apiService: ApiService, public sessionService: SessionService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    for (let lingua of this.lingue) {
      let tagCrm = new TagCrmXLingua();
      tagCrm.lingua = lingua;
      tagCrm.testoMyPage = this.testoMypage;
      tagCrm.tagCrm = new TagCrm(this.oidTagCrm);
      this.rq.push(tagCrm);
    }

    this.apiService.getTagCrmXLingua(this.oidTagCrm).subscribe((tagRS: WebApiResponse) => {
      for (let tagCrmXLingua of tagRS.data.filter(x => x.testoMyPage == this.testoMypage)) {
        if (tagCrmXLingua.lingua != null) {
          let index = this.rq.findIndex(x => x.lingua.oid == tagCrmXLingua.lingua.oid);

          if (index >= 0) {
            this.rq[index].testo = tagCrmXLingua.testo;
          }
        }
      }

      this.setIsLoading(false);
    });
  }

  save() {
    this.setIsLoading(true);

    if (this.oidTagCrm > 0) {
      this.apiService.setTagCrmXLingua(this.rq).subscribe(() => {
        if (this.testoMypage == TestoMyPage.TestoIniziale) {
          this.apiService.getPreventivo(this.oidPreventivo).subscribe((res: WebApiResponse) => {
            res.data.testoIniziale = this.rq.find(x => x.lingua.codice == (res.data.lingua != null ? res.data.lingua.codice : 'it')).testo;

            this.apiService.setPreventivo(res.data).subscribe(() => {
              this.download();
            });
          });
        } else {
          this.download();
        }
      });
    } else {
      if (this.testoMypage == TestoMyPage.TestoIniziale) {
        this.apiService.getPreventivo(this.oidPreventivo).subscribe((res: WebApiResponse) => {
          res.data.testoIniziale = this.rq.find(x => x.lingua.codice == (res.data.lingua != null ? res.data.lingua.codice : 'it')).testo;

          this.apiService.setPreventivo(res.data).subscribe(() => {
            this.download();
          });
        });
      } else {
        this.download();
      }
    }
  }

  download() {
    this.apiService.getMyPage(this.getMyPageRQ).subscribe((res: GetMyPageRS) => {
      this.dialogRef.close(res);
      this.setIsLoading(false);
    });
  }
}