import { Component, Input } from '@angular/core';
import { MyPageClienteRS } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-header',
  templateUrl: './mypage-header.component.html',
  styleUrls: ['./mypage-header.component.scss']
})
export class MypageHeaderComponent {

  @Input() url: string;
  @Input() urlLogo: string;
  @Input() indirizzo: string;
  @Input() cap: string;
  @Input() citta: string;
  @Input() provincia: string;
  @Input() telefono: string;
  @Input() urlIFrameGoogleMap: string;
  @Input() cliente: MyPageClienteRS;
  @Input() oidAlbergo: number;
  @Input() v2: boolean;

  constructor(public utility: UtilityService, public containerService: ContainerService) { }

  openSelfCheckIn() {
    window.open(window.location.origin + '/self-checkin?oidAlbergo=' + this.oidAlbergo + '&email=' + this.cliente.email/* + '&password=' + this.cliente.password*/);
  }
}