import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { RifiutaPreventivoMyPageRQ } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-mypage-rifiuta-preventivo',
  templateUrl: './mypage-rifiuta-preventivo.component.html',
  styleUrls: ['./mypage-rifiuta-preventivo.component.scss']
})
export class MypageRifiutaPreventivoComponent extends BaseComponent {

  @Input() oidPreventivo: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public note: string;

  constructor(private apiService: ApiService) { super(); }

  rifiuta() {
    this.setIsLoading(true);

    let rq = new RifiutaPreventivoMyPageRQ();
    rq.oidPreventivo = this.oidPreventivo;
    rq.note = this.note;

    this.apiService.rifiutaPreventivoMyPage(rq).subscribe(rs => {
      this.setIsLoading(false);

      if (rs.data) {
        this.showInfo('booking.my-page.emailInviata').then(() => {
          this.dialogRef.close(true);
        });
      } else {
        this.showError('interfaces.email-send.erroreInvioEmail').then(() => {
          this.dialogRef.close();
        });
      }
    });
  }
}