import { SelectionModel } from '@angular/cdk/collections';
import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Preventivo, Template } from 'src/app/shared/models/bo.model';
import { GetMyPageRQ, GetMyPageRS, GetTemplatesRS, WebApiRequestFilter, WebApiResponse } from 'src/app/shared/models/dto';
import { CriteriaOperator, FilterValueType, TipoTemplate } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-mypage-contenuti-edit',
  templateUrl: './mypage-contenuti-edit.component.html',
  styleUrls: ['./mypage-contenuti-edit.component.scss']
})
export class MypageContenutiEditComponent extends BaseComponent implements OnInit {

  @Input() getMyPageRQ: GetMyPageRQ;
  @Input() oidAlbergo: number;
  @Input() oidPreventivo: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: Preventivo = new Preventivo();

  public selectionContenuti = new SelectionModel<number>(true, []);
  public contenuti: Template[];

  constructor(private apiService: ApiService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.apiService.getPreventivo(this.oidPreventivo).subscribe((res: WebApiResponse) => {
      this.rq = res.data;

      this.apiService.getTemplatesFilter(WebApiRequestFilter.fromFilter('tipoTemplate', CriteriaOperator.Equal, TipoTemplate.PreventivoContenuto.toString(), FilterValueType.Number), this.oidAlbergo).subscribe((templatesRS: GetTemplatesRS) => {
        this.contenuti = templatesRS.data.templates.map(x => x.template);

        this.selectionContenuti = new SelectionModel<number>(true, []);

        if (!!this.rq.oidContenutiMyPage) {
          let oidContenuti = this.rq.oidContenutiMyPage.split(';');

          for (let oid of oidContenuti) {
            if (oid != '' && !isNaN(+oid) && this.contenuti.findIndex(x => x.oid == +oid) != -1) {
              this.selectionContenuti.toggle(+oid);
            }
          }
        }

        this.setIsLoading(false);
      });
    });
  }

  save() {
    this.setIsLoading(true);

    this.rq.oidContenutiMyPage = this.selectionContenuti.selected.toString().replace(/,/g, ';');

    this.apiService.setPreventivo(this.rq).subscribe(() => {
      this.apiService.getMyPage(this.getMyPageRQ).subscribe((res: GetMyPageRS) => {
        this.dialogRef.close(res);
        this.setIsLoading(false);
      });
    });
  }

  isAllSelectedContenuti() {
    const numSelected = this.selectionContenuti.selected.length;
    const numRows = this.contenuti.length;
    return numSelected == numRows;
  }

  masterToggleContenuti() {
    this.isAllSelectedContenuti() ? this.selectionContenuti.clear() : this.contenuti.forEach(row => this.selectionContenuti.select(row.oid));
  }
}
