import { Injectable, OnDestroy } from '@angular/core';
import { NgScrollbar } from 'ngx-scrollbar';
import { Subscription } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MypageService implements OnDestroy {

  public scrollbarRef: NgScrollbar;
  public scrollTop: number = 0;
  public sub: Subscription;

  constructor() { }

  setScrollbarRef(scrollbarRef: NgScrollbar) {
    this.scrollbarRef = scrollbarRef;

    this.sub = this.scrollbarRef.verticalScrolled.subscribe((e: Event) => {
      this.scrollTop = (e.target as any).scrollTop;
    });
  }

  scrollToTop(smooth: boolean = true) {
    this.scrollbarRef.scrollTo({ duration: smooth ? 400 : 0 });
  }

  ngOnDestroy() {
    if (this.sub != null) {
      this.sub.unsubscribe();
    }
  }
}