import { animate, state, style, transition, trigger } from '@angular/animations';
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewRef,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingSolutionBannerRS } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';

@Component({
  selector: 'app-booking-banner',
  templateUrl: './booking-banner.component.html',
  styleUrls: ['./booking-banner.component.scss'],
  animations: [
    trigger('fade', [
      state('opaque', style({
        opacity: 1
      })),
      state('transparent', style({
        opacity: 0
      })),
      transition('opaque => transparent', [
        animate('700ms ease-out')
      ]),
      transition('transparent => opaque', [
        animate('700ms ease-in')
      ])
    ])
  ]
})
export class BookingBannerComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() set _banner(value: BookingSolutionBannerRS[]) {
    if (value != null) {
      this.banner = value.slice(0, 10);
      this.currentBanner = 0;
      this.state = 'transparent';
      this.toggleState();
      this.stopInterval();
      this.startInterval();
    }
  };

  @Input() modal: boolean = false;

  @Output() onCloseClick: EventEmitter<any> = new EventEmitter();

  public currentBanner: number = 0;
  public banner: BookingSolutionBannerRS[] = [];
  public interval: NodeJS.Timer;
  public state: string = 'transparent';
  public operation: string;
  public goToBannerIndex: number;
  public disableButtons: boolean = false;
  public sub: Subscription;

  @ViewChild('bannersContainer') bannersContainer: ElementRef;

  constructor(private cdRef: ChangeDetectorRef, private containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      this.updateScroll();
    });
  }

  startInterval() {
    if (this.banner.length > 1) {
      this.interval = setInterval(this.changeBanner.bind(this), 7000);
    }
  }

  stopInterval() {
    if (this.interval != null) {
      clearInterval(this.interval);
    }
  }

  nextBanner() {
    if (this.currentBanner < this.banner.length - 1) {
      this.currentBanner++;
    } else {
      this.currentBanner = 0;
    }

    this.updateScroll();
  }

  showBanner() {
    this.currentBanner = this.goToBannerIndex;
    this.updateScroll();
    this.goToBannerIndex = null;
  }

  toggleState() {
    this.state = this.state == 'opaque' ? 'transparent' : 'opaque';

    if (!(this.cdRef as ViewRef).destroyed) {
      this.cdRef.detectChanges();
    }
  }

  onAnimationDone(toState: string) {
    if (toState == 'transparent') {
      if (this.goToBannerIndex != null) {
        this.showBanner();
        this.toggleState();
      } else if (this.operation == 'next') {
        this.nextBanner();
        this.toggleState();
      }
    } else if (toState == 'opaque') {
      this.disableButtons = false;
    }
  }

  updateScroll() {
    this.bannersContainer.nativeElement.scroll({ top: 0, left: this.currentBanner * this.bannersContainer.nativeElement.offsetWidth });
  }

  changeBanner() {
    this.operation = 'next';
    this.toggleState();
  }

  goToBanner(index: number) {
    this.goToBannerIndex = index;
    this.toggleState();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    clearInterval(this.interval);
  }
}