import { Component, Input, OnInit } from '@angular/core';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-error-page',
  templateUrl: './booking-error-page.component.html',
  styleUrls: ['./booking-error-page.component.scss']
})
export class BookingErrorPageComponent implements OnInit {

  @Input() errorMessage: string;

  public backgroundImage: string;

  constructor(private utility: UtilityService) { }

  ngOnInit() {
    this.backgroundImage = this.utility.getRandomImage();
  }
}