import { Component, EventEmitter, Input, Output } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingSolutionDataAlternativaRS } from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-non-disponibile',
  templateUrl: './booking-non-disponibile.component.html',
  styleUrls: ['./booking-non-disponibile.component.scss']
})
export class BookingNonDisponibileComponent extends BaseComponent {

  @Input() set _dateAlternative(value: BookingSolutionDataAlternativaRS[]) {
    if (value != null) {
      this.dateAlternative = value;

      this.nights = [];

      for (let data of this.dateAlternative) {
        this.nights.push(Math.max(moment(data.partenza).diff(moment(data.arrivo), 'days'), 1));
      }
    }
  };

  @Input() valuta: string;

  @Output() dateSelected: EventEmitter<BookingSolutionDataAlternativaRS> = new EventEmitter();

  public dateAlternative: BookingSolutionDataAlternativaRS[];
  public nights: number[] = [];

  constructor(public utility: UtilityService) {
    super();
  }
}