import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingReviewConfigRS, MyPageRecensioneRS } from 'src/app/shared/models/dto';
import { FonteRecensione, ImmagineFonteRecensione } from 'src/app/shared/models/enum';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import clip from 'text-clipper';

@Component({
  selector: 'app-recensione',
  templateUrl: './recensione.component.html',
  styleUrls: ['./recensione.component.scss']
})
export class RecensioneComponent extends BaseComponent implements OnInit {

  @Input() recensione: MyPageRecensioneRS | BookingReviewConfigRS;

  public enumFonteRecensione = FonteRecensione;
  public enumImmagineFonteRecensione = ImmagineFonteRecensione;
  public shortTextVisible: boolean = false;
  public shortText: string;

  constructor(public utility: UtilityService, private localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    if (this.recensione.limiteCaratteri > 0) {
      this.shortText = clip(this.recensione.descrizione, this.recensione.limiteCaratteri, { html: true, breakWords: true });

      let index = this.shortText.lastIndexOf('');

      if (index >= 0) {
        this.shortText = this.shortText.slice(0, index) + '<a class="continuaLink" href="#" onclick="event.preventDefault()">' + this.localizationService.getTranslationSync('booking.more') + '</a>' + this.shortText.slice(index + 1);
        this.shortTextVisible = true;
      }
    }
  }
}