import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { DettaglioSupplementoBooking, ExtraBooking, GiornoDettaglioSupplementoBooking } from 'src/app/shared/models/dto';
import { TipoSupplemento } from 'src/app/shared/models/enum';

@Component({
  selector: 'app-articolo-dialog',
  templateUrl: './articolo-dialog.component.html',
  styleUrls: ['./articolo-dialog.component.scss']
})
export class ArticoloDialogComponent extends BaseComponent implements OnInit {

  @Input() numeroPax: number;
  @Input() articolo: ExtraBooking;
  @Input() valuta: string;

  public giorni: GiornoDettaglioSupplementoBooking[] = [];

  constructor() { super(); }

  ngOnInit() {
    for (let giorno of this.articolo.extra.giorni.filter(x => x.selected)) {
      let g = new GiornoDettaglioSupplementoBooking();
      g.data = giorno.data;
      g.dettagli = [];

      if (this.articolo.extra.tipo == TipoSupplemento.Persona && this.numeroPax != null) {
        for (let i = 0; i < this.articolo.quantita; i++) {
          let cpIndex = i % this.numeroPax;

          let dettaglio = g.dettagli[cpIndex];

          if (dettaglio == null) {
            dettaglio = new DettaglioSupplementoBooking();
            dettaglio.indiceComponente = cpIndex + 1;
            dettaglio.quantita = 0;
            g.dettagli.push(dettaglio);
          }

          dettaglio.quantita++;
          dettaglio.prezzo = (giorno.prezzoScontato > 0 ? giorno.prezzoScontato : giorno.prezzo) * dettaglio.quantita;
        }
      } else {
        let dettaglio = new DettaglioSupplementoBooking();
        dettaglio.quantita = this.articolo.quantita;
        dettaglio.prezzo = (giorno.prezzoScontato > 0 ? giorno.prezzoScontato : giorno.prezzo) * dettaglio.quantita;
        g.dettagli.push(dettaglio);
      }

      this.giorni.push(g);
    }
  }
}