import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ClienteUnsubscribe, GetUnsubscribeRQ, GetUnsubscribeRS, PortaleUnsubscribe } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-unsubscribe',
  templateUrl: './unsubscribe.component.html',
  styleUrls: ['./unsubscribe.component.scss']
})
export class UnsubscribeComponent extends BaseComponent implements OnInit, OnDestroy {

  public cliente: ClienteUnsubscribe = new ClienteUnsubscribe();
  public portale: PortaleUnsubscribe = new PortaleUnsubscribe();
  public initialized: boolean = false;
  public sub: Subscription;
  public backgroundImage: string;
  public email: string;

  constructor(private router: Router,
    private route: ActivatedRoute,
    private apiService: ApiService,
    private utility: UtilityService,
    private containerService: ContainerService,
    private deviceService: DeviceDetectorService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('unsubscribe-scrolling');

    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {

      let uid = x.get('uid');

      if (!!uid) {
        let rq = new GetUnsubscribeRQ();
        rq.uid = uid;
        rq.isMobile = this.deviceService.isMobile();

        this.apiService.getUnsubscribe(rq).subscribe((res: GetUnsubscribeRS) => {
          if (res.data != null && res.data.cliente != null) {
            this.cliente = res.data.cliente;

            if (res.data.portale != null) {
              this.portale = res.data.portale;
            }

            this.utility.setLingua(this.cliente.lingua).then(() => {
              this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.initialized = true;
                this.setIsLoading(false);
              });
            });
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  unsubscribe() {
    if (this.email == this.cliente.email) {
      this.setIsLoading(true);

      this.apiService.newsletterUnsubscribe(this.cliente.oid).subscribe(() => {
        this.setIsLoading(false);

        this.showInfo('booking.unsubscribe.disiscrizioneEffettuata').then(() => {
          this.cliente.isSubscribed = false;
        });
      });
    } else {
      this.showError('booking.unsubscribe.emailNonCorrispondente');
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}