import { AfterViewInit, Component, ElementRef, OnDestroy, ViewChild } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MypageExtraComponent } from '../mypage-extra/mypage-extra.component';

@Component({
  selector: 'app-mypage-extra-v2',
  templateUrl: './mypage-extra-v2.component.html',
  styleUrls: ['./mypage-extra-v2.component.scss']
})
export class MypageExtraV2Component extends MypageExtraComponent implements AfterViewInit, OnDestroy {

  public imageWidth: number = 0;
  public hideImage: boolean = false;
  public updateImageSize: Subject<void> = new Subject();
  public initializedImage: boolean = false;
  public sub: Subscription;

  @ViewChild('imageContainer') imageContainer: ElementRef;

  constructor(public utility: UtilityService, public containerService: ContainerService) {
    super(utility, containerService);
  }

  ngAfterViewInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      this.hideImage = true;

      setTimeout(() => {
        this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth);
        this.hideImage = false;
        this.updateImageSize.next();
      }, 200);
    });

    setTimeout(() => {
      this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth);
      this.initializedImage = true;
    }, 200);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}