import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import moment from 'moment';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ComponenteXPrenotazione, Comune, Lingua } from 'src/app/shared/models/bo.model';
import {
  AlbergoMySelf,
  AlbergoRoomingList,
  AutocompleteDataLoader,
  DialogOptions,
  GetRoomingListRQ,
  GetRoomingListRS,
  PortaleRoomingList,
  PrenotazioneRoomingList,
  setDatiCheckInMultipleRQ,
  WebApiRequestFilter,
} from 'src/app/shared/models/dto';
import { Sesso, StatoPrenotazione, TipoComponentePrenotazione } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { RoomingListCheckinDialogComponent } from '../rooming-list-checkin-dialog/rooming-list-checkin-dialog.component';

@Component({
  selector: 'app-rooming-list',
  templateUrl: './rooming-list.component.html',
  styleUrls: ['./rooming-list.component.scss']
})
export class RoomingListComponent extends BaseComponent implements OnInit, OnDestroy {

  public initialized: boolean = false;
  public portale: PortaleRoomingList = new PortaleRoomingList();
  public albergo: AlbergoRoomingList = new AlbergoRoomingList();
  public sub: Subscription;
  public password: string;
  public passwordInserted: boolean = false;
  public prenotazioni: PrenotazioneRoomingList[] = [];
  public selectedComuni: Comune[][] = [];
  public comuneDataLoaders: AutocompleteDataLoader[][] = [];
  public enumStatoPrenotazione = StatoPrenotazione;
  public enumTipoComponentePrenotazione = TipoComponentePrenotazione;
  public enumSesso = Sesso;
  public backgroundImage: string;
  public lingua: string;

  public dataNascitaMinDate: string = this.getDate(moment().subtract(100, 'years'));
  public dataNascitaMaxDate: string = this.getDate(moment());

  constructor(private containerService: ContainerService,
    private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router,
    private deviceService: DeviceDetectorService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('rooming-list-scrolling');

    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {

      let uid = x.get('uid');

      if (!!uid) {
        let rq = new GetRoomingListRQ();
        rq.uid = uid;
        rq.isMobile = this.deviceService.isMobile();

        this.apiService.getRoomingList(rq).subscribe((res: GetRoomingListRS) => {
          if (res.data.roomingList != null) {
            this.password = res.data.password;
            this.prenotazioni = res.data.prenotazioni;
            this.passwordInserted = !this.password;

            if (res.data.albergo != null) {
              this.albergo = res.data.albergo;
            }

            if (res.data.portale != null) {
              this.portale = res.data.portale;
            }

            let i = 0;

            for (let prenotazione of this.prenotazioni) {
              this.selectedComuni.push([]);
              this.comuneDataLoaders.push([]);

              for (let datiCheckIn of prenotazione.ospiti) {
                if (datiCheckIn.comuneNascita != null && datiCheckIn.comuneNascita.oid > 0) {
                  this.selectedComuni[i].push(Object.assign({}, datiCheckIn.comuneNascita));
                } else {
                  this.selectedComuni[i].push(new Comune());
                }
              }

              i++;
            }

            this.utility.setLingua(res.data.lingua).then((lingua: Lingua) => {
              this.lingua = lingua?.codice;

              this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.initialized = true;
              });
            });

            this.setIsLoading(false);
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  checkPassword(password: string) {
    if (password == this.password) {
      this.passwordInserted = true;
    } else {
      this.showError('booking.rooming-list.passwordErrata');
    }
  }

  onUpdateOptionsComuneNascita(prenotazioneIndex: number, datiIndex: number, rq: WebApiRequestFilter) {
    this.comuneDataLoaders[prenotazioneIndex][datiIndex] = new AutocompleteDataLoader(this.apiService.getComuniFilter(rq));
  }

  save() {
    this.setIsLoading(true);

    let rq = new setDatiCheckInMultipleRQ();
    rq.lingua = this.lingua;
    rq.componenti = [];

    for (let prenotazione of this.prenotazioni) {
      for (let ospite of prenotazione.ospiti) {
        ospite.isCheckInOnlineEseguito = true;
        ospite.isAggiornaSuWelcome = true;
        ospite.dataNascita = this.getDate(ospite.dataNascita);
        rq.componenti.push(ospite);
      }
    }

    this.apiService.setDatiCheckInMultiple(rq).subscribe(() => {
      this.setIsLoading(false);

      this.showInfo('globals.messages.datiSalvati').then(() => {
        window.location.reload();
      });
    });
  }

  openCheckIn(iPrenotazione: number, iDati: number) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.panelClass = 'booking-dialog';

    let dialog = this.showModalDialog(RoomingListCheckinDialogComponent, dialogOptions);
    let component = <RoomingListCheckinDialogComponent>dialog.componentInstance;
    component.albergo = new AlbergoMySelf(this.albergo.oid);
    component.isVisualizzaTuttiDatiSelfCheckIn = this.portale.isVisualizzaTuttiDatiSelfCheckIn;
    component.isBloccaModificaDatiCheckIn = this.portale.isBloccaModificaDatiCheckIn;
    component.model = Object.assign({}, this.prenotazioni[iPrenotazione].ospiti[iDati]);
    component.lingua = this.lingua;
    component.orarioDiArrivoMinimo = this.portale.orarioDiArrivoMinimo;
    component.orarioDiArrivoMassimo = this.portale.orarioDiArrivoMassimo;
    component.dialogRef = dialog;

    dialog.afterClosed().subscribe((x: ComponenteXPrenotazione) => {
      if (x != null) {
        this.prenotazioni[iPrenotazione].ospiti[iDati] = x;
      }
    });
  }

  dataNascitaChange(componente: ComponenteXPrenotazione) {
    if (!!componente.dataNascita) {
      let dataNascita = moment(componente.dataNascita).startOf('day');

      if (dataNascita.isBefore(moment(this.dataNascitaMinDate)) || dataNascita.isAfter(moment(this.dataNascitaMaxDate))) {
        componente.dataNascita = null;
        this.showError('booking.rooming-list.formatoDataNonValido');
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}
