import { SocialAuthService, SocialUser } from '@abacritt/angularx-social-login';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { GoogleTagManagerService } from 'angular-google-tag-manager';
import { Observable, Subject, Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente } from 'src/app/shared/models/bo.model';
import {
  BookingConfigAlbergoRS,
  BookingConfigPortaleRS,
  BookingInput,
  BookingSolutionCancellationRS,
  BookingSolutionPaymentRS,
  BookingTravelReasonConfigRS,
  CreatePrenotazioneRQ,
  CreatePrenotazioneRS,
  GetNazioniTradotteRQ,
  PaymentModel,
  PrenotazioneDetailsBooking,
  PrenotazioneDetailsOutput,
  PrenotazioneDetailsRoom,
  VerificaVoucherRQ,
  VerificaVoucherRS,
  WebApiResponseFilter,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

declare let ga;
declare let fbq;

@Component({
  selector: 'app-prenotazione-details',
  templateUrl: './prenotazione-details.component.html',
  styleUrls: ['./prenotazione-details.component.scss']
})
export class PrenotazioneDetailsComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() scroll: Subject<void>;
  @Input() set _prenotazioneDetails(value: BookingInput) {
    if (value != null && value.camerePax != null && value.extraSelected != null && value.tariffeTipiCamera != null && value.sistemazioni != null) {
      this.prenotazioneDetails = value;

      this.modalitaPagamento = [];

      let paymentMethods: BookingSolutionPaymentRS[] = [];

      for (let tariffaTipoCamera of value.tariffeTipiCamera) {
        paymentMethods = paymentMethods.concat(tariffaTipoCamera.tariffa.modalitaPagamento);
      }

      for (let paymentMethod of paymentMethods) {
        if (this.modalitaPagamento.findIndex(x => x.paymentId == paymentMethod.paymentId) == -1 && paymentMethods.filter(x => x.paymentId == paymentMethod.paymentId).length == value.tariffeTipiCamera.length) {
          this.modalitaPagamento.push(paymentMethod);
        }
      }
    }
  };

  @Input() set _lingua(value: string) {
    if (!!value) {
      this.lingua = value;

      if (this.sessionService.isLoggedInMySelf() && this.details != null) {
        this.details.nazione = this.sessionService.getNazioneTradotta(this.lingua);
      }
    }
  };

  @Input() albergo: BookingConfigAlbergoRS;
  @Input() portale: BookingConfigPortaleRS;
  @Input() condizioniPrenotazione: string;
  @Input() condizioniContratto: string;
  @Input() cancellazioni: BookingSolutionCancellationRS[];
  @Input() from: string;
  @Input() to: string;
  @Input() nights: number;
  @Input() numeroPax: number;
  @Input() dayUse: boolean;
  @Input() descrizioneFinale: string;
  @Input() loginSubject: Subject<void>;
  @Input() bulletPoints: string[];
  @Input() gtmService: GoogleTagManagerService;
  @Input() allModalitaPagamento: BookingSolutionPaymentRS[];
  @Input() oidCodicePromozione: number;
  @Input() oidMotivoViaggio: number;
  @Input() numeroVoucher: number;
  @Input() isGoogleHotelsTravel: boolean;
  @Input() isTrivago: boolean;
  @Input() isRichiediInformazioniOspiti: boolean;
  @Input() motiviViaggio: BookingTravelReasonConfigRS[];
  @Input() bookingSolutionUid: string;

  @Output() back: EventEmitter<any> = new EventEmitter();
  @Output() loginEvent: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() onTotaleChange: EventEmitter<number> = new EventEmitter();
  @Output() onPrenotazioneCompleted: EventEmitter<any> = new EventEmitter();

  @ViewChild('container') container: ElementRef;

  public lingua: string;
  public prenotazioneDetails: BookingInput;
  public details: PrenotazioneDetailsBooking = new PrenotazioneDetailsBooking();
  public rooms: PrenotazioneDetailsRoom[] = [];
  public totale: number = 0;
  public totaleScontato: number = 0;
  public modalitaPagamento: BookingSolutionPaymentRS[] = [];
  public cliente: Cliente;
  public formValid: boolean = false;
  public errorMessage: string = 'globals.messages.inserireDati';
  public noPaymentMethod: boolean = false;
  public buttonsDisabled: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public oraArrivo: string;
  public sistemazioni: boolean = false;
  public emailConferma: string;
  public voucher: string;
  public oidVoucher: number;
  public primoOspiteUpdated: boolean = false;
  public privacy: boolean = false;
  public newsletter: boolean = false;

  constructor(public utility: UtilityService,
    private apiService: ApiService,
    public containerService: ContainerService,
    private authService: SocialAuthService,
    public sessionService: SessionService,
    public localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    this.sub = this.authService.authState.subscribe((user: SocialUser) => {
      if (user != null && this.details != null) {
        this.details.nome = user.firstName;
        this.details.cognome = user.lastName;
        this.details.email = user.email;
        this.updateInformazioniPrimoOspite(true);
        this.resetVoucher();
        this.checkFormValidity();
      }
    });

    this.sub1 = this.loginSubject.subscribe(() => {
      if (this.sessionService.isLoggedInMySelf() && this.details != null) {
        this.details.nome = this.sessionService.sessione.clienteMySelf.nome;
        this.details.cognome = this.sessionService.sessione.clienteMySelf.cognome;
        this.details.email = this.sessionService.sessione.clienteMySelf.email;
        this.details.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
        this.details.nazione = this.sessionService.getNazioneTradotta(this.lingua);
        this.privacy = this.sessionService.sessione.clienteMySelf.privacy;
        this.newsletter = this.sessionService.sessione.clienteMySelf.newsletter;
        this.updateInformazioniPrimoOspite(true);
        this.resetVoucher();
        this.checkFormValidity();
      }
    });

    this.sub2 = this.scroll.subscribe(() => {
      if (this.containerService.dimension.width < 992) {
        setTimeout(() => {
          if (this.container != null) {
            this.container.nativeElement.scrollIntoView({ behavior: 'smooth' });
          }
        }, 500);
      } else {
        this.containerService.scrollToTop();
      }
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.sessionService.isLoggedInMySelf() && this.details != null) {
        this.details.nome = this.sessionService.sessione.clienteMySelf.nome;
        this.details.cognome = this.sessionService.sessione.clienteMySelf.cognome;
        this.details.email = this.sessionService.sessione.clienteMySelf.email;
        this.details.cellulare = this.sessionService.sessione.clienteMySelf.cellulare;
        this.privacy = this.sessionService.sessione.clienteMySelf.privacy;
        this.newsletter = this.sessionService.sessione.clienteMySelf.newsletter;
        this.updateInformazioniPrimoOspite();
        this.resetVoucher();
        this.checkFormValidity();
      }
    }, 200);
  }

  prenota(pagamento: PaymentModel) {
    if (!pagamento.isPrenotazioneCreata) {
      this.setIsLoading(true);

      let rq = new CreatePrenotazioneRQ();
      rq.arrivo = this.from;
      rq.partenza = this.to;
      rq.oidPortale = this.portale.oid;
      rq.camere = this.rooms;
      rq.cliente = this.details;
      rq.pagamento = pagamento;
      rq.oraArrivo = this.oraArrivo;
      rq.lingua = this.lingua;
      rq.oidCodicePromozione = this.oidCodicePromozione;
      rq.oidMotivoViaggio = this.oidMotivoViaggio;
      rq.oidVoucher = this.oidVoucher;
      rq.isGoogleHotelsTravel = this.isGoogleHotelsTravel;
      rq.isTrivago = this.isTrivago;
      rq.bookingSolutionUid = this.bookingSolutionUid;

      this.apiService.createPrenotazione(rq).subscribe((rs: CreatePrenotazioneRS) => {
        if (rs.errori == null || rs.errori.length == 0) {
          this.onPrenotazioneCreata(rs.data.transazione != null ? rs.data.transazione.oid : rs.data.bookingIdGA, rs.data.bookingCode);
        }
      });
    } else {
      this.onPrenotazioneCreata(pagamento.oidTransazione, pagamento.codicePrenotazione);
    }
  }

  onPrenotazioneCreata(bookingIdGA: number, bookingCode: string) {
    if (this.gtmService != null) {
      this.gtmService.pushTag({
        event: "new_booking",
        booking: {
          hotelId: this.albergo.codice,
          hotelName: this.albergo.nome,
          portalId: this.portale.oid,
          portalNome: this.portale.nome,
          transactionId: bookingIdGA,
          bookingCode: bookingCode,
          currencyCode: 'EUR',
          rooms: this.rooms.length,
          roomNights: this.nights,
          totalAmount: this.totale,
          totalAmountTax: this.totale,
          checkInDate: this.from,
          checkOutDate: this.to,
          lingua: this.lingua
        }
      });
    }

    if (!!this.portale.googleUA) {
      setTimeout(() => {
        let transaction = {
          'id': bookingIdGA,    // ID Transazione (in genere corrisponde all'ID della transazione dato dal Booking Engine)
          'name': this.albergo.nome,   // Nome della camera o tariffa applicata 
          'affiliation': this.albergo.nome,   // Nome della camera o tariffa applicata 
          'revenue': this.totale,      // Importo totale della prenotazione.
          'currency': 'EUR'            // Valuta 
        };

        ga('ecommerce:addTransaction', transaction);

        if (this.rooms != null) {
          this.rooms.forEach(element => {
            ga('ecommerce:addItem', {
              'id': bookingIdGA,                     // Transaction ID. Required.
              'name': element.tariffa.descrizione,    // Product name. Required.
              'category': element.tipoCamera.nome,         // Category or variation.
              'price': element.tariffa.prezzoScontato,                 // Unit price.
              'quantity': '1'                   // Quantity.
            });
          });
        }

        ga('ecommerce:send');
      }, 2000);
    }

    if (!!this.portale.facebookPixel) {
      setTimeout(() => {
        fbq('track', 'Purchase', {
          value: this.totale,
          currency: 'EUR',
          content_type: this.portale.nome
        });
      }, 2000);
    }

    this.setIsLoading(false);

    this.showInfo('globals.messages.prenotazioneCompletata').then(() => {
      this.buttonsDisabled = true;
      this.onPrenotazioneCompleted.emit();
      this.containerService.scrollToTop();
    });
  }

  checkFormValidity() {
    if (!!this.details.nome && !!this.details.cognome && !!this.details.email && (!this.portale.isCellulareObbligatorio || !!this.details.cellulare) && (!this.portale.isRichiesteObbligatorie || !!this.details.richieste) && (!this.portale.isOraArrivoObbligatoria || !!this.oraArrivo) && (!this.portale.isSistemazioneObbligatoria || !this.sistemazioni || this.rooms.every(x => x.oidSistemazione > 0)) && (!this.portale.isNazioneObbligatoria || (this.details.nazione != null && this.details.nazione.oid > 0)) && this.rooms.every(x => (!x.cliente.nome && !x.cliente.cognome && !x.cliente.email) || (!!x.cliente.nome && !!x.cliente.cognome && !!x.cliente.email))) {
      if (this.utility.validateEmail(this.details.email)) {
        if (this.details.email == this.emailConferma) {
          this.formValid = true;
          this.cliente = new Cliente();
          this.cliente.nome = this.details.nome;
          this.cliente.cognome = this.details.cognome;
          this.cliente.email = this.details.email;
          this.cliente.cellulare = this.details.cellulare;
        } else {
          this.errorMessage = 'booking.emailDiverse';
          this.formValid = false;
        }
      } else {
        this.errorMessage = 'globals.messages.emailNonValida';
        this.formValid = false;
      }
    } else {
      this.errorMessage = 'globals.messages.inserireDati';
      this.formValid = false;
    }
  }

  onPrenotazioneDetailsOutput(output: PrenotazioneDetailsOutput) {
    if (output != null) {
      this.totale = output.totale;
      this.totaleScontato = output.totaleScontato;
      this.rooms = output.rooms;
      this.sistemazioni = this.rooms.some(x => x.tipoCamera.sistemazioni != null && x.tipoCamera.sistemazioni.length > 0);
      this.onTotaleChange.emit(this.totaleScontato > 0 ? this.totaleScontato : this.totale);
    }
  }

  login() {
    this.loginEvent.emit();
  }

  verificaVoucher() {
    this.setIsLoading(true);

    let rq = new VerificaVoucherRQ();
    rq.oidAlbergo = this.albergo.oid;
    rq.oidPortale = this.portale.oid;
    rq.codice = this.voucher;
    rq.email = this.details.email;
    rq.arrivo = this.from;

    this.apiService.verificaVoucher(rq).subscribe((res: VerificaVoucherRS) => {
      if (res.data.oidVoucher != null && res.data.oidVoucher > 0) {
        this.oidVoucher = res.data.oidVoucher;
        this.prenotazioneDetails.scontoVoucher = res.data.sconto;
        this.onChange.emit();
        this.showInfo('booking.codiceApplicato', { value: Math.min(res.data.sconto, this.totaleScontato > 0 ? this.totaleScontato : this.totale) + ' ' + this.utility.getValuta(this.portale.valuta, false) });
      } else {
        this.resetVoucher();
        this.showError('booking.erroreVoucher');
      }

      this.setIsLoading(false);
    });
  }

  resetVoucher() {
    if (this.voucher != null || this.oidVoucher != null || this.prenotazioneDetails.scontoVoucher != null) {
      this.voucher = null;
      this.oidVoucher = null;
      this.prenotazioneDetails.scontoVoucher = null;
      this.onChange.emit();
    }
  }

  updateInformazioniPrimoOspite(force: boolean = false) {
    if (this.isRichiediInformazioniOspiti && (force || !this.primoOspiteUpdated) && this.rooms != null && this.rooms.length > 0 && this.rooms[0].cliente != null) {
      this.rooms[0].cliente.nome = this.details.nome;
      this.rooms[0].cliente.cognome = this.details.cognome;
      this.rooms[0].cliente.email = this.details.email;
    }

    if (force) {
      this.primoOspiteUpdated = false;
    }
  }

  getDataLoaderNazioni(): Observable<WebApiResponseFilter> {
    let rq = new GetNazioniTradotteRQ();
    rq.addEmptyItem = !this.portale.isNazioneObbligatoria;
    rq.lingua = this.lingua;

    return this.apiService.getNazioniTradotte(rq);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
  }
}