import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { BookingSolutionOfferRS, Dimension } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-pacchetti',
  templateUrl: './pacchetti.component.html',
  styleUrls: ['./pacchetti.component.scss']
})
export class PacchettiComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() set _promozioni(value: BookingSolutionOfferRS[]) {
    if (value != null) {
      this.promozioni = value.slice(0, 10);
    }
  };

  @Input() valuta: string;

  @Output() onPromozioneSelected: EventEmitter<BookingSolutionOfferRS> = new EventEmitter();

  public showImageTop: boolean = false;
  public rowHeight: number = 0;
  public imageWidth: number = 0;
  public sub: Subscription;
  public initialized: boolean = false;
  public currentPromozione: number = 0;
  public promozioni: BookingSolutionOfferRS[] = [];
  public isChanging: boolean = false;
  public images: string[] = [];
  public intervalStop: Subject<void> = new Subject();
  public intervalStart: Subject<void> = new Subject();
  public showImage: Subject<number> = new Subject();

  @ViewChild('row') row: ElementRef;
  @ViewChild('imageContainer') imageContainer: ElementRef;

  constructor(private containerService: ContainerService,
    public utility: UtilityService) { }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      setTimeout(() => {
        if ((d.width >= 1200 && d.width <= 1550) || d.width < 992) {
          this.showImageTop = true;
        } else {
          this.showImageTop = false;
        }

        this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 48 + 1);
        this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
      }, 200);
    });

    for (let promozione of this.promozioni) {
      this.images.push(promozione.urlImmagineEvidenza);
    }

    this.startInterval();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if ((this.containerService.dimension.width >= 1200 && this.containerService.dimension.width <= 1550) || this.containerService.dimension.width < 992) {
        this.showImageTop = true;
      } else {
        this.showImageTop = false;
      }

      this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 48 + 1);
      this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
      this.initialized = true;
    }, 200);
  }

  startInterval() {
    this.intervalStart.next();
  }

  stopInterval() {
    this.intervalStop.next();
  }

  showPromozione(index: number) {
    this.showImage.next(index);
  }

  onImageChangeStart(index: number) {
    this.isChanging = true;
    this.currentPromozione = index;

    setTimeout(() => {
      this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 48 + 1);
      this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
    }, 200);
  }

  onImageChangeStop() {
    this.isChanging = false;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.intervalStart.complete();
    this.intervalStop.complete();
  }
}