import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren,
} from '@angular/core';
import { MatExpansionPanel } from '@angular/material/expansion';
import { Subject, Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  BookingSolutionRateRS,
  BookingSolutionRoomRQ,
  BookingSolutionRoomRS,
  DialogOptions,
  Dimension,
  TariffaQuantityBooking,
  TariffaQuantitySelectBooking,
  TariffaTipoCameraBooking,
} from 'src/app/shared/models/dto';
import { MealPlanType } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import clip from 'text-clipper';

import {
  TipoCameraTariffaDetailsDialogComponent,
} from '../../../interfaces/tipo-camera-tariffa-details-dialog/tipo-camera-tariffa-details-dialog.component';

declare let holipay;

@Component({
  selector: 'app-booking-room',
  templateUrl: './booking-room.component.html',
  styleUrls: ['./booking-room.component.scss']
})
export class BookingRoomComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() set _room(value: BookingSolutionRoomRS) {
    if (value != null) {
      this.room = value;

      this.immagini = this.room.immagini.map(x => x.immagine);
      this.descrizioni = this.room.immagini.map(x => x.descrizione);

      this.initialization(this.room.tariffe);

      this.persone = new Array(this.room.paxMax >= 0 ? this.room.paxMax : 0).fill(0);

      if (!!this.room.descrizione) {
        let indicator = '<a class="continuaLink" href="#" onclick="event.preventDefault()">' + this.localizationService.getTranslationSync('booking.more') + '</a>';

        this.shortDescription = clip(this.room.descrizione, 115, { html: true, breakWords: true });

        let index = this.shortDescription.lastIndexOf('');

        if (index >= 0) {
          this.shortDescription = this.shortDescription.slice(0, index) + indicator + this.shortDescription.slice(index + 1);
        } else if (this.shortDescription != this.room.descrizione) {
          this.shortDescription += indicator;
        }
      }

      if (this.room.tariffe != null && this.room.tariffe.length > 0) {
        for (let tariffa of this.room.tariffe) {
          if (!!tariffa.testoNonPrenotabile) {
            tariffa.testoTradottoNonPrenotabile = this.localizationService.getTranslationSync(tariffa.testoNonPrenotabile, { value: tariffa.valoreNonPrenotabile });
          }
        }
      }

      setTimeout(() => {
        this.refreshHolipayWidget();
      }, 100);
    }
  };

  @Input() set roomsSelected(value: TariffaTipoCameraBooking[]) {
    if (value != null) {
      this.tariffeTipiCamera = value;
      this.roomsSelectedNumber = 0;

      for (let camera of value) {
        this.roomsSelectedNumber += camera.quantity;
      }

      this.setDisabledQuantities();
    }
  };

  @Input() set rooms(value: BookingSolutionRoomRQ[]) {
    if (value != null) {
      this.roomsNumber = value.length;
    }
  };

  @Input() set _trattamento(value: MealPlanType) {
    this.trattamento = value;

    this.filteredTariffe = this.room.tariffe.filter(x => x.mealPlanType == this.trattamento && x.isPrenotabile);

    if (this.showTariffe) {
      this.onShowTariffeClick();
    }

    this.initialization(this.trattamento == null ? this.room.tariffe : this.filteredTariffe);
  };

  @Input() disablePrenotaButton: Subject<boolean>;
  @Input() loginSubject: Subject<void>;
  @Input() dayUse: boolean;
  @Input() isVisualizzaTotaleSoggiornoSeNonDisponibile: boolean;
  @Input() isPaxStep1Visualizzate: boolean;
  @Input() isCamereLibereStep1Visualizzate: boolean;
  @Input() isVisualizzaDettaglioGiornaliero: boolean;
  @Input() isDisplayOnly: boolean;
  @Input() isVisualizzaDescrizionePoliticaCancellazione: boolean;
  @Input() isVisualizzaDescrizioneDettagliataTariffa: boolean;
  @Input() isVisualizzaTariffeEspanse: boolean;
  @Input() arrivo: string;
  @Input() partenza: string;
  @Input() lingua: string;
  @Input() valuta: string;

  @Output() login: EventEmitter<any> = new EventEmitter();
  @Output() onPrenota: EventEmitter<BookingSolutionRateRS> = new EventEmitter();
  @Output() onQuantityChange: EventEmitter<TariffaQuantityBooking> = new EventEmitter();
  @Output() onButtonsClick: EventEmitter<any> = new EventEmitter();

  @ViewChild('tariffePanel') tariffePanel: MatExpansionPanel;
  @ViewChildren('tariffeRow') tariffeRow: QueryList<ElementRef>;

  public room: BookingSolutionRoomRS;
  public prezzo: number;
  public prezzoScontato: number;
  public sconto: number;
  public persone: number[] = [];
  public disponibili: number;
  public showTariffe: boolean = false;
  public prenotaButtonDisabled: boolean = true;
  public quantities: TariffaQuantitySelectBooking[][] = [];
  public quantitiesSelected: TariffaQuantityBooking[] = [];
  public selectedQuantity: number = 0;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public roomsNumber: number = 0;
  public roomsSelectedNumber: number = 0;
  public offersAvailable: boolean;
  public shortDescription: string;
  public trattamento: MealPlanType;
  public wideScreen: boolean;
  public firstTariffaIndex: number;
  public bestPriceIndex: number;
  public tariffaRowHeight: number;
  public filteredTariffe: BookingSolutionRateRS[];
  public immagini: string[] = [];
  public descrizioni: string[] = [];
  public tariffeTipiCamera: TariffaTipoCameraBooking[] = [];

  constructor(public containerService: ContainerService,
    public utility: UtilityService,
    public sessionService: SessionService,
    private localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      if (d.width >= 1250) {
        this.wideScreen = true;
      } else {
        this.wideScreen = false;
      }

      this.tariffaRowHeight = 0;

      setTimeout(() => {
        for (let row of this.tariffeRow.toArray()) {
          this.tariffaRowHeight = Math.max(this.tariffaRowHeight, row.nativeElement.offsetHeight);
        }
      }, 200);
    });

    if (this.disablePrenotaButton != null) {
      this.sub2 = this.disablePrenotaButton.subscribe((disabled: boolean) => {
        this.prenotaButtonDisabled = disabled;
      });
    }

    this.sub1 = this.loginSubject.subscribe(() => {
      this.calculatePrice(this.trattamento == null ? this.room.tariffe : this.filteredTariffe);
    });

    this.setDisabledQuantities();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.containerService.dimension.width >= 1250) {
        this.wideScreen = true;
      } else {
        this.wideScreen = false;
      }

      if (this.isVisualizzaTariffeEspanse) {
        this.onShowTariffeClick();
      }
    }, 200);
  }

  initialization(tariffe: BookingSolutionRateRS[]) {
    this.firstTariffaIndex = 0;

    for (let tariffa of this.room.tariffe) {
      if (tariffa.disponibili > 0 && (this.trattamento == null || (tariffa.mealPlanType == this.trattamento && tariffa.isPrenotabile))) {
        break;
      }

      this.firstTariffaIndex++;
    }

    this.calculatePrice(tariffe);

    if (this.room.tariffe != null && this.room.tariffe.length > 0) {
      this.quantities = [];

      for (let tariffa of this.room.tariffe) {
        let quantities: TariffaQuantitySelectBooking[] = [];

        for (let disp = 0; disp <= this.disponibili; disp++) {
          let q = new TariffaQuantitySelectBooking();
          q.quantity = disp;
          q.disabled = false;
          q.tariffaOid = tariffa.rateId;
          q.oidPromozione = tariffa.oidPromozione;
          q.numeroAdulti = tariffa.numeroAdulti;
          q.numeroBambini = tariffa.numeroBambini;

          if (disp == 0) {
            q.viewValue = '0';
          } else {
            q.viewValue = disp + ' (' + ((tariffa.prezzoScontato > 0 ? tariffa.prezzoScontato : tariffa.prezzo) * disp).toFixed(2) + this.utility.getValuta(this.valuta) + ')';
          }

          quantities.push(q);
        }

        this.quantities.push(quantities);
      }
    }
  }

  calculatePrice(tariffe: BookingSolutionRateRS[]) {
    this.prezzo = 0;
    this.prezzoScontato = 0;
    this.sconto = 0;

    if (tariffe != null && tariffe.length > 0) {
      this.disponibili = this.room.disponibili;
      let prezzoMin: number = Number.POSITIVE_INFINITY;

      let i = 0;

      for (let tariffa of tariffe) {
        if (tariffa.isPrenotabile) {
          if (tariffa.disponibili > 0) {
            this.disponibili = Math.min(tariffa.disponibili, this.disponibili);
          }

          if (tariffa.prezzoScontato > 0) {
            this.offersAvailable = true;

            if (tariffa.prezzoScontato < prezzoMin) {
              if (!tariffa.isOffertaSegreta || this.sessionService.isLoggedInMySelf()) {
                prezzoMin = tariffa.prezzoScontato;
                this.prezzo = tariffa.prezzo;
                this.prezzoScontato = tariffa.prezzoScontato;
                this.sconto = tariffa.scontoPercentuale;
                this.bestPriceIndex = i;
              }
            }
          } else {
            if (tariffa.prezzo < prezzoMin) {
              if (!tariffa.isOffertaSegreta || this.sessionService.isLoggedInMySelf()) {
                prezzoMin = tariffa.prezzo;
                this.prezzo = tariffa.prezzo;
                this.prezzoScontato = 0;
                this.sconto = 0;
                this.bestPriceIndex = i;
              }
            }
          }
        }

        i++;
      }
    } else {
      this.disponibili = 0;
    }
  }

  quantityChange(tariffa: BookingSolutionRateRS, quantity: number) {
    let tariffaQuantity: TariffaQuantityBooking = new TariffaQuantityBooking();
    tariffaQuantity.tariffa = tariffa;
    tariffaQuantity.quantity = quantity;

    this.onQuantityChange.emit(tariffaQuantity);

    let index = this.quantitiesSelected.findIndex(x => x.tariffa != null && x.tariffa.rateId == tariffa.rateId && x.tariffa.oidPromozione == tariffa.oidPromozione && x.tariffa.numeroAdulti == tariffa.numeroAdulti && x.tariffa.numeroBambini == tariffa.numeroBambini);

    if (index >= 0) {
      if (quantity > 0) {
        this.quantitiesSelected[index].quantity = quantity;
      } else {
        this.quantitiesSelected.splice(index, 1);
      }
    } else {
      if (quantity > 0) {
        this.quantitiesSelected.push(tariffaQuantity);
      }
    }

    this.selectedQuantity = 0;

    for (let q of this.quantitiesSelected) {
      this.selectedQuantity += q.quantity;
    }

    this.setDisabledQuantities();
  }

  setDisabledQuantities() {
    let i = 0;

    for (let tariffa of this.quantities) {
      let j = 0;

      for (let quantity of tariffa) {
        let roomsNumberToSelect = this.roomsNumber - this.roomsSelectedNumber;
        let roomsAvailableRemaining = this.disponibili - this.selectedQuantity;

        if (this.room.camereWelcomeDisponibiliXTipoCamera != null && this.room.tipoCameraIdHotel > 0) {
          let camereSelezionateTipoCameraHotel = this.tariffeTipiCamera.filter(x => x.tipoCamera != null && x.tipoCamera.tipoCameraIdHotel == this.room.tipoCameraIdHotel).reduce((sum, current) => sum + current.quantity, 0);

          if (camereSelezionateTipoCameraHotel > 0) {
            let camereDisponibiliRimanenti = this.room.camereWelcomeDisponibiliXTipoCamera - camereSelezionateTipoCameraHotel;

            if (camereDisponibiliRimanenti < roomsAvailableRemaining) {
              roomsAvailableRemaining = camereDisponibiliRimanenti;
            }
          }
        }

        let quantitySelected = this.quantitiesSelected.find(x => x.tariffa != null && x.tariffa.rateId == quantity.tariffaOid && x.tariffa.oidPromozione == quantity.oidPromozione && x.tariffa.numeroAdulti == quantity.numeroAdulti && x.tariffa.numeroBambini == quantity.numeroBambini);

        if (quantitySelected != null && quantitySelected.quantity > 0) {
          roomsNumberToSelect += quantitySelected.quantity;
          roomsAvailableRemaining += quantitySelected.quantity;
        }

        if (quantity.quantity == 0 || (quantitySelected != null && quantity.quantity <= quantitySelected.quantity)) {
          this.quantities[i][j].disabled = false;
        } else {
          this.quantities[i][j].disabled = quantity.quantity > roomsNumberToSelect || quantity.quantity > roomsAvailableRemaining;
        }

        j++;
      }

      i++;
    }
  }

  openTariffaDetails(camera: BookingSolutionRoomRS, tariffa?: BookingSolutionRateRS) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'dialog-overlay';

    let ref = this.showModalDialog(TipoCameraTariffaDetailsDialogComponent, dialogOptions);
    let component = <TipoCameraTariffaDetailsDialogComponent>ref.componentInstance;
    component.tariffa = tariffa;
    component.tipoCamera = camera;
    component.isVisualizzaDettaglioGiornaliero = this.isVisualizzaDettaglioGiornaliero;
    component.valuta = this.valuta;
  }

  prenota(tariffa: BookingSolutionRateRS) {
    if (this.roomsNumber > 1) {
      this.onPrenota.emit();
    } else {
      this.onPrenota.emit(tariffa);
    }
  }

  onShowTariffeClick() {
    if (!this.tariffePanel.disabled && this.disponibili > 0 && this.room.tariffe != null && this.room.tariffe.length > 0) {
      if (!this.showTariffe) {
        this.showTariffe = true;
        this.tariffaRowHeight = 0;
      }

      this.tariffePanel.toggle();

      this.tariffePanel.disabled = true;
    }
  }

  afterExpand() {
    this.refreshHolipayWidget();

    this.tariffePanel.disabled = false;
    this.tariffaRowHeight = 0;

    setTimeout(() => {
      for (let row of this.tariffeRow.toArray()) {
        this.tariffaRowHeight = Math.max(this.tariffaRowHeight, row.nativeElement.offsetHeight);
      }
    }, 200);
  }

  afterCollapse() {
    this.showTariffe = false;
    this.tariffePanel.disabled = false;
    this.tariffaRowHeight = 0;
  }

  refreshHolipayWidget() {
    if (this.room.tariffe.some(x => x.isHolipayDisponibile) && holipay != null) {
      holipay.updateWidgets();
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();

    if (this.sub2 != null) {
      this.sub2.unsubscribe();
    }
  }
}