import { Component, EventEmitter, Input, OnDestroy, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
    BookingSolutionHighlightedOfferRS,
    BookingSolutionRateRS,
    BookingSolutionRoomRS,
    DialogOptions,
} from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import {
    TipoCameraTariffaDetailsDialogComponent,
} from '../../../interfaces/tipo-camera-tariffa-details-dialog/tipo-camera-tariffa-details-dialog.component';

@Component({
  selector: 'app-pacchetti-evidenza',
  templateUrl: './pacchetti-evidenza.component.html',
  styleUrls: ['./pacchetti-evidenza.component.scss']
})
export class PacchettiEvidenzaComponent extends BaseComponent implements OnDestroy {

  @Input() set _promozioni(value: BookingSolutionHighlightedOfferRS[]) {
    if (value != null) {
      this.promozioni = value.slice(0, 10);
      this.images = this.promozioni.map(x => x.tariffa.urlImmaginePromozione);
      this.currentPromozione = 0;
      this.showImage.next(0);
      this.intervalStop.next();
      this.intervalStart.next();
    }
  };

  @Input() isVisualizzaDettaglioGiornaliero: boolean;
  @Input() valuta: string;
  @Input() isDisplayOnly: boolean;

  @Output() onPromozioneSelected: EventEmitter<BookingSolutionHighlightedOfferRS> = new EventEmitter();
  @Output() login: EventEmitter<any> = new EventEmitter();
  @Output() onButtonsClick: EventEmitter<any> = new EventEmitter();

  public currentPromozione: number = 0;
  public promozioni: BookingSolutionHighlightedOfferRS[] = [];
  public isChanging: boolean = false;
  public images: string[] = [];
  public intervalStop: Subject<void> = new Subject();
  public intervalStart: Subject<void> = new Subject();
  public showImage: Subject<number> = new Subject();

  constructor(public utility: UtilityService, private containerService: ContainerService, public sessionService: SessionService) { super(); }

  onImageChangeStart(index: number) {
    this.isChanging = true;
    this.currentPromozione = index;
  }

  showDetails(camera: BookingSolutionRoomRS, tariffa: BookingSolutionRateRS) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'dialog-overlay';

    let ref = this.showModalDialog(TipoCameraTariffaDetailsDialogComponent, dialogOptions);
    let component = <TipoCameraTariffaDetailsDialogComponent>ref.componentInstance;
    component.tariffa = tariffa;
    component.tipoCamera = camera;
    component.isVisualizzaDettaglioGiornaliero = this.isVisualizzaDettaglioGiornaliero;
    component.valuta = this.valuta;
  }

  ngOnDestroy() {
    this.intervalStart.complete();
    this.intervalStop.complete();
  }
}