import { SocialAuthService, SocialUser } from '@abacritt/angularx-social-login';
import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente } from 'src/app/shared/models/bo.model';
import {
  AlbergoPayMail,
  GetPayMailRQ,
  GetPayMailRS,
  MessaggioPayMail,
  PaymentMethodPayMail,
  PaymentModel,
  PortalePayMail,
  SetTransazioneRQ,
  TransazionePayMail,
} from 'src/app/shared/models/dto';
import { MittenteMessaggio, Pagina, StatoEsecuzioneTransazione } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-paymail',
  templateUrl: './paymail.component.html',
  styleUrls: ['./paymail.component.scss']
})
export class PaymailComponent extends BaseComponent implements OnInit, OnDestroy {

  public initialized: boolean = false;
  public portale: PortalePayMail = new PortalePayMail();
  public transazione: TransazionePayMail = new TransazionePayMail();
  public messaggi: MessaggioPayMail[] = [];
  public modalitaPagamento: PaymentMethodPayMail[] = [];
  public sub: Subscription;
  public sub1: Subscription;
  public formValid: boolean = false;
  public noPaymentMethod: boolean = false;
  public errorMessage: string = 'globals.messages.inserireDati';
  public cliente: Cliente = new Cliente();
  public enumStatoEsecuzione = StatoEsecuzioneTransazione;
  public enumMittente = MittenteMessaggio;
  public albergo: AlbergoPayMail = new AlbergoPayMail();
  public condizioniPrenotazione: string;
  public condizioniContratto: string;
  public showMessageOnClose: boolean = true;
  public backgroundImage: string;

  @HostListener('window:beforeunload', ['$event'])
  beforeUnloadHandler(event) {
    if (!this.transazione.isPagamentoEffettuato && this.showMessageOnClose) {
      if (event) {
        event.returnValue = '';
      }

      return '';
    }

    return null;
  }

  constructor(private router: Router,
    private route: ActivatedRoute,
    private localizationService: LocalizationService,
    private apiService: ApiService,
    private containerService: ContainerService,
    private deviceService: DeviceDetectorService,
    public utility: UtilityService,
    private authService: SocialAuthService,
    private cssLoaderService: CssLoaderService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('paymail-scrolling');

    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {
      let uid = x.get('uid');

      if (!!uid) {
        let rq = new GetPayMailRQ();
        rq.uid = uid;
        rq.isMobile = this.deviceService.isMobile();

        this.apiService.getPayMail(rq).subscribe((res: GetPayMailRS) => {
          if (res.data != null && res.data.transazione != null && res.data.albergo != null) {
            this.transazione = res.data.transazione;
            this.albergo = res.data.albergo;
            this.cssLoaderService.load(this.albergo.oid, Pagina.Paymail, res.data.portale?.oidUbicazioneIstat);
            this.modalitaPagamento = res.data.modalitaPagamento;
            this.messaggi = res.data.messaggi;
            this.condizioniPrenotazione = res.data.condizioniPrenotazione;
            this.condizioniContratto = res.data.condizioniContratto;

            if (res.data.portale != null) {
              this.portale = res.data.portale;
            }

            if (this.transazione.cliente != null) {
              this.cliente.nome = this.transazione.cliente.nome;
              this.cliente.cognome = this.transazione.cliente.cognome;
              this.cliente.email = this.transazione.cliente.email;
              this.cliente.cellulare = this.transazione.cliente.cellulare;
              this.cliente.privacy = this.transazione.cliente.privacy;
              this.cliente.newsletter = this.transazione.cliente.newsletter;
              this.checkFormValidity();
            }

            this.utility.setLingua(this.transazione.lingua).then(() => {
              if (!!res.data.traduzioni) {
                let json;

                try {
                  json = JSON.parse(res.data.traduzioni);
                } catch (e) { }

                if (!!json) {
                  this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
                }
              }

              this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.initialized = true;
                this.setIsLoading(false);
              });
            });
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });

    this.sub1 = this.authService.authState.subscribe((user: SocialUser) => {
      if (user != null && this.cliente != null) {
        this.cliente.nome = user.firstName;
        this.cliente.cognome = user.lastName;
        this.cliente.email = user.email;
        this.checkFormValidity();
      }
    });
  }

  checkFormValidity() {
    if (!!this.cliente.nome && !!this.cliente.cognome && !!this.cliente.email) {
      if (this.utility.validateEmail(this.cliente.email)) {
        this.formValid = true;
      } else {
        this.errorMessage = 'globals.messages.emailNonValida';
        this.formValid = false;
      }
    } else {
      this.errorMessage = 'globals.messages.inserireDati';
      this.formValid = false;
    }
  }

  onPaymentComplete(pagamento: PaymentModel) {
    this.setIsLoading(true);

    let rq = new SetTransazioneRQ();
    rq.oid = this.transazione.oid;
    rq.isErrore = false;
    rq.token = pagamento.modalitaPagamento.token;
    /*rq.statoEsecuzione = StatoEsecuzioneTransazione.Eseguito;
    rq.statoContabilizzazione = StatoContabilizzazione.Contabilizzato;*/
    rq.oidModalitaPagamento = pagamento.modalitaPagamento.paymentId;
    rq.descrizioneEsito = 'Pagamento eseguito';
    rq.isNotificaGestionale = true;
    rq.oidTransazioneEseguita = pagamento.oidTransazione;
    rq.numeroCarta = pagamento.numeroCarta;
    rq.titolareCarta = pagamento.titolareCarta;
    rq.scadenzaCartaAnno = pagamento.scadenzaCartaAnno;
    rq.scadenzaCartaMese = pagamento.scadenzaCartaMese;
    rq.codiceSicurezza = pagamento.codiceSicurezza;
    rq.codiceRiferimentoOperazione = pagamento.codiceRiferimentoOperazione;

    this.apiService.setTransazione(rq).subscribe(() => {
      this.setIsLoading(false);

      this.showMessageOnClose = false;

      this.showInfo('booking.paymail.pagamentoCompletato').then(() => {
        window.location.reload();
      });
    });
  }

  onPaymentError(error: string) {
    this.setIsLoading(true);

    let rq = new SetTransazioneRQ();
    rq.oid = this.transazione.oid;
    rq.isErrore = true;
    //rq.statoEsecuzione = StatoEsecuzioneTransazione.NonEseguito;
    rq.descrizioneEsito = error;
    rq.isNotificaGestionale = false;

    this.apiService.setTransazione(rq).subscribe(() => {
      this.setIsLoading(false);
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
  }
}