import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BonificoDetailsModalComponent } from 'src/app/interfaces/bonifico-details-modal/bonifico-details-modal.component';
import { Cliente } from 'src/app/shared/models/bo.model';
import {
  ConfermaPreventivoRQ,
  DialogOptions,
  MyPageAlbergoRS,
  MyPageCameraRS,
  MyPagePagamentoRS,
  MyPagePortaleRS,
  MyPagePrenotazioneRS,
  MyPagePreventivoRS,
  MyPagePropostaRS,
  PaymentModel,
  ReplaceTagsTemplateRQ,
  ReplaceTagsTemplateRS,
  VerificaVoucherRQ,
  VerificaVoucherRS,
} from 'src/app/shared/models/dto';
import {
  StatoPrenotazione,
  StatoPrenotazioneWelcome,
  StatoPreventivo,
  TestoMyPage,
  TipoModalitaPagamento,
} from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MypageService } from '../mypage.service';

@Component({
  selector: 'app-mypage-prenotazione',
  templateUrl: './mypage-prenotazione.component.html',
  styleUrls: ['./mypage-prenotazione.component.scss']
})
export class MypagePrenotazioneComponent extends BaseComponent implements OnInit {

  @Input() lingua: string;
  @Input() albergo: MyPageAlbergoRS;
  @Input() portale: MyPagePortaleRS;
  @Input() preventivo: MyPagePreventivoRS;
  @Input() totale: number;
  @Input() modalitaPagamento: MyPagePagamentoRS[];
  @Input() allModalitaPagamento: MyPagePagamentoRS[];
  @Input() prenotazione: MyPagePrenotazioneRS;
  @Input() camere: MyPageCameraRS[];
  @Input() isVisualizzaCondizioniPrenotazioneStep2: boolean;
  @Input() isVisualizzaRiepilogoPagamentoMyPage: boolean;
  @Input() condizioniPrenotazione: string;
  @Input() condizioniContratto: string;
  @Input() oidSistemazioni: number[];
  @Input() sistemazioni: boolean;
  @Input() templateStampaOid: number;
  @Input() numeroVoucher: number;
  @Input() ospiti: Cliente[];
  @Input() config: boolean;

  @Output() confirmed: EventEmitter<any> = new EventEmitter();
  @Output() onEditTestoClick: EventEmitter<TestoMyPage> = new EventEmitter();
  @Output() onScontoVoucherChange: EventEmitter<number> = new EventEmitter();

  public notti: number;
  public note: string;
  public enumStatoPreventivo = StatoPreventivo;
  public enumStatoPrenotazione = StatoPrenotazione;
  public enumStatoPrenotazioneWelcome = StatoPrenotazioneWelcome;
  public enumTipoModalitaPagamento = TipoModalitaPagamento;
  public noPaymentMethod: boolean = false;
  public cliente: Cliente;
  public templateStampaHtml: string;
  public templateStampaHtmlTop: number;
  public formValid: boolean = false;
  public voucher: string;
  public oidVoucher: number;
  public scontoVoucher: number = 0;
  public caparra: number = 0;

  constructor(public utility: UtilityService,
    protected mypageService: MypageService,
    protected containerService: ContainerService,
    protected apiService: ApiService) {
    super();
  }

  ngOnInit() {
    this.notti = Math.max(moment(this.getDate(this.prenotazione.partenza)).diff(moment(this.getDate(this.prenotazione.arrivo)), 'days'), 1);

    if (this.preventivo != null) {
      this.cliente = new Cliente();
      this.cliente.nome = this.preventivo.cliente.nome;
      this.cliente.cognome = this.preventivo.cliente.cognome;
      this.cliente.email = this.preventivo.cliente.email;
      this.cliente.privacy = this.preventivo.cliente.privacy;
      this.cliente.newsletter = this.preventivo.cliente.newsletter;
      this.updateInformazioniPrimoOspite();
    }

    this.checkFormValidity();
  }

  conferma(pagamento: PaymentModel) {
    this.setIsLoading(true);

    let rq = new ConfermaPreventivoRQ();
    rq.oidPreventivo = this.preventivo.oid;
    rq.oidPrenotazioni = this.prenotazione.oids;
    rq.oidSistemazioni = this.oidSistemazioni;
    rq.ospiti = this.ospiti;
    rq.note = this.note;
    rq.pagamento = pagamento;
    rq.oidVoucher = this.oidVoucher;

    this.apiService.confermaMyPage(rq).subscribe(() => {
      this.setIsLoading(false);

      this.confirmed.emit();

      this.showInfo('globals.messages.prenotazioneCompletata').then(() => {
        this.setIsLoading(true);
        window.location.reload();
      });
    });
  }

  stampa() {
    this.setIsLoading(true);

    let rq = new ReplaceTagsTemplateRQ();
    rq.oidTemplate = this.templateStampaOid;
    rq.oidPrenotazioni = this.prenotazione.oids;

    this.apiService.replaceTagsTemplate(rq).subscribe((rs: ReplaceTagsTemplateRS) => {
      this.templateStampaHtml = rs.data.testo;
      this.templateStampaHtmlTop = this.mypageService.scrollTop;
      this.setIsLoading(false);

      setTimeout(() => {
        window.print();
      }, 200);
    });
  }

  openBonificoDetails() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(BonificoDetailsModalComponent, dialogOptions);
    let component = <BonificoDetailsModalComponent>ref.componentInstance;
    component.modalitaPagamento = this.prenotazione.modalitaPagamento;
  }

  getCamere(): MyPagePropostaRS[] {
    return this.camere.map(x => x.proposte != null && x.proposte.length == 1 && x.proposte[0].proposte != null && x.proposte[0].proposte.length == 1 ? x.proposte[0].proposte[0] : null);
  }

  checkFormValidity() {
    if ((!this.portale.isSistemazioneObbligatoria || !this.sistemazioni || this.oidSistemazioni.every(x => x > 0)) && this.ospiti.every(x => (!x.nome && !x.cognome && !x.email) || (!!x.nome && !!x.cognome && !!x.email))) {
      this.formValid = true;
    } else {
      this.formValid = false;
    }
  }

  verificaVoucher() {
    this.setIsLoading(true);

    let rq = new VerificaVoucherRQ();
    rq.oidAlbergo = this.albergo.oid;
    rq.oidPortale = this.portale.oid;
    rq.codice = this.voucher;
    rq.email = this.cliente.email;
    rq.arrivo = this.preventivo.arrivo;

    this.apiService.verificaVoucher(rq).subscribe((res: VerificaVoucherRS) => {
      if (res.data.oidVoucher != null && res.data.oidVoucher > 0) {
        this.oidVoucher = res.data.oidVoucher;
        this.scontoVoucher = res.data.sconto;
        this.onScontoVoucherChange.emit(this.scontoVoucher);
        this.showInfo('booking.my-page.codiceApplicato', { value: Math.min(res.data.sconto, this.totale) });
      } else {
        this.resetVoucher();
        this.showError('booking.my-page.erroreVoucher');
      }

      this.setIsLoading(false);
    });
  }

  resetVoucher() {
    if (this.voucher != null || this.oidVoucher != null || this.scontoVoucher != 0) {
      this.voucher = null;
      this.oidVoucher = null;
      this.scontoVoucher = 0;
      this.onScontoVoucherChange.emit(this.scontoVoucher);
    }
  }

  updateInformazioniPrimoOspite() {
    if (this.portale.isRichiediInformazioniOspiti && this.ospiti != null && this.ospiti.length > 1) {
      this.ospiti[0].nome = this.cliente.nome;
      this.ospiti[0].cognome = this.cliente.cognome;
      this.ospiti[0].email = this.cliente.email;
    }
  }

  onCaparraChange(caparra: number) {
    setTimeout(() => {
      this.caparra = caparra;
    }, 200);
  }
}