import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';

@Component({
  selector: 'app-booking-self-checkin',
  templateUrl: './booking-self-checkin.component.html',
  styleUrls: ['./booking-self-checkin.component.scss']
})
export class BookingSelfCheckinComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: Subscription;
  public uid: string;
  public initialized: boolean = false;

  constructor(private route: ActivatedRoute,
    private router: Router) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {
      let uid = x.get('uid');

      if (!!uid) {
        this.uid = uid;
        this.initialized = true;
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}
