import { SelectionModel } from '@angular/cdk/collections';
import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Articolo, Preventivo } from 'src/app/shared/models/bo.model';
import {
  GetMyPageRQ,
  GetMyPageRS,
  WebApiRequestFilter,
  WebApiResponse,
  WebApiResponseFilter,
} from 'src/app/shared/models/dto';
import { CriteriaOperator } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-mypage-articoli-edit',
  templateUrl: './mypage-articoli-edit.component.html',
  styleUrls: ['./mypage-articoli-edit.component.scss']
})
export class MypageArticoliEditComponent extends BaseComponent implements OnInit {

  @Input() getMyPageRQ: GetMyPageRQ;
  @Input() oidAlbergo: number;
  @Input() oidPreventivo: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: Preventivo = new Preventivo();

  public selectionArticoli = new SelectionModel<number>(true, []);
  public articoli: Articolo[];

  constructor(private apiService: ApiService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.apiService.getPreventivo(this.oidPreventivo).subscribe((res: WebApiResponse) => {
      this.rq = res.data;

      this.apiService.getArticoliFilter(WebApiRequestFilter.fromFilter('isUpSellingCRM', CriteriaOperator.IsTrue), this.oidAlbergo).subscribe((articoliRS: WebApiResponseFilter) => {
        this.articoli = articoliRS.data;

        this.selectionArticoli = new SelectionModel<number>(true, []);

        if (!!this.rq.oidArticoliUpSelling) {
          let oidArticoli = this.rq.oidArticoliUpSelling.split(';');

          for (let oid of oidArticoli) {
            if (oid != '' && !isNaN(+oid) && this.articoli.findIndex(x => x.oid == +oid) != -1) {
              this.selectionArticoli.toggle(+oid);
            }
          }
        }

        this.setIsLoading(false);
      });
    });
  }

  save() {
    this.setIsLoading(true);

    this.rq.oidArticoliUpSelling = this.selectionArticoli.selected.toString().replace(/,/g, ';');

    this.apiService.setPreventivo(this.rq).subscribe(() => {
      this.apiService.getMyPage(this.getMyPageRQ).subscribe((res: GetMyPageRS) => {
        this.dialogRef.close(res);
        this.setIsLoading(false);
      });
    });
  }

  isAllSelectedArticoli() {
    const numSelected = this.selectionArticoli.selected.length;
    const numRows = this.articoli.length;
    return numSelected == numRows;
  }

  masterToggleArticoli() {
    this.isAllSelectedArticoli() ? this.selectionArticoli.clear() : this.articoli.forEach(row => this.selectionArticoli.select(row.oid));
  }
}
