import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import * as Color from 'color';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ImageEditComponent } from 'src/app/interfaces/image-edit/image-edit.component';
import { Template, TemplateXLingua } from 'src/app/shared/models/bo.model';
import {
  DialogOptions,
  GetMyPageRQ,
  GetMyPageRS,
  GetTemplatesRS,
  SetImmagineTemplateRQ,
  SetImmagineTemplateRS,
  SetTemplatesXLinguaRQ,
  WebApiRequestFilter,
} from 'src/app/shared/models/dto';
import { CriteriaOperator, FilterValueType } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-mypage-contenuto-edit',
  templateUrl: './mypage-contenuto-edit.component.html',
  styleUrls: ['./mypage-contenuto-edit.component.scss']
})
export class MypageContenutoEditComponent extends BaseComponent implements OnInit {

  @Input() getMyPageRQ: GetMyPageRQ;
  @Input() oidContenuto: number;
  @Input() v2: boolean;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: SetTemplatesXLinguaRQ = new SetTemplatesXLinguaRQ();
  public imageEdited: boolean = false;

  constructor(private apiService: ApiService, public sessionService: SessionService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    for (let lingua of this.lingue) {
      let template = new TemplateXLingua();
      template.lingua = lingua;
      template.template = new Template(this.oidContenuto);
      this.rq.templatesXLingua.push(template);
    }

    this.apiService.getTemplatesFilter(WebApiRequestFilter.fromFilter('oid', CriteriaOperator.Equal, this.oidContenuto.toString(), FilterValueType.Number)).subscribe((templatesRS: GetTemplatesRS) => {
      if (templatesRS.data.templates.length > 0) {
        this.rq.template = templatesRS.data.templates[0].template;

        try {
          if (this.rq.template.coloreSfondo != null) {
            this.rq.template.coloreSfondoHex = Color(this.rq.template.coloreSfondo).hex();
          }
        } catch (e) { }

        for (let templateXLingua of templatesRS.data.templates[0].templatesXLingua) {
          if (templateXLingua.lingua != null) {
            let index = this.rq.templatesXLingua.findIndex(x => x.lingua.oid == templateXLingua.lingua.oid);

            if (index >= 0) {
              this.rq.templatesXLingua[index].titolo = templateXLingua.titolo;
              this.rq.templatesXLingua[index].testo = templateXLingua.testo;
              this.rq.templatesXLingua[index].design = templateXLingua.design;
            }
          }
        }
      }

      this.setIsLoading(false);
    });
  }

  save() {
    this.setIsLoading(true);

    if (!!this.rq.template.coloreSfondoHex) {
      try {
        this.rq.template.coloreSfondo = Color(this.rq.template.coloreSfondoHex).rgbNumber();
      } catch (e) { }
    } else {
      this.rq.template.coloreSfondo = null;
    }

    this.apiService.setTemplate(this.rq).subscribe(() => {
      this.download();
    });
  }

  download() {
    this.setIsLoading(true);

    this.apiService.getMyPage(this.getMyPageRQ).subscribe((res: GetMyPageRS) => {
      this.dialogRef.close(res);
      this.setIsLoading(false);
    });
  }

  editImage() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "50vw";
    let ref = this.showModalDialog(ImageEditComponent, dialogOptions);
    let component = <ImageEditComponent>ref.componentInstance;
    component._url = this.rq.template.urlImmagine;
    component.title = 'globals.immagine';
    component.blue = true;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(base64 => {
      if (base64 != null) {
        this.setIsLoading(true);
        let rq = new SetImmagineTemplateRQ();
        rq.oid = this.rq.template.oid;
        rq.base64 = base64;
        this.apiService.setImmagineTemplate(rq).subscribe((res: SetImmagineTemplateRS) => {
          if (res.data != null) {
            this.rq.template.urlImmagine = res.data.url;
          }

          this.imageEdited = true;

          this.setIsLoading(false);
        });
      }
    });
  }

  deleteImage() {
    this.showDialogConfirm('globals.eliminareImmagine')
      .then(si => {
        this.setIsLoading(true);
        let rq = new SetImmagineTemplateRQ();
        rq.oid = this.rq.template.oid;
        rq.base64 = '';
        this.apiService.setImmagineTemplate(rq).subscribe((res: SetImmagineTemplateRS) => {
          if (res.data != null) {
            this.rq.template.urlImmagine = res.data.url;
          }

          this.imageEdited = true;

          this.setIsLoading(false);
        });
      },
        no => { }
      );
  }
}