import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Subscription } from 'rxjs';
import { ModalitaPagamento, Transazione } from 'src/app/shared/models/bo.model';
import {
  GetModalitaPagamentoGestPayRQ,
  GetTransazioneByIdRQ,
  PaymentRQ,
  PaymentRS,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import { StatoContabilizzazione, StatoEsecuzioneTransazione } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { BaseComponent } from '../../base-component/base-component';

@Component({
  selector: 'app-pagamento-form-result',
  templateUrl: './pagamento-form-result.component.html',
  styleUrls: ['./pagamento-form-result.component.scss']
})
export abstract class PagamentoFormResultComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: Subscription;

  constructor(protected apiService: ApiService, protected route: ActivatedRoute) { super(); }

  ngOnInit() {
    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {
      let oidTransazione = x.get('oidTransazione');
      let oidModalitaPagamento = x.get('oidModalitaPagamento');

      if (!!oidTransazione && !isNaN(+oidTransazione) && !!oidModalitaPagamento && !isNaN(+oidModalitaPagamento)) {
        this.apiService.getModalitaPagamento(+oidModalitaPagamento).subscribe((res: WebApiResponse) => {
          if (res.data != null) {
            this.checkPagamento(res.data, +oidTransazione, x);
          }
        });
      } else {
        this.checkGestPayOrder(x.get('a'), x.get('b'));
      }
    });
  }

  abstract checkPagamento(modalitaPagamento: ModalitaPagamento, oidTransazione: number, params: ParamMap): void;

  getTransaction(oidTransazione: number): Promise<Transazione> {
    return new Promise<Transazione>((resolve) => {
      this.apiService.getTransazione(+oidTransazione).subscribe((transazioneRS: WebApiResponse) => {
        if (transazioneRS.data != null) {
          resolve(transazioneRS.data as Transazione);
        } else {
          resolve(null);
        }
      });
    });
  }

  confirmTransaction(oidTransazione: number) {
    let transazione = new Transazione(+oidTransazione);
    transazione.urlRisposta = window.location.href;
    transazione.statoEsecuzione = StatoEsecuzioneTransazione.Eseguito;
    transazione.statoContabilizzazione = StatoContabilizzazione.Contabilizzato;
    this.apiService.updateTransazione(transazione).subscribe();
  }

  errorTransaction(oidTransazione: number, descrizioneEsito?: string) {
    let transazione = new Transazione(+oidTransazione);
    transazione.urlRisposta = window.location.href;
    transazione.statoEsecuzione = StatoEsecuzioneTransazione.Errore;
    transazione.statoContabilizzazione = StatoContabilizzazione.Nessuno;
    transazione.descrizioneEsito = descrizioneEsito;
    this.apiService.updateTransazione(transazione).subscribe();
  }

  checkGestPayOrder(shopId: string, cryptedString: string) {
    if (!!shopId && !!cryptedString) {
      let rq = new GetModalitaPagamentoGestPayRQ();
      rq.shopId = shopId;

      this.apiService.getModalitaPagamentoGestPay(rq).subscribe((getModalitaPagamentoRS: WebApiResponse) => {
        if (getModalitaPagamentoRS.data != null && !getModalitaPagamentoRS.data.isUsePost) {
          this.verifyGestPayOrder(getModalitaPagamentoRS.data, cryptedString).then((verifyRS) => {
            let getTransazioneRQ = new GetTransazioneByIdRQ();
            getTransazioneRQ.idTransazione = verifyRS.transaction_id;

            this.apiService.getTransazioneById(getTransazioneRQ).subscribe((getTransazioneRS: WebApiResponse) => {
              if (getTransazioneRS.data != null) {
                this.confirmTransaction(getTransazioneRS.data.oid);
              }
            });
          }, (verifyRS) => {
            let getTransazioneRQ = new GetTransazioneByIdRQ();
            getTransazioneRQ.idTransazione = verifyRS.transaction_id;

            this.apiService.getTransazioneById(getTransazioneRQ).subscribe((getTransazioneRS: WebApiResponse) => {
              if (getTransazioneRS.data != null) {
                this.errorTransaction(getTransazioneRS.data.oid);
              }
            });
          });
        }
      });
    }
  }

  verifyGestPayOrder(modalitaPagamento: ModalitaPagamento, cryptedString: string): Promise<PaymentRS> {
    let promise = new Promise<PaymentRS>((resolve, reject) => {
      let rq = PaymentRQ.getGestpayDecryptRQ(modalitaPagamento.oidAlbergo, null, modalitaPagamento.oid, null, null, null, null, null, null, null, null, null, null, null, null, null, modalitaPagamento.gestpayShopId, modalitaPagamento.gestpayApiKey, cryptedString, modalitaPagamento.isTest);

      this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
        if (rs.success && !!rs.transaction_id) {
          resolve(rs);
        } else {
          reject(rs);
        }
      });
    });

    return promise;
  }

  ngOnDestroy() {
    this.setIsLoading(false);
    this.sub.unsubscribe();
  }
}