import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ImageEditComponent } from 'src/app/interfaces/image-edit/image-edit.component';
import { ImagesListEditComponent } from 'src/app/interfaces/images-list-edit/images-list-edit.component';
import { TagCrm, TagCrmImmagini } from 'src/app/shared/models/bo.model';
import { DialogOptions, SetImmagineTagCrmRQ, SetImmagineTagCrmRS, WebApiResponseUpdate } from 'src/app/shared/models/dto';
import { TipoImmagineTagCrm, VersioneMyPage } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-immagini-header-edit',
  templateUrl: './mypage-immagini-header-edit.component.html',
  styleUrls: ['./mypage-immagini-header-edit.component.scss']
})
export class MypageImmaginiHeaderEditComponent extends BaseComponent implements OnInit {

  @Input() oidTagCrm: number;
  @Input() urlImmagine: string;
  @Input() immagini: TagCrmImmagini[];
  @Input() versioneMyPage: VersioneMyPage;

  public title: string;
  public tipoImmagine: TipoImmagineTagCrm;

  constructor(private apiService: ApiService, private utility: UtilityService) { super(); }

  ngOnInit() {
    if (this.versioneMyPage == VersioneMyPage.Versione1) {
      this.title = 'sbm.tabelle.tag-crm.immagine';
      this.tipoImmagine = TipoImmagineTagCrm.Header;
    } else {
      this.title = 'sbm.tabelle.tag-crm.immagineSfondo';
      this.tipoImmagine = TipoImmagineTagCrm.Sfondo;
    }
  }

  editImage() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "50vw";

    let ref = this.showModalDialog(ImageEditComponent, dialogOptions);
    let component = <ImageEditComponent>ref.componentInstance

    component._url = this.urlImmagine;
    component.title = this.title;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(base64 => {
      if (base64 != null) {
        this.setIsLoading(true);

        let rq = new SetImmagineTagCrmRQ();
        rq.oid = this.oidTagCrm;
        rq.tipoImmagine = this.tipoImmagine;
        rq.base64 = base64;

        this.apiService.setImmagineTagCrm(rq).subscribe((res: SetImmagineTagCrmRS) => {
          if (res.data != null) {
            this.urlImmagine = res.data.url;
          }

          this.setIsLoading(false);
        });
      }
    });
  }

  deleteImage() {
    this.showDialogConfirm('globals.eliminareImmagine').then(() => {
      this.setIsLoading(true);

      let rq = new SetImmagineTagCrmRQ();
      rq.oid = this.oidTagCrm;
      rq.tipoImmagine = this.tipoImmagine;
      rq.base64 = '';

      this.apiService.setImmagineTagCrm(rq).subscribe((res: SetImmagineTagCrmRS) => {
        if (res.data != null) {
          this.urlImmagine = res.data.url;
        }

        this.setIsLoading(false);
      });
    }, () => { });
  }

  editImageList(image: TagCrmImmagini) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "50vw";

    let ref = this.showModalDialog(ImagesListEditComponent, dialogOptions);
    let component = <ImagesListEditComponent>ref.componentInstance;

    component.image = Object.assign({}, image);
    component.dialogRef = ref;

    ref.afterClosed().subscribe((img: TagCrmImmagini) => {
      if (img != null) {
        this.setIsLoading(true);

        this.apiService.setImmagineTagCrmList(img).subscribe((res: WebApiResponseUpdate) => {
          if (img.oid > 0) {
            let index = this.utility.findIndexByOid(this.immagini, img.oid);

            if (img.isDeleted) {
              this.immagini.splice(index, 1);
            } else {
              this.immagini[index] = res.data.obj;
            }
          } else {
            this.immagini.push(res.data.obj);
          }

          this.setIsLoading(false);
        });
      }
    });
  }

  addImageList() {
    let img = new TagCrmImmagini();
    img.tagCrm = new TagCrm(this.oidTagCrm);
    img.isAttiva = true;
    this.editImageList(img);
  }

  deleteImageList(image: TagCrmImmagini) {
    this.showDialogConfirm('globals.eliminareImmagine').then(() => {
      image.isDeleted = true;

      this.setIsLoading(true);

      this.apiService.setImmagineTagCrmList(image).subscribe(() => {
        let index = this.utility.findIndexByOid(this.immagini, image.oid);
        this.immagini.splice(index, 1);
        this.setIsLoading(false);
      });
    }, () => { });
  }
}