import { animate, state, style, transition, trigger } from '@angular/animations';
import { ChangeDetectorRef, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild, ViewRef } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingReviewConfigRS, MyPageRecensioneRS } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-recensioni',
  templateUrl: './booking-recensioni.component.html',
  styleUrls: ['./booking-recensioni.component.scss'],
  animations: [
    trigger('fade', [
      state('opaque', style({
        opacity: 1
      })),
      state('transparent', style({
        opacity: 0
      })),
      transition('opaque => transparent', [
        animate('700ms ease-out')
      ]),
      transition('transparent => opaque', [
        animate('700ms ease-in')
      ])
    ])
  ]
})
export class BookingRecensioniComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() recensioni: MyPageRecensioneRS[] | BookingReviewConfigRS[];
  @Input() startSubject: Subject<void>;

  public reviewIndex: number = 0;
  public interval: NodeJS.Timer;
  public state: string = 'transparent';
  public operation: string;
  public goToReviewIndex: number;
  public disableButtons: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;

  @ViewChild('reviewsContainer') reviewsContainer: ElementRef;

  constructor(private cdRef: ChangeDetectorRef,
    private containerService: ContainerService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      this.refreshSize();
    });

    if (this.startSubject != null) {
      this.sub1 = this.startSubject.subscribe(() => {
        this.toggleState();

        if (this.recensioni != null && this.recensioni.length > 1) {
          this.interval = setInterval(this.changeReview.bind(this), 7000);
        }
      });
    } else {
      this.toggleState();

      if (this.recensioni != null && this.recensioni.length > 1) {
        this.interval = setInterval(this.changeReview.bind(this), 7000);
      }
    }
  }

  refreshSize() {
    this.reviewsContainer.nativeElement.scroll({ top: 0, left: this.reviewIndex * this.reviewsContainer.nativeElement.offsetWidth });
  }

  nextReview() {
    if (this.reviewIndex < this.recensioni.length - 1) {
      this.reviewIndex++;
    } else {
      this.reviewIndex = 0;
    }

    this.reviewsContainer.nativeElement.scroll({ top: 0, left: this.reviewIndex * this.reviewsContainer.nativeElement.offsetWidth });
  }

  previousReview() {
    if (this.reviewIndex > 0) {
      this.reviewIndex--;
    } else {
      this.reviewIndex = this.recensioni.length - 1;
    }

    this.reviewsContainer.nativeElement.scroll({ top: 0, left: this.reviewIndex * this.reviewsContainer.nativeElement.offsetWidth });
  }

  showReview(index: number) {
    this.reviewIndex = index;
    this.reviewsContainer.nativeElement.scroll({ top: 0, left: this.reviewIndex * this.reviewsContainer.nativeElement.offsetWidth });
    this.goToReviewIndex = null;
  }

  toggleState() {
    this.state = this.state == 'opaque' ? 'transparent' : 'opaque';

    if (!(this.cdRef as ViewRef).destroyed) {
      this.cdRef.detectChanges();
    }
  }

  onAnimationDone(toState: string) {
    if (toState == 'transparent') {
      if (this.goToReviewIndex != null) {
        this.showReview(this.goToReviewIndex);
        this.toggleState();
      } else if (this.operation == 'next') {
        this.nextReview();
        this.toggleState();
      } else if (this.operation == 'previous') {
        this.previousReview();
        this.toggleState();
      }
    } else if (toState == 'opaque') {
      this.disableButtons = false;
    }
  }

  stopInterval() {
    if (this.interval != null) {
      clearInterval(this.interval);
    }
  }

  resumeInterval() {
    if (this.interval != null) {
      this.interval = setInterval(this.changeReview.bind(this), 7000);
    }
  }

  changeReview() {
    this.operation = 'next';
    this.toggleState();
  }

  goToReview(index: number) {
    this.goToReviewIndex = index;
    this.toggleState();
  }

  ngOnDestroy() {
    clearInterval(this.interval);

    this.sub.unsubscribe();

    if (this.sub1 != null) {
      this.sub1.unsubscribe();
    }
  }
}