import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BookingSolutionOfferRS } from 'src/app/shared/models/dto';
import { LocalizationService } from 'src/app/shared/services/localization.service';

@Component({
  selector: 'app-pacchetto-details',
  templateUrl: './pacchetto-details.component.html',
  styleUrls: ['./pacchetto-details.component.scss']
})
export class PacchettoDetailsComponent implements OnInit {

  @Input() promozione: BookingSolutionOfferRS;

  @Output() backEvent: EventEmitter<any> = new EventEmitter();

  public promozioneValidaDalAl: string;
  public promozionePrenotabileDalAl: string;

  constructor(private translationService: LocalizationService) { }

  ngOnInit() {
    if (this.promozione != null) {
      if (!!this.promozione.validaDal || !!this.promozione.validaAl) {
        this.promozioneValidaDalAl = this.translationService.getTranslationSync('booking.offer.promozioneValidaDalAl', { dal: !!this.promozione.validaDal ? this.promozione.validaDal : '---', al: !!this.promozione.validaAl ? this.promozione.validaAl : '---' });
      }

      if (!!this.promozione.prenotabileDal || !!this.promozione.prenotabileAl) {
        this.promozionePrenotabileDalAl = this.translationService.getTranslationSync('booking.offer.promozionePrenotabileDalAl', { dal: !!this.promozione.prenotabileDal ? this.promozione.prenotabileDal : '---', al: !!this.promozione.prenotabileAl ? this.promozione.prenotabileAl : '---' });
      }
    }
  }
}