import { Component, Input, OnInit } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { MyPageRichiestaRS } from 'src/app/shared/models/dto';

@Component({
  selector: 'app-mypage-richiesta',
  templateUrl: './mypage-richiesta.component.html',
  styleUrls: ['./mypage-richiesta.component.scss']
})
export class MypageRichiestaComponent extends BaseComponent implements OnInit {

  public notti: number;

  @Input() richiesta: MyPageRichiestaRS;

  constructor() {
    super();
  }

  ngOnInit() {
    if (this.richiesta != null) {
      this.notti = Math.max(moment(this.getDate(this.richiesta.partenza)).diff(moment(this.getDate(this.richiesta.arrivo)), 'days'), 1);
    }
  }
}