import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { MatExpansionPanel } from '@angular/material/expansion';
import * as moment from 'moment';
import { Subject } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  TipoCameraTariffaDetailsDialogComponent,
} from 'src/app/interfaces/tipo-camera-tariffa-details-dialog/tipo-camera-tariffa-details-dialog.component';
import {
  DialogOptions,
  MyPageCameraRS,
  MyPagePropostaExtraRS,
  MyPagePropostaRS,
  MyPageProposteXTipoCameraRS,
  MyPageTariffaRS,
  MyPageTipoCameraRS,
} from 'src/app/shared/models/dto';
import { TipoScontoProposta } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import clip from 'text-clipper';

declare let holipay;

@Component({
  selector: 'app-mypage-proposta',
  templateUrl: './mypage-proposta.component.html',
  styleUrls: ['./mypage-proposta.component.scss']
})
export class MypagePropostaComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() set _proposteXTipoCamera(value: MyPageProposteXTipoCameraRS) {
    if (value != null) {
      this.proposteXTipoCamera = value;

      this.immagini = this.proposteXTipoCamera.tipoCamera.immagini.map(x => x.immagine);
      this.descrizioni = this.proposteXTipoCamera.tipoCamera.immagini.map(x => x.descrizione);

      if (!!this.proposteXTipoCamera.tipoCamera.descrizione) {
        let indicator = '<a class="continuaLink" href="#" onclick="event.preventDefault()">' + this.localizationService.getTranslationSync('booking.my-page.more') + '</a>';

        this.shortDescription = clip(this.proposteXTipoCamera.tipoCamera.descrizione, 115, { html: true, breakWords: true });

        let index = this.shortDescription.lastIndexOf('');

        if (index >= 0) {
          this.shortDescription = this.shortDescription.slice(0, index) + indicator + this.shortDescription.slice(index + 1);
        } else if (this.shortDescription != this.proposteXTipoCamera.tipoCamera.descrizione) {
          this.shortDescription += indicator;
        }
      }

      this.notti = Math.max(moment(this.getDate(this.proposteXTipoCamera.partenza)).diff(moment(this.getDate(this.proposteXTipoCamera.arrivo)), 'days'), 1);

      setTimeout(() => {
        this.refreshHolipayWidget();
      }, 200);
    }
  };

  @Input() camera: MyPageCameraRS;
  @Input() cameraTabIndex: number;
  @Input() proposteSelected: MyPagePropostaRS[];
  @Input() isPrenotato: boolean;
  @Input() numeroCamere: number;
  @Input() isVisualizzaDettagli: boolean;
  @Input() isVisualizzaDescrizionePoliticaCancellazione: boolean;
  @Input() isVisualizzaDescrizioneDettagliataTariffa: boolean;
  @Input() isVisualizzaInformazioniPropostaMyPage: boolean;
  @Input() isExpired: boolean;

  @Input() set _maxImageHeight(value: number) {
    if (value != null) {
      this.maxImageHeight = value > 400 ? 400 : value;
      this.updateSizeSubject.next();
    } else {
      this.maxImageHeight = 400;
    }
  };

  @Input() isVisualizzaDettaglioGiornaliero: boolean;
  @Input() isVisualizzaProposteEspanse: boolean;

  @Output() _onPropostaSelected: EventEmitter<number> = new EventEmitter();

  @ViewChild('tariffePanel') tariffePanel: MatExpansionPanel;

  public shortDescription: string;
  public immagini: string[] = [];
  public descrizioni: string[] = [];
  public showTariffe: boolean = false;
  public proposteXTipoCamera: MyPageProposteXTipoCameraRS;
  public updateSizeSubject: Subject<void> = new Subject();
  public maxImageHeight: number = 400;
  public notti: number;
  public enumTipoScontoProposta = TipoScontoProposta;

  constructor(public utility: UtilityService,
    public localizationService: LocalizationService,
    public containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    this.showTariffe = this.isVisualizzaProposteEspanse;
  }

  showDetails(tipoCamera: MyPageTipoCameraRS, tariffa?: MyPageTariffaRS, extra?: MyPagePropostaExtraRS[], isPrezziPersona?: boolean) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'dialog-overlay';

    let ref = this.showModalDialog(TipoCameraTariffaDetailsDialogComponent, dialogOptions);
    let component = <TipoCameraTariffaDetailsDialogComponent>ref.componentInstance;
    component.tipoCamera = tipoCamera;
    component.tariffa = tariffa;
    component.extra = extra;
    component.isPrezziPersona = isPrezziPersona;
    component.isVisualizzaDettaglioGiornaliero = this.isVisualizzaDettaglioGiornaliero;
    component.isVisualizzaDettagli = this.isVisualizzaDettagli;
  }

  onPropostaSelected(proposta: MyPagePropostaRS) {
    if (this.cameraTabIndex == 0 || this.proposteSelected[this.cameraTabIndex - 1] != null) {
      let allProposteSelected: boolean = this.proposteSelected.every(x => x != null);

      this.proposteSelected[this.cameraTabIndex] = proposta;

      if (this.proposteSelected.every(x => x != null)) {
        if (!allProposteSelected) {
          this._onPropostaSelected.emit(-1);
        } else {
          this._onPropostaSelected.emit(this.cameraTabIndex);
        }
      } else {
        if (this.cameraTabIndex < this.numeroCamere - 1) {
          this._onPropostaSelected.emit(this.cameraTabIndex + 1);
        } else {
          this._onPropostaSelected.emit(this.cameraTabIndex);
        }
      }
    } else {
      let cameraIndex = this.proposteSelected.findIndex(x => x != null);

      if (cameraIndex == -1) {
        cameraIndex = 0;
      }

      this.showError('booking.my-page.selezionareProposta', { camera: cameraIndex + 1 }).then(() => {
        this._onPropostaSelected.emit(cameraIndex);
      });
    }
  }

  isNonPrenotabile(): boolean {
    return !this.isPrenotato && this.proposteXTipoCamera.proposte.every(x => !x.isPrenotabile);
  }

  onShowTariffeClick() {
    if (!this.tariffePanel.disabled) {
      if (!this.showTariffe) {
        this.showTariffe = true;
      }

      this.tariffePanel.toggle();

      this.tariffePanel.disabled = true;
    }
  }

  afterExpand() {
    this.tariffePanel.disabled = false;
    this.refreshHolipayWidget();
  }

  afterCollapse() {
    this.showTariffe = false;
    this.tariffePanel.disabled = false;
  }

  refreshHolipayWidget() {
    if (!this.isPrenotato && !this.isExpired && this.proposteXTipoCamera.proposte.some(x => x.isHolipayDisponibile && x.isPrenotabile) && holipay != null) {
      setTimeout(() => {
        holipay.updateWidgets()
      }, 100);
    }
  }

  ngOnDestroy() {
    this.updateSizeSubject.complete();
  }
}