import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/core/local-storage.service';

import { BaseComponent } from '../../base-component/base-component';

@Component({
  selector: 'app-popup-check',
  templateUrl: './popup-check.component.html',
  styleUrls: ['./popup-check.component.scss']
})
export class PopupCheckComponent extends BaseComponent implements OnInit {

  constructor(private localStorage: LocalStorageService) { super(); }

  ngOnInit() {
    if (this.localStorage.get('popupCheckFailed') == true) {
      window.close();
    } else {
      this.setIsLoading(true);
      this.localStorage.set('popupCheckSuccess', true, true);
    }
  }
}