import { SocialAuthService, SocialUser } from '@abacritt/angularx-social-login';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { GoogleTagManagerService } from 'angular-google-tag-manager';
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, Nazione } from 'src/app/shared/models/bo.model';
import {
  AccettaPreventivoRQ,
  GetNazioniTradotteRQ,
  MyPageAlbergoRS,
  MyPageClienteRS,
  MyPageExtra,
  MyPagePagamentoRS,
  MyPagePortaleRS,
  MyPagePreventivoRS,
  MyPagePropostaRS,
  PaymentModel,
  PrenotaPreventivoRQ,
  PrenotaPreventivoRS,
  VerificaVoucherRQ,
  VerificaVoucherRS,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-prenotazione-form',
  templateUrl: './mypage-prenotazione-form.component.html',
  styleUrls: ['./mypage-prenotazione-form.component.scss']
})
export class MypagePrenotazioneFormComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() lingua: string;
  @Input() albergo: MyPageAlbergoRS;
  @Input() portale: MyPagePortaleRS;
  @Input() preventivo: MyPagePreventivoRS;
  @Input() proposteSelected: MyPagePropostaRS[];
  @Input() totale: number;
  @Input() condizioniPrenotazione: string;
  @Input() condizioniContratto: string;
  @Input() modalitaPagamento: MyPagePagamentoRS[];
  @Input() allModalitaPagamento: MyPagePagamentoRS[];
  @Input() extraSelected: MyPageExtra[][];
  @Input() oidSistemazioni: number[];
  @Input() gtmService: GoogleTagManagerService;
  @Input() oidPreventivo: number;
  @Input() isAccettaPreventiviConPagamentoImmediato: boolean;
  @Input() v2: boolean;
  @Input() numeroVoucher: number;

  @Input() set _ospiti(value: Cliente[]) {
    if (value != null) {
      this.ospiti = value;
      this.updateInformazioniPrimoOspite();
    }
  };

  @Input() set _cliente(value: MyPageClienteRS) {
    this.cliente = new Cliente();

    if (value != null) {
      this.cliente.nome = value.nome;
      this.cliente.cognome = value.cognome;
      this.cliente.email = value.email;
      this.cliente.cellulare = value.cellulare;
      this.cliente.nazioneObject = value.nazione != null ? Object.assign({}, value.nazione) : new Nazione();
      this.cliente.privacy = value.privacy;
      this.cliente.newsletter = value.newsletter;
      this.updateInformazioniPrimoOspite();
      this.resetVoucher();
      this.checkFormValidity();
    }
  };

  @Input() set _sistemazioni(value: boolean) {
    if (value != null) {
      this.sistemazioni = value;
      this.checkFormValidity();
    }
  };

  @Output() confirmed: EventEmitter<any> = new EventEmitter();
  @Output() onScontoVoucherChange: EventEmitter<number> = new EventEmitter();

  public formValid: boolean = false;
  public errorMessage: string = 'globals.messages.inserireDati';
  public noPaymentMethod: boolean = false;
  public cliente: Cliente;
  public note: string;
  public oraArrivo: string;
  public sub: Subscription;
  public sistemazioni: boolean;
  public voucher: string;
  public oidVoucher: number;
  public scontoVoucher: number = 0;
  public ospiti: Cliente[] = [];
  public primoOspiteUpdated: boolean = false;
  public getNazioniTradotteRQ = new GetNazioniTradotteRQ();
  public caparra: number = 0;

  constructor(public utility: UtilityService,
    public apiService: ApiService,
    private authService: SocialAuthService) {
    super();
  }

  ngOnInit() {
    this.sub = this.authService.authState.subscribe((user: SocialUser) => {
      if (user != null && this.cliente != null) {
        this.cliente.nome = user.firstName;
        this.cliente.cognome = user.lastName;
        this.cliente.email = user.email;
        this.updateInformazioniPrimoOspite(true);
        this.resetVoucher();
        this.checkFormValidity();
      }
    });

    this.getNazioniTradotteRQ.addEmptyItem = !this.portale.isNazioneObbligatoria;
    this.getNazioniTradotteRQ.lingua = this.lingua;
  }

  checkFormValidity() {
    if (!!this.cliente.nome && !!this.cliente.cognome && !!this.cliente.email && (!this.portale.isCellulareObbligatorio || !!this.cliente.cellulare) && (!this.portale.isNazioneObbligatoria || (this.cliente.nazioneObject != null && this.cliente.nazioneObject.oid > 0)) && (!this.portale.isSistemazioneObbligatoria || !this.sistemazioni || this.oidSistemazioni.every(x => x > 0)) && this.ospiti.every(x => (!x.nome && !x.cognome && !x.email) || (!!x.nome && !!x.cognome && !!x.email))) {
      if (this.utility.validateEmail(this.cliente.email)) {
        this.formValid = true;
      } else {
        this.errorMessage = 'globals.messages.emailNonValida';
        this.formValid = false;
      }
    } else {
      this.errorMessage = 'globals.messages.inserireDati';
      this.formValid = false;
    }
  }

  accetta() {
    if (this.formValid) {
      this.setIsLoading(true);

      let rq = new AccettaPreventivoRQ();
      rq.oidPreventivo = this.oidPreventivo;
      rq.cliente = this.cliente;
      rq.oidProposte = this.proposteSelected.map(x => x.oid);
      rq.oidSistemazioni = this.oidSistemazioni;

      let i = 0;
      rq.extras = [];

      for (let extras of this.extraSelected) {
        rq.extras.push(extras.filter(x => x.quantita > 0));

        for (let extra of rq.extras[i]) {
          extra.giorni = extra.giorni.filter(x => x.selected);
        }

        i++;
      }

      this.apiService.accettaMyPage(rq).subscribe(() => {
        this.setIsLoading(false);

        this.confirmed.emit();

        this.showInfo('booking.my-page.preventivoAccettato').then(() => {
          this.setIsLoading(true);
          window.location.reload();
        })
      });
    }
  }

  prenota(pagamento: PaymentModel) {
    if (!pagamento.isPrenotazioneCreata) {
      this.setIsLoading(true);

      let rq = new PrenotaPreventivoRQ();
      rq.oidProposte = this.proposteSelected.map(x => x.oid);
      rq.cliente = this.cliente;
      rq.pagamento = pagamento;
      rq.oidSistemazioni = this.oidSistemazioni;
      rq.ospiti = this.ospiti;
      rq.note = this.note;
      rq.oraArrivo = this.oraArrivo;
      rq.oidVoucher = this.oidVoucher;

      let i = 0;

      rq.extras = [];

      for (let extras of this.extraSelected) {
        rq.extras.push(extras.filter(x => x.quantita > 0));

        for (let extra of rq.extras[i]) {
          extra.giorni = extra.giorni.filter(x => x.selected);
        }

        i++;
      }

      this.apiService.prenotaMyPage(rq).subscribe((rs: PrenotaPreventivoRS) => {
        this.onPrenotazioneCreata(rs.data.transazione?.oid, rs.data.bookingCode);
      });
    } else {
      this.onPrenotazioneCreata(pagamento.oidTransazione, pagamento.codicePrenotazione);
    }
  }

  onPrenotazioneCreata(bookingIdGA: number, bookingCode: string) {
    if (this.gtmService != null) {
      this.gtmService.pushTag({
        event: 'new_booking_mypage',
        booking: {
          hotelId: this.albergo.codice,
          hotelName: this.albergo.nome,
          portalId: this.portale.oid,
          portalNome: this.portale.nome,
          transactionId: bookingIdGA,
          bookingCode: bookingCode,
          currencyCode: 'EUR',
          rooms: this.proposteSelected.length,
          roomNights: this.getNotti(),
          totalAmount: this.totale,
          totalAmountTax: this.totale,
          checkInDate: this.getDate(this.proposteSelected[0].arrivo),
          checkOutDate: this.getDate(this.proposteSelected[0].partenza),
          lingua: this.lingua
        }
      });
    }

    this.setIsLoading(false);

    this.confirmed.emit();

    this.showInfo('globals.messages.prenotazioneCompletata').then(() => {
      this.setIsLoading(true);
      window.location.reload();
    });
  }

  getNotti(): number {
    return Math.max(moment(this.getDate(this.proposteSelected[0].partenza)).diff(moment(this.getDate(this.proposteSelected[0].arrivo)), 'days'), 1);
  }

  verificaVoucher() {
    this.setIsLoading(true);

    let rq = new VerificaVoucherRQ();
    rq.oidAlbergo = this.albergo.oid;
    rq.oidPortale = this.portale.oid;
    rq.codice = this.voucher;
    rq.email = this.cliente.email;
    rq.arrivo = this.preventivo.arrivo;

    this.apiService.verificaVoucher(rq).subscribe((res: VerificaVoucherRS) => {
      if (res.data.oidVoucher != null && res.data.oidVoucher > 0) {
        this.oidVoucher = res.data.oidVoucher;
        this.scontoVoucher = res.data.sconto;
        this.onScontoVoucherChange.emit(this.scontoVoucher);
        this.showInfo('booking.my-page.codiceApplicato', { value: Math.min(res.data.sconto, this.totale) });
      } else {
        this.resetVoucher();
        this.showError('booking.my-page.erroreVoucher');
      }

      this.setIsLoading(false);
    });
  }

  resetVoucher() {
    if (this.voucher != null || this.oidVoucher != null || this.scontoVoucher != 0) {
      this.voucher = null;
      this.oidVoucher = null;
      this.scontoVoucher = 0;
      this.onScontoVoucherChange.emit(this.scontoVoucher);
    }
  }

  updateInformazioniPrimoOspite(force: boolean = false) {
    if (this.portale.isRichiediInformazioniOspiti && (force || !this.primoOspiteUpdated) && this.ospiti != null && this.ospiti.length > 1) {
      this.ospiti[0].nome = this.cliente.nome;
      this.ospiti[0].cognome = this.cliente.cognome;
      this.ospiti[0].email = this.cliente.email;
    }

    if (force) {
      this.primoOspiteUpdated = false;
    }
  }

  onCaparraChange(caparra: number) {
    setTimeout(() => {
      this.caparra = caparra;
    }, 200);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}