import { SocialAuthService, SocialUser } from '@abacritt/angularx-social-login';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { Subscription } from 'rxjs';
import { AuthenticationService } from 'src/app/core/auth/authentication.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  PrivacyNewsletterDialogComponent,
} from 'src/app/interfaces/form-v1/privacy-newsletter-dialog/privacy-newsletter-dialog.component';
import { Albergo, Cliente } from 'src/app/shared/models/bo.model';
import {
  DialogOptions,
  GetPasswordClienteRQ,
  GetPrivacyPortaleRQ,
  NewsLetterPortale,
  PrivacyPortale,
  RecuperaPasswordRQ,
  RecuperaPasswordRS,
  RegistraClienteRQ,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-login',
  templateUrl: './booking-login.component.html',
  styleUrls: ['./booking-login.component.scss']
})
export class BookingLoginComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() oidPortale: number;
  @Input() oidAlbergo: number;
  @Input() lingua: string;
  @Input() isVisualizzaBottoniLoginSocial: boolean;
  @Input() newsLetterPortale: NewsLetterPortale;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public email: string;
  public recuperoPassword: boolean = false;
  public registrazione: boolean = false;

  public cliente: Cliente = new Cliente();

  public sub: Subscription;

  constructor(private _authService: AuthenticationService,
    public utility: UtilityService,
    private apiService: ApiService,
    private authService: SocialAuthService,
    private containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    this.sub = this.authService.authState.subscribe((user: SocialUser) => {
      if (user != null) {
        let rq = new GetPasswordClienteRQ();
        rq.oidAlbergo = this.oidAlbergo;
        rq.email = user.email;

        this.apiService.getPasswordCliente(rq).subscribe((res: WebApiResponse) => {
          if (!!res.data) {
            this.email = user.email;
            this.login(res.data);
          } else {
            this.showError('booking.credenzialiNonCorrette');
          }
        });
      }
    });
  }

  login(password: string) {
    if (!!this.email && !!password) {
      this.setIsLoading(true);

      this._authService.loginMySelf(false, this.oidAlbergo, null, this.email, password).then(() => {
        this.dialogRef.close(true);
      }, () => { });
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  recuperaPassword(email: string) {
    if (!!email) {
      this.setIsLoading(true);

      let rq = new RecuperaPasswordRQ();
      rq.email = email;
      rq.oidPortale = this.oidPortale;

      this.apiService.recuperaPassword(rq).subscribe((res: RecuperaPasswordRS) => {
        this.setIsLoading(false);

        if (res.data.success) {
          this.showInfo('booking.emailInviata').then(() => {
            this.recuperoPassword = false;
          });
        } else {
          this.showError('booking.erroreRecuperoPassword').then(() => {
            this.recuperoPassword = false;
          });
        }
      });
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  register(email: string, password: string) {
    if (!!this.cliente.nome && !!this.cliente.cognome && !!this.cliente.email && !!this.cliente.password) {
      if (this.utility.validateEmail(this.cliente.email)) {
        if (email == this.cliente.email) {
          if (this.utility.validatePassword(this.cliente.password)) {
            if (password == this.cliente.password) {
              this.setIsLoading(true);

              this.cliente.albergo = new Albergo(this.oidAlbergo);
              this.cliente.codiceLingua = this.lingua;

              let rq = new RegistraClienteRQ();
              rq.oidPortale = this.oidPortale;
              rq.cliente = this.cliente;

              this.apiService.registraCliente(rq).subscribe((rs: WebApiResponse) => {
                this.setIsLoading(false);

                if (rs.errori == null || rs.errori.length == 0) {
                  this.showInfo('booking.successoRegistrazione').then(() => {
                    this.email = email;
                    this.login(password);
                  });
                }
              });
            } else {
              this.showError('booking.passwordDiverse');
            }
          } else {
            this.showError('globals.messages.passwordNonValida');
          }
        } else {
          this.showError('booking.emailDiverse');
        }
      } else {
        this.showError('globals.messages.emailNonValida');
      }
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  openPrivacyDialog() {
    this.setIsLoading(true);

    let rq = new GetPrivacyPortaleRQ();
    rq.oidPortale = this.oidPortale;
    rq.lingua = this.lingua;

    this.apiService.getPrivacyPortale(rq).subscribe(rs => {
      this.setIsLoading(false);
      this.openPrivacyNewsletterDialog(rs.data);
    });
  }

  openNewsletterDialog() {
    this.openPrivacyNewsletterDialog(this.newsLetterPortale);
  }

  openPrivacyNewsletterDialog(privacyNewsletterPortale: PrivacyPortale | NewsLetterPortale) {
    if (!!privacyNewsletterPortale.html) {
      let dialogOptions = new DialogOptions();

      if (this.containerService.dimension.width >= 992) {
        dialogOptions.width = "50vw";
      } else {
        dialogOptions.width = "80vw";
      }

      let ref = this.showModalDialog(PrivacyNewsletterDialogComponent, dialogOptions);
      let component = <PrivacyNewsletterDialogComponent>ref.componentInstance;

      component.text = privacyNewsletterPortale.html;
    } else if (!!privacyNewsletterPortale.url) {
      this.utility.openUrlInNewTab(privacyNewsletterPortale.url);
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}