  public prezzoMinimo: number = null;
  public enumMealPlanType = MealPlanType;
  public trattamento: MealPlanType = null;
  public clickSubject: Subject<void> = new Subject();
  public showSearchButtonSubject: Subject<void> = new Subject();
  public dayUse: boolean;
  public extraSelect: boolean;
  public riduzioni: BookingReductionConfigRS[];
  public prenotaSubject: Subject<void> = new Subject();
  public adultiDefault: number = 0;
