  public tariffeComparazione: BookingSolutionPortalRS[] = [];
  public pacchettoHover: boolean = false;
  public promozioni: BookingSolutionOfferRS[] = [];
  public pacchettiOpenedFromHome: boolean;
  public cancellazioni: BookingSolutionCancellationRS[];
  public loginSubject: Subject<void> = new Subject();
  public bulletPoints: string[];
