  public numeroUltimePrenotazioni: number;
  public descrizionePopup: string;
  public numeroPersoneCollegateTranslated: string;
  public numeroUltimePrenotazioniTranslated: string;
  public popupHidden: boolean = true;
  public numeroPersoneCollegatePopupHidden: boolean = true;
  public numeroUltimePrenotazioniPopupHidden: boolean = true;
  public shortScreen: boolean;
  public isEtaRiduzioniAttiva: boolean;
  public gtmService: GoogleTagManagerService;
