  public prenotazioneCompleted: boolean = false;
  public urlImmagineSfondo: string;
  public allModalitaPagamento: BookingSolutionPaymentRS[] = [];
  public promozioniInEvidenza: BookingSolutionHighlightedOfferRS[] = [];
  public banner: BookingSolutionBannerRS[] = [];
