  public isVisualizzaTariffeEspanse: boolean;
  public verificaCodicePromozionaleSubject: Subject<void> = new Subject();
  public noExtraSelected: boolean = true;
  public enumTipoVisualizzazioneBanner = TipoVisualizzazioneBanner;
