import { AfterViewInit, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { MyPageRecensioneRS } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-recensioni-v2',
  templateUrl: './mypage-recensioni-v2.component.html',
  styleUrls: ['./mypage-recensioni-v2.component.scss']
})
export class MypageRecensioniV2Component implements OnInit, AfterViewInit, OnDestroy {

  @Input() recensioni: MyPageRecensioneRS[];

  @ViewChild('recensioniContainer') recensioniContainer: ElementRef;

  public recensioniPageIndex: number = 0;
  public recensioniPages: MyPageRecensioneRS[][] = [];
  public recensioneIndex: number = 0;
  public changeEnabled: boolean = true;
  public cursorStartX: number;
  public cursorStartY: number;
  public cursorEndX: number;
  public cursorEndY: number;
  public width: number;
  public startEndScroll: number;
  public sub: Subscription;

  constructor(public utility: UtilityService, public containerService: ContainerService) { }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      setTimeout(() => {
        this.calculateWidth();
        this.toIndex(this.recensioneIndex);
      }, 500);
    });

    let i = 0;
    let page: MyPageRecensioneRS[] = [];

    do {
      page = this.recensioni.slice(i, i + 3);

      if (page.length > 0) {
        this.recensioniPages.push(page);
      }

      i += 3;
    } while (page.length == 3)
  }

  ngAfterViewInit() {
    this.calculateWidth();
  }

  previousRecensione() {
    if (this.changeEnabled && this.recensioneIndex > 0) {
      this.changeEnabled = false;

      if (this.recensioneIndex == this.recensioni.length - 1) {
        this.recensioniContainer.nativeElement.scrollTo({ top: 0, left: this.recensioniContainer.nativeElement.scrollLeft - this.startEndScroll, behavior: 'smooth' });
      } else {
        this.recensioniContainer.nativeElement.scrollTo({ top: 0, left: this.recensioniContainer.nativeElement.scrollLeft - this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.recensioneIndex--;
        this.changeEnabled = true;
      }, 500);
    }
  }

  nextRecensione() {
    if (this.changeEnabled && this.recensioneIndex < this.recensioni.length - 1) {
      this.changeEnabled = false;

      if (this.recensioneIndex == 0) {
        this.recensioniContainer.nativeElement.scrollTo({ top: 0, left: this.recensioniContainer.nativeElement.scrollLeft + this.startEndScroll, behavior: 'smooth' });
      } else {
        this.recensioniContainer.nativeElement.scrollTo({ top: 0, left: this.recensioniContainer.nativeElement.scrollLeft + this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.recensioneIndex++;
        this.changeEnabled = true;
      }, 500);
    }
  }

  toIndex(index: number) {
    if (this.recensioniContainer != null && this.changeEnabled) {
      this.changeEnabled = false;
      this.recensioniContainer.nativeElement.scrollTo({ top: 0, left: index >= 1 ? this.startEndScroll + (this.width * (index - 1)) : 0, behavior: 'smooth' });

      setTimeout(() => {
        this.recensioneIndex = index;
        this.changeEnabled = true;
      }, 500);
    }
  }

  checkGesture() {
    if (this.recensioni.length > 1) {
      if (Math.abs(this.cursorStartX - this.cursorEndX) > 50 && Math.abs(this.cursorStartY - this.cursorEndY) < 50) {
        if (this.cursorStartX < this.cursorEndX) {
          this.previousRecensione();
        } else {
          this.nextRecensione();
        }
      }
    }
  }

  calculateWidth() {
    if (this.recensioniContainer != null) {
      this.width = (this.recensioniContainer.nativeElement.offsetWidth * 70 / 100) + 16;
      this.startEndScroll = this.width - (this.recensioniContainer.nativeElement.offsetWidth * 15 / 100) + 24;
    }
  }

  previous() {
    if (this.recensioniPageIndex > 0) {
      this.recensioniPageIndex--;
    }
  }

  next() {
    if (this.recensioniPageIndex < this.recensioniPages.length - 1) {
      this.recensioniPageIndex++;
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}