import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {
  PagamentoFormErrorComponent,
} from '../interfaces/pagamento-form/pagamento-form-error/pagamento-form-error.component';
import {
  PagamentoFormSuccessComponent,
} from '../interfaces/pagamento-form/pagamento-form-success/pagamento-form-success.component';
import { PaymentFormComponent } from '../interfaces/pagamento-form/payment-form/payment-form.component';
import { PopupCheckComponent } from '../interfaces/pagamento-form/popup-check/popup-check.component';
import { BloccoRecensioniComponent } from './blocco-recensioni/blocco-recensioni.component';
import { BookingSelfCheckinComponent } from './booking-self-checkin/booking-self-checkin.component';
import { BookingComponent } from './booking/booking.component';
import { BookingFormV1Component } from './form-richiesta/booking-form-v1/booking-form-v1.component';
import { MypageComponent } from './mypage/mypage/mypage.component';
import { OfferComponent } from './offer/offer/offer.component';
import { PaymailComponent } from './paymail/paymail.component';
import { RoomingListComponent } from './rooming-list/rooming-list/rooming-list.component';
import { SondaggioFormComponent } from './sondaggio-form/sondaggio-form/sondaggio-form.component';
import { TemplateComponent } from './template/template.component';
import { UnsubscribeComponent } from './unsubscribe/unsubscribe.component';

const routes: Routes = [
  {
    path: "",
    component: BookingComponent
  },
  {
    path: "mypage",
    component: MypageComponent
  },
  {
    path: "paymail",
    component: PaymailComponent
  },
  {
    path: "formv1",
    component: BookingFormV1Component
  },
  {
    path: "self-checkin",
    component: BookingSelfCheckinComponent
  },
  {
    path: "sondaggio",
    component: SondaggioFormComponent
  },
  {
    path: "rooming-list",
    component: RoomingListComponent
  },
  {
    path: "offer",
    component: OfferComponent
  },
  {
    path: "pagamentoSuccess",
    component: PagamentoFormSuccessComponent
  },
  {
    path: "pagamentoError",
    component: PagamentoFormErrorComponent
  },
  {
    path: "popupCheck",
    component: PopupCheckComponent
  },
  {
    path: "paymentForm",
    component: PaymentFormComponent
  },
  {
    path: "template",
    component: TemplateComponent
  },
  {
    path: "unsubscribe",
    component: UnsubscribeComponent
  },
  {
    path: "recensioni",
    component: BloccoRecensioniComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class BookingRoutingModule { }