import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, SondaggioRisposta, SondaggioXLingua } from 'src/app/shared/models/bo.model';
import {
  DomandaRispostaSondaggio,
  GetSondaggioPrenotazioneDomandaRS,
  GetSondaggioPrenotazioneRQ,
  GetSondaggioPrenotazioneRS,
  PortaleSondaggioPrenotazione,
} from 'src/app/shared/models/dto';
import { Pagina, StatoSondaggio, TipoDomandaSondaggio } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-sondaggio-form',
  templateUrl: './sondaggio-form.component.html',
  styleUrls: ['./sondaggio-form.component.scss']
})
export class SondaggioFormComponent extends BaseComponent implements OnInit, OnDestroy {

  public initialized: boolean = false;
  public sondaggio: SondaggioXLingua = new SondaggioXLingua();
  public domande: GetSondaggioPrenotazioneDomandaRS[] = [];
  public risposta: SondaggioRisposta = new SondaggioRisposta();
  public portale: PortaleSondaggioPrenotazione = new PortaleSondaggioPrenotazione();
  public sub: Subscription;
  public stepsCompleted: boolean[] = [];
  public rispostaJSON: any = {};
  public enumTipoDomanda = TipoDomandaSondaggio;
  public sondaggioCompletato: boolean = false;
  public risposteDomande: DomandaRispostaSondaggio[];
  public clienteStepVisible: boolean = false;
  public clienteStepCompleted: boolean = true;
  public backgroundImage: string;
  public sogliaPunteggio: number;
  public testoFinale: string;
  public smileIcons: string[] = ['tired', 'frown', 'meh', 'smile', 'grin'];

  constructor(private containerService: ContainerService,
    private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router,
    private deviceService: DeviceDetectorService,
    private localizationService: LocalizationService,
    private cssLoaderService: CssLoaderService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('sondaggio-form-scrolling');

    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {
      let uid = x.get('uid');
      let oidPrenotazione = x.get('oidPrenotazione');
      let lingua = x.get('lingua');

      if (!!uid || (!!oidPrenotazione && !isNaN(+oidPrenotazione))) {
        let rq = new GetSondaggioPrenotazioneRQ();
        rq.uid = uid;
        rq.oidPrenotazione = +oidPrenotazione;
        rq.isMobile = this.deviceService.isMobile();
        rq.lingua = lingua;

        this.apiService.getSondaggioPrenotazione(rq).subscribe((res: GetSondaggioPrenotazioneRS) => {
          if (res.data.sondaggio != null && res.data.risposta != null && res.data.domande.length > 0) {
            this.sondaggio = res.data.sondaggio;
            this.sogliaPunteggio = res.data.sogliaPunteggio;
            this.domande = res.data.domande;
            this.risposta = res.data.risposta;

            if (res.data.portale != null) {
              this.portale = res.data.portale;
            }

            this.cssLoaderService.load(res.data.oidAlbergo, Pagina.Sondaggio, res.data.portale?.oidUbicazioneIstat);

            if (this.risposta.stato == StatoSondaggio.Visualizzato) {
              if (this.risposta.cliente == null) {
                this.risposta.cliente = new Cliente();
                this.clienteStepVisible = true;
                this.clienteStepCompleted = false;
              }

              for (let domanda of this.domande) {
                this.stepsCompleted.push(!domanda.isObbligatoria);

                this.rispostaJSON[domanda.titolo] = '';

                if (!!domanda.valori) {
                  domanda.valoriArray = domanda.valori.split(';');
                  domanda.valoriArraySelected = [];
                }

                domanda.numeroStelle = 0;
              }
            } else {
              this.initRiepilogoSondaggio();
            }

            this.utility.setLingua(res.data.sondaggio.lingua.codice).then(() => {
              if (!!res.data.traduzioni) {
                let json;

                try {
                  json = JSON.parse(res.data.traduzioni);
                } catch (e) { }

                if (!!json) {
                  this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
                }
              }

              this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.initialized = true;
              });
            });

            this.setIsLoading(false);
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  initRiepilogoSondaggio() {
    this.risposteDomande = [];

    if (!!this.risposta.risposta) {
      try {
        let rispostaJSON = JSON.parse(this.risposta.risposta);

        if (rispostaJSON != null) {
          for (let property in rispostaJSON) {
            let domandaRisposta = new DomandaRispostaSondaggio();
            domandaRisposta.domanda = property;

            let value: string = rispostaJSON[property];

            if (!!value && value.length > 0 && value.length <= 5) {
              if (Array.from(value).every(x => x == '*')) {
                domandaRisposta.numeroStelle = value.length;
              } else if (Array.from(value).every(x => x == '')) {
                domandaRisposta.numeroFaccine = value.length;
              } else {
                domandaRisposta.risposta = value;
              }
            } else {
              domandaRisposta.risposta = value;
            }

            this.risposteDomande.push(domandaRisposta);
          }
        }
      } catch { }
    }

    this.sondaggioCompletato = true;
  }

  save() {
    this.setIsLoading(true);

    this.risposta.risposta = JSON.stringify(this.rispostaJSON);
    this.risposta.punteggio = this.domande.reduce((sum, current) => sum + current.numeroStelle, 0);
    this.risposta.stato = StatoSondaggio.Compilato;
    this.risposta.isNotificataAGestionale = true;

    if (this.domande.some(x => x.tipo == TipoDomandaSondaggio.VotoStelle || x.tipo == TipoDomandaSondaggio.VotoFaccine) && this.sogliaPunteggio > 0 && ((this.risposta.punteggio < this.sogliaPunteggio && !!this.sondaggio.testoFinalePunteggioBasso) || (this.risposta.punteggio >= this.sogliaPunteggio && !!this.sondaggio.testoFinalePunteggioAlto))) {
      if (this.risposta.punteggio < this.sogliaPunteggio) {
        this.testoFinale = this.sondaggio.testoFinalePunteggioBasso;
      } else {
        this.testoFinale = this.sondaggio.testoFinalePunteggioAlto;
      }
    }

    this.apiService.setRispostaSondaggio(this.risposta).subscribe(() => {
      this.initRiepilogoSondaggio();
      this.setIsLoading(false);
    });
  }

  onOptionToggle(index: number, value: string, checked: boolean, domanda: string) {
    if (checked) {
      this.domande[index].valoriArraySelected.push(value);
    } else {
      this.domande[index].valoriArraySelected.splice(this.domande[index].valoriArraySelected.indexOf(value), 1);
    }

    this.rispostaJSON[domanda] = this.domande[index].valoriArraySelected.join(', ');

    if (!this.domande[index].isObbligatoria || this.domande[index].valoriArraySelected.length > 0) {
      this.stepsCompleted[index] = true;
    } else {
      this.stepsCompleted[index] = false;
    }
  }

  onStelleSelected(index: number, domanda: string) {
    this.onVotoSelected(index, domanda, '*');
  }

  onFaccinaSelected(index: number, domanda: string) {
    this.onVotoSelected(index, domanda, '');
  }

  onVotoSelected(index: number, domanda: string, char: string) {
    this.domande[index].numeroStelle = this.domande[index].numeroStelleHover;
    this.stepsCompleted[index] = true;
    this.rispostaJSON[domanda] = char.repeat(this.domande[index].numeroStelle);
  }

  checkClienteStepCompleted() {
    if (!!this.risposta.cliente.nome && !!this.risposta.cliente.cognome && !!this.risposta.cliente.email && this.utility.validateEmail(this.risposta.cliente.email)) {
      this.clienteStepCompleted = true;
    } else {
      this.clienteStepCompleted = false;
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}