import { Component } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { ModalitaPagamento } from 'src/app/shared/models/bo.model';
import { TipoModalitaPagamento } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { PagamentoFormResultComponent } from '../pagamento-form-result/pagamento-form-result.component';

@Component({
  selector: 'app-pagamento-form-error',
  templateUrl: './pagamento-form-error.component.html',
  styleUrls: ['./pagamento-form-error.component.scss']
})
export class PagamentoFormErrorComponent extends PagamentoFormResultComponent {

  constructor(protected apiService: ApiService, protected route: ActivatedRoute) { super(apiService, route); }

  checkPagamento(modalitaPagamento: ModalitaPagamento, oidTransazione: number, _: ParamMap) {
    if (modalitaPagamento.tipo == TipoModalitaPagamento.XPay || modalitaPagamento.tipo == TipoModalitaPagamento.PayWay || modalitaPagamento.tipo == TipoModalitaPagamento.Holipay || modalitaPagamento.tipo == TipoModalitaPagamento.Siapay || modalitaPagamento.tipo == TipoModalitaPagamento.VirtualPay || modalitaPagamento.tipo == TipoModalitaPagamento.MyPOS) {
      if (!modalitaPagamento.isUsePost) {
        this.errorTransaction(+oidTransazione);
      }
    } else {
      this.errorTransaction(+oidTransazione);
    }
  }
}