import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { MyPageCameraRS, MyPagePropostaRS } from 'src/app/shared/models/dto';
import { TestoMyPage } from 'src/app/shared/models/enum';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-camere',
  templateUrl: './mypage-camere.component.html',
  styleUrls: ['./mypage-camere.component.scss']
})
export class MypageCamereComponent extends BaseComponent {

  @Input() camere: MyPageCameraRS[];
  @Input() scadenzaPreventivo: string;
  @Input() proposteSelected: MyPagePropostaRS[];
  @Input() isPrenotato: boolean;
  @Input() testoPrimaProposte: string;
  @Input() testoDopoProposte: string;
  @Input() isVisualizzaDettagli: boolean;
  @Input() isExpired: boolean;
  @Input() config: boolean;
  @Input() isVisualizzaDettaglioGiornaliero: boolean;
  @Input() isVisualizzaProposteEspanse: boolean;
  @Input() isVisualizzaDescrizionePoliticaCancellazione: boolean;
  @Input() isVisualizzaDescrizioneDettagliataTariffa: boolean;
  @Input() isVisualizzaInformazioniPropostaMyPage: boolean;

  @Output() continuaButtonEnabled: EventEmitter<any> = new EventEmitter();
  @Output() propostaSelected: EventEmitter<any> = new EventEmitter();
  @Output() onEditTestoClick: EventEmitter<TestoMyPage> = new EventEmitter();

  @ViewChild('camereTabGroup') camereTabGroup: ElementRef;

  public cameraTabIndex: number = 0;
  public enumTestoMyPage = TestoMyPage;

  constructor(public utility: UtilityService) {
    super();
  }

  onPropostaSelected(cameraTabIndex: number) {
    if (cameraTabIndex == -1) {
      this.continuaButtonEnabled.emit();
    } else {
      if (cameraTabIndex != this.cameraTabIndex) {
        this.cameraTabIndex = cameraTabIndex;
        this.camereTabGroup.nativeElement.scrollIntoView({ behavior: 'smooth' });

        setTimeout(() => {
          this.cameraTabIndex = cameraTabIndex;
        }, 200);
      }
    }

    this.propostaSelected.emit();
  }
}
