import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ReplaceTagsTemplateRQ, ReplaceTagsTemplateRS } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-template',
  templateUrl: './template.component.html',
  styleUrls: ['./template.component.scss']
})
export class TemplateComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: Subscription;
  public templateHtml: string;

  constructor(private route: ActivatedRoute,
    private apiService: ApiService,
    private router: Router) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);
    
    this.sub = this.route.queryParamMap.subscribe(x => {
      let uid = x.get('uid');
      let oidTemplate = x.get('oidTemplate');

      if (!!uid && !!oidTemplate && !isNaN(+oidTemplate)) {
        let rq = new ReplaceTagsTemplateRQ();
        rq.uidPreventivo = uid;
        rq.oidTemplate = +oidTemplate;

        this.apiService.replaceTagsTemplate(rq).subscribe((res: ReplaceTagsTemplateRS) => {
          this.templateHtml = res.data.testo;
          this.setIsLoading(false);
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}