import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { GetOfferRQ, GetOfferRS, OfferRS, PortaleOffer } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-offer',
  templateUrl: './offer.component.html',
  styleUrls: ['./offer.component.scss']
})
export class OfferComponent extends BaseComponent implements OnInit, OnDestroy {

  public initialized: boolean = false;
  public promozione: OfferRS;
  public portale: PortaleOffer = new PortaleOffer();
  public sub: Subscription;
  public arrivo: string;
  public partenza: string;
  public lingua: string;
  public promozioneValidaDalAl: string;
  public promozionePrenotabileDalAl: string;
  public backgroundImage: string;

  constructor(private containerService: ContainerService,
    private localizationService: LocalizationService,
    private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router,
    private deviceService: DeviceDetectorService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('offer-scrolling');

    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(x => {

      let oidPromozione = x.get('oidPromozione');
      this.lingua = x.get('lingua');

      if (!!oidPromozione && !isNaN(+oidPromozione)) {
        let rq = new GetOfferRQ();
        rq.oidPromozione = +oidPromozione;
        rq.isMobile = this.deviceService.isMobile();
        rq.lingua = this.lingua;

        this.apiService.getOffer(rq).subscribe((res: GetOfferRS) => {
          if (res.data.promozione != null) {
            this.promozione = res.data.promozione;
            this.portale = res.data.portale;

            if (!!this.promozione.validaDal || !!this.promozione.validaAl) {
              this.promozioneValidaDalAl = this.localizationService.getTranslationSync('booking.offer.promozioneValidaDalAl', { dal: !!this.promozione.validaDal ? this.promozione.validaDal : '---', al: !!this.promozione.validaAl ? this.promozione.validaAl : '---' });
            }

            if (!!this.promozione.prenotabileDal || !!this.promozione.prenotabileAl) {
              this.promozionePrenotabileDalAl = this.localizationService.getTranslationSync('booking.offer.promozionePrenotabileDalAl', { dal: !!this.promozione.prenotabileDal ? this.promozione.prenotabileDal : '---', al: !!this.promozione.prenotabileAl ? this.promozione.prenotabileAl : '---' });
            }

            this.utility.setLingua(this.lingua).then(() => {
              this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.initialized = true;
              });
            });

            this.setIsLoading(false);
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });
  }

  search() {
    window.location.href = window.location.origin + '/booking?oidPortale=' + this.portale.oid + '&arrivo=' + this.arrivo + '&partenza=' + this.partenza + '&codicePromozione=' + this.promozione.codice + (!!this.lingua ? ('&lingua=' + this.lingua) : '');
  }

  onArrivoChange() {
    setTimeout(() => {
      this.arrivo = this.getDate(this.arrivo);

      if (moment(this.arrivo) < moment().startOf('day')) {
        this.arrivo = this.getDate(moment());
      }

      if (this.partenza == null || moment(this.arrivo) >= moment(this.partenza)) {
        this.partenza = this.getDate(moment(this.arrivo).add(1, 'day'));
      }
    }, 200);
  }

  onPartenzaChange() {
    setTimeout(() => {
      this.partenza = this.getDate(this.partenza);

      if (moment(this.partenza) < moment().add(1, 'day').startOf('day')) {
        this.partenza = this.getDate(moment().add(1, 'day'));
      }

      if (this.arrivo == null || moment(this.arrivo) >= moment(this.partenza)) {
        this.arrivo = this.getDate(moment(this.partenza).subtract(1, 'day'));
      }
    }, 200);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}