import { AfterViewInit, Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { Meta } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { GoogleTagManagerService } from 'angular-google-tag-manager';
import * as Color from 'color';
import * as moment from 'moment';
import { DeviceDetectorService } from 'ngx-device-detector';
import { NgScrollbar } from 'ngx-scrollbar';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ImageEditComponent } from 'src/app/interfaces/image-edit/image-edit.component';
import { MessaggiSendComponent } from 'src/app/interfaces/messaggi-send/messaggi-send.component';
import { RichiesteAddComponent } from 'src/app/interfaces/richieste-add/richieste-add.component';
import { Cliente, TagCrm } from 'src/app/shared/models/bo.model';
import {
  DialogOptions,
  Dimension,
  GetMyPageRQ,
  GetMyPageRS,
  MyPageAlbergoRS,
  MyPageArticoloRS,
  MyPageCameraRS,
  MyPageContenutoRS,
  MyPageExtra,
  MyPageMessaggioRS,
  MyPagePagamentoRS,
  MyPagePortaleRS,
  MyPagePrenotazioneRS,
  MyPagePreventivoRS,
  MyPagePropostaRS,
  MyPageRecensioneRS,
  MyPageRichiestaRS,
  RinnovaRichiestaRQ,
  ScriptParameter,
  SetImmagineTagCrmRQ,
  SetImmagineTagCrmRS,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import {
  MittenteMessaggio,
  Pagina,
  StatoPreventivo,
  TestoMyPage,
  TipoImmagineTagCrm,
  TipoModalitaPagamento,
  VersioneMyPage,
} from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MypageArticoliEditComponent } from '../mypage-articoli-edit/mypage-articoli-edit.component';
import { MypageContenutiEditComponent } from '../mypage-contenuti-edit/mypage-contenuti-edit.component';
import { MypageContenutoEditComponent } from '../mypage-contenuto-edit/mypage-contenuto-edit.component';
import { MypageImmaginiHeaderEditComponent } from '../mypage-immagini-header-edit/mypage-immagini-header-edit.component';
import { MypageRifiutaPreventivoComponent } from '../mypage-rifiuta-preventivo/mypage-rifiuta-preventivo.component';
import { MypageTestoEditComponent } from '../mypage-testo-edit/mypage-testo-edit.component';
import { MypageService } from '../mypage.service';

declare let FB;

@Component({
  selector: 'app-mypage',
  templateUrl: './mypage.component.html',
  styleUrls: ['./mypage.component.scss', './mypage-v2.component.scss']
})
export class MypageComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  public initialized: boolean = false;
  public albergo: MyPageAlbergoRS = new MyPageAlbergoRS();
  public portale: MyPagePortaleRS = new MyPagePortaleRS();
  public richiesta: MyPageRichiestaRS;
  public messaggi: MyPageMessaggioRS[];
  public contenuti: MyPageContenutoRS[] = [];
  public camere: MyPageCameraRS[] = [];
  public modalitaPagamento: MyPagePagamentoRS[] = [];
  public allModalitaPagamento: MyPagePagamentoRS[] = [];
  public articoli: MyPageArticoloRS[] = [];
  public articoliVisible: MyPageArticoloRS[] = [];
  public preventivo: MyPagePreventivoRS;
  public prenotazione: MyPagePrenotazioneRS;
  public sub: Subscription;
  public sub1: Subscription;
  public recensioni: MyPageRecensioneRS[] = [];
  public enumMittente = MittenteMessaggio;
  public proposteSelected: MyPagePropostaRS[] = [];
  public totale: number = 0;
  public startSubject: Subject<void> = new Subject();
  public enumStatoPreventivo = StatoPreventivo;
  public isExpired: boolean;
  public extraSelected: MyPageExtra[][] = [];
  public oidSistemazioni: number[] = [];
  public ospiti: Cliente[] = [];
  public showPrenotazioneForm: boolean = false;
  public gtmService: GoogleTagManagerService;
  public condizioniPrenotazione: string;
  public condizioniContratto: string;
  public continuaButtonDisabled: boolean = true;
  public allProposteSelected: boolean = false;
  public sistemazioni: boolean;
  public enumVersioneMyPage = VersioneMyPage;
  public versioneMyPage: VersioneMyPage;
  public richiestaContainerHeight: number;
  public bulletPoints: string[] = [];
  public config: boolean = false;
  public uid: string;
  public enumTestoMyPage = TestoMyPage;
  public chatButtonVisible: boolean = false;
  public chatDialog: MatDialogRef<{}, any>;
  public tag: TagCrm = new TagCrm();
  public configPanelVisible: boolean = true;
  public setColorTimeout: NodeJS.Timeout;
  public isPrenotabile: boolean = true;
  public showMessageOnClose: boolean = true;
  public backgroundImage: string;
  public numeroVoucher: number;
  public scontoVoucher: number = 0;
  public sessionTimeout: number = 30 * 60000;
  public getMyPageRQ = new GetMyPageRQ();
  public immaginiHeader: string[] = [];
  public updateImageSize: Subject<void> = new Subject();

  @ViewChild('prenotazioneForm') prenotazioneForm: ElementRef;
  @ViewChild('datiForm') datiForm: ElementRef;
  @ViewChild('continuaButton') continuaButton: ElementRef;
  @ViewChild('richiestaInnerContainer') richiestaInnerContainer: ElementRef;
  @ViewChild('camereContainer') camereContainer: ElementRef;
  @ViewChild(NgScrollbar) scrollbarRef: NgScrollbar;

  @HostListener('window:beforeunload', ['$event'])
  beforeUnloadHandler(event) {
    if ((this.showPrenotazioneForm || (this.preventivo != null && this.preventivo.statoPreventivo == StatoPreventivo.InAttesaPagamento && this.prenotazione != null && this.preventivo.modalitaPagamento == null)) && this.showMessageOnClose) {
      if (event) {
        event.returnValue = '';
      }

      return '';
    }

    return null;
  }

  public readonly HOLIPAY_WIDGET_URL = 'https://api.holipay.com/holipay.js?hotel_id=';

  public readonly SCALAPAY_WIDGET_URL = 'https://cdn.scalapay.com/widget/v3/js/scalapay-widget.js';
  public readonly SCALAPAY_ESM_WIDGET_URL = 'https://cdn.scalapay.com/widget/v3/js/scalapay-widget.esm.js';

  constructor(private router: Router,
    private route: ActivatedRoute,
    private localizationService: LocalizationService,
    private cssLoaderService: CssLoaderService,
    public apiService: ApiService,
    public containerService: ContainerService,
    private deviceService: DeviceDetectorService,
    public utility: UtilityService,
    private mypageService: MypageService,
    private meta: Meta) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.sub = this.route.queryParamMap.subscribe(params => {
      this.uid = params.get('uid');

      this.config = params.get('mode') == 'config';

      let v = params.get('v');

      if (!!this.uid) {
        this.getMyPageRQ.uid = this.uid;
        this.getMyPageRQ.isMobile = this.deviceService.isMobile();
        this.getMyPageRQ.isHotel = params.get('isHotel') == 'true';

        this.apiService.getMyPage(this.getMyPageRQ).subscribe((res: GetMyPageRS) => {
          if (res.data != null && res.data.preventivo != null) {
            this.preventivo = res.data.preventivo;
            this.camere = res.data.camere;
            this.recensioni = res.data.recensioni;
            this.contenuti = res.data.contenuti;

            let imagePosition: 'left' | 'right' = 'left';

            for (let contenuto of this.contenuti) {
              if (!!contenuto.urlImmagine) {
                contenuto.imagePosition = imagePosition;
                imagePosition = imagePosition == 'left' ? 'right' : 'left';
              }
            }

            this.prenotazione = res.data.prenotazione;
            this.articoli = res.data.articoli;
            this.articoliVisible = this.articoli.slice(0, 3);
            this.condizioniPrenotazione = res.data.condizioniPrenotazione;
            this.condizioniContratto = res.data.condizioniContratto;
            this.bulletPoints = res.data.bulletPoints;
            this.allModalitaPagamento = res.data.modalitaPagamento;
            this.numeroVoucher = res.data.numeroVoucher;

            this.cssLoaderService.setColor(this.preventivo.coloreMyPage, this.preventivo.coloreSecondarioMyPage);

            this.tag = new TagCrm(this.preventivo.oidTagCrm);
            this.tag.nome = this.preventivo.nomeTagCrm;

            this.totale = 0;
            this.sistemazioni = false;

            let modalitaPagamento = [];

            if (this.camere != null && this.camere.length > 0) {
              for (let camera of this.camere) {
                let oidSistemazione: number = -1;

                this.proposteSelected.push(null);
                this.ospiti.push(new Cliente());

                if (this.articoli != null && this.articoli.length > 0) {
                  let extras: MyPageExtra[] = [];

                  for (let articolo of this.articoli) {
                    let extra: MyPageExtra = new MyPageExtra();
                    extra.oid = articolo.oid;
                    extra.nome = articolo.nome;
                    extra.quantita = 0;
                    extras.push(extra);
                  }

                  this.extraSelected.push(extras);
                }

                if (this.preventivo.statoPreventivo == StatoPreventivo.InAttesaPagamento) {
                  if (camera.proposte.length == 1) {
                    if (camera.proposte[0].proposte.length == 1) {
                      this.totale += camera.proposte[0].proposte[0].totaleScontato;

                      if (camera.proposte[0].proposte[0].modalitaPagamento != null && camera.proposte[0].proposte[0].modalitaPagamento.length > 0) {
                        modalitaPagamento = modalitaPagamento.concat(camera.proposte[0].proposte[0].modalitaPagamento.filter(x => modalitaPagamento.findIndex(y => y.paymentId == x.paymentId) == -1));
                      }

                      oidSistemazione = camera.proposte[0].proposte[0].oidSistemazione;
                    }

                    if (camera.proposte[0].tipoCamera.sistemazioni != null && camera.proposte[0].tipoCamera.sistemazioni.length > 0) {
                      this.sistemazioni = true;
                    }
                  }
                }

                if (camera.proposte.length == 0) {
                  this.isPrenotabile = false;
                }

                this.oidSistemazioni.push(oidSistemazione);
              }
            } else {
              this.isPrenotabile = false;
            }

            this.modalitaPagamento = modalitaPagamento;

            this.albergo = res.data.albergo;
            this.cssLoaderService.load(this.albergo.oid, Pagina.MyPage, res.data.portale?.oidUbicazioneIstat);

            if (res.data.portale != null) {
              this.portale = res.data.portale;
              this.versioneMyPage = res.data.portale.versioneMyPage;

              this.meta.updateTag({ property: 'og:site_name', content: res.data.portale.nomeStruttura });
              this.meta.updateTag({ property: 'og:title', content: res.data.portale.nomeStruttura });
              this.meta.updateTag({ property: 'og:description', content: res.data.portale.descrizione });
              this.meta.updateTag({ property: 'og:image', content: res.data.portale.urlImmagineLogo });

              if (!!this.portale.googleGTM) {
                this.gtmService = new GoogleTagManagerService(null, res.data.portale.googleGTM, null, null, null, null, null);

                this.gtmService.pushTag({
                  event: 'page',
                  pageName: 'mypage'
                });
              }

              if (!!this.portale.facebookPageId) {
                setTimeout(() => {
                  FB.init({ xfbml: true, version: 'v3.2' });
                }, 2000);
              }
            } else {
              this.versioneMyPage = VersioneMyPage.Versione1;
            }

            if (!!v) {
              if (v == '1') {
                this.versioneMyPage = VersioneMyPage.Versione1;
              } else if (v == '2') {
                this.versioneMyPage = VersioneMyPage.Versione2;
              }
            }

            if (this.versioneMyPage == VersioneMyPage.Versione2 && !this.portale.facebookPageId) {
              this.chatButtonVisible = true;
            }

            this.richiesta = res.data.richiesta;
            this.messaggi = res.data.messaggi;

            if (this.preventivo.dataScadenza != null && moment(this.getDate(this.preventivo.dataScadenza)).isBefore(moment().startOf('day'))) {
              this.isExpired = true;
            } else {
              this.isExpired = false;
            }

            this.setImmaginiHeader();

            if (!this.isExpired && this.preventivo.statoPreventivo != StatoPreventivo.Prenotato && this.preventivo.statoPreventivo != StatoPreventivo.Accettato && this.preventivo.statoPreventivo != StatoPreventivo.InAttesaPagamento) {
              if (this.camere.some(x => x.proposte.some(y => y.proposte.some(z => z.isHolipayDisponibile)))) {
                let holipay = this.allModalitaPagamento.find(x => x.tipo == TipoModalitaPagamento.Holipay);

                if (holipay != null) {
                  this.utility.loadExternalScript(this.HOLIPAY_WIDGET_URL + holipay.holipayHotelId);
                }
              }

              if (this.camere.some(x => x.proposte.some(y => y.proposte.some(z => z.isScalaPayDisponibile)))) {
                this.utility.loadExternalScript(this.SCALAPAY_ESM_WIDGET_URL, [new ScriptParameter('type', 'module')]);
                this.utility.loadExternalScript(this.SCALAPAY_WIDGET_URL, [new ScriptParameter('nomodule')]);
              }
            }

            if (!this.isExpired && this.preventivo.statoPreventivo != StatoPreventivo.Accettato && this.preventivo.statoPreventivo != StatoPreventivo.Prenotato && this.isPrenotabile) {
              setTimeout(() => {
                this.showError('booking.my-page.sessioneScaduta').then(() => window.location.reload());
              }, this.sessionTimeout);
            }

            this.utility.setLingua(this.preventivo.lingua).then(() => {
              if (!!res.data.traduzioni) {
                let json;

                try {
                  json = JSON.parse(res.data.traduzioni);
                } catch (e) { }

                if (!!json) {
                  this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
                }
              }

              if (this.versioneMyPage == VersioneMyPage.Versione1) {
                this.utility.setBackgroundImage(!!this.preventivo.urlImmagineSfondo ? this.preventivo.urlImmagineSfondo : this.portale.urlImmagineSfondo).then((image: string) => {
                  this.backgroundImage = image;
                  this.init();
                });
              } else {
                this.init();
              }
            });
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']).then(() => {
          this.setIsLoading(false);
        });
      }
    });

    this.sub1 = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      if (this.versioneMyPage == VersioneMyPage.Versione1) {
        if (!this.portale.facebookPageId && d.width < 992) {
          this.chatButtonVisible = true;
        } else {
          this.chatButtonVisible = false;
        }
      }

      if (this.richiestaInnerContainer != null) {
        setTimeout(() => {
          this.richiestaContainerHeight = this.richiestaInnerContainer.nativeElement.clientHeight;
          this.updateImageSize.next();
        }, 200);
      }
    });
  }

  ngAfterViewInit() {
    this.mypageService.setScrollbarRef(this.scrollbarRef);
  }

  init() {
    this.initialized = true;

    setTimeout(() => {
      if (this.versioneMyPage == VersioneMyPage.Versione1 && !this.portale.facebookPageId && this.containerService.dimension.width < 992) {
        this.chatButtonVisible = true;
      }

      this.startSubject.next();

      setTimeout(() => {
        if (this.richiestaInnerContainer != null) {
          this.richiestaContainerHeight = this.richiestaInnerContainer.nativeElement.clientHeight;
          this.updateImageSize.next();
        }
      }, 200);

      this.setIsLoading(false);
    }, 200);
  }

  onContinuaButtonEnabled() {
    this.continuaButtonDisabled = false;
    this.showPrenotazioneForm = true;

    /*if (this.continuaButton != null) {
      this.continuaButton.nativeElement.scrollIntoView({ behavior: 'smooth' });
    }*/

    setTimeout(() => {
      //DA CONTROLLARE
      this.containerService.windowResizeEvent.emit(this.containerService.dimension);
      this.onContinuaClick();
    }, 200);
  }

  onContinuaClick() {
    if (this.prenotazioneForm != null) {
      this.prenotazioneForm.nativeElement.scrollIntoView({ behavior: 'smooth' });
    }
  }

  onProseguiClick() {
    this.datiForm.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  onPropostaSelected() {
    let totale = 0;
    this.sistemazioni = false;
    this.allProposteSelected = true;
    let modalitaPagamento = [];

    let i = 0;

    for (let proposta of this.proposteSelected) {
      if (proposta != null) {
        totale += proposta.totaleScontato;

        if (this.extraSelected != null && this.extraSelected[i] != null) {
          for (let extra of this.extraSelected[i].filter(x => x.quantita > 0)) {
            totale += extra.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0) * extra.quantita;
          }
        }

        if (proposta.tipoCamera.sistemazioni != null && proposta.tipoCamera.sistemazioni.length > 0) {
          this.sistemazioni = true;
        }

        if (proposta.modalitaPagamento != null && proposta.modalitaPagamento.length > 0) {
          modalitaPagamento = modalitaPagamento.concat(proposta.modalitaPagamento.filter(x => modalitaPagamento.findIndex(y => y.paymentId == x.paymentId) == -1));
        }
      } else {
        this.allProposteSelected = false;
      }

      i++;
    }

    this.totale = totale;
    this.proposteSelected = Object.assign([], this.proposteSelected);
    this.modalitaPagamento = modalitaPagamento;
  }

  onExtrasSelectedChange() {
    let totale = 0;

    let i = 0;

    for (let proposta of this.proposteSelected.filter(x => x != null)) {
      totale += proposta.totaleScontato;

      if (this.extraSelected != null && this.extraSelected[i] != null) {
        for (let extra of this.extraSelected[i].filter(x => x.quantita > 0)) {
          extra.totale = extra.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0) * extra.quantita;
          totale += extra.totale;
        }
      }

      i++;
    }

    this.totale = totale;
  }

  editTesto(testoMyPage: TestoMyPage) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(MypageTestoEditComponent, dialogOptions);
    let component = <MypageTestoEditComponent>ref.componentInstance;
    component.getMyPageRQ = this.getMyPageRQ;
    component.oidPreventivo = this.preventivo.oid;
    component.oidTagCrm = this.preventivo.oidTagCrm;
    component.testoMypage = testoMyPage;
    component.dialogRef = ref;

    ref.afterClosed().subscribe((res: GetMyPageRS) => {
      if (res != null) {
        this.preventivo = res.data.preventivo;
      }
    });
  }

  editImmagineHeader() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(MypageImmaginiHeaderEditComponent, dialogOptions);
    let component = <MypageImmaginiHeaderEditComponent>ref.componentInstance;
    component.oidTagCrm = this.preventivo.oidTagCrm;
    component.urlImmagine = this.versioneMyPage == VersioneMyPage.Versione1 ? this.preventivo.urlHeaderMyPage : this.preventivo.urlImmagineSfondo;
    component.immagini = this.preventivo.immaginiTagCrm;
    component.versioneMyPage = this.versioneMyPage;

    ref.afterClosed().subscribe(() => {
      if (this.versioneMyPage == VersioneMyPage.Versione1) {
        this.preventivo.urlHeaderMyPage = component.urlImmagine;
      } else {
        this.preventivo.urlImmagineSfondo = component.urlImmagine;
      }

      this.preventivo.immaginiTagCrm = component.immagini;

      this.setImmaginiHeader();
    });
  }

  editImmagineSfondo() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '50vw';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(ImageEditComponent, dialogOptions);
    let component = <ImageEditComponent>ref.componentInstance;
    component._url = this.preventivo.urlImmagineSfondo;
    component.title = 'sbm.tabelle.tag-crm.immagineSfondo';
    component.blue = true;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(base64 => {
      if (base64 != null) {
        this.setIsLoading(true);

        let rq = new SetImmagineTagCrmRQ();
        rq.oid = this.preventivo.oidTagCrm;
        rq.tipoImmagine = TipoImmagineTagCrm.Sfondo;
        rq.base64 = base64;

        this.apiService.setImmagineTagCrm(rq).subscribe((res: SetImmagineTagCrmRS) => {
          if (res.data != null) {
            this.preventivo.urlImmagineSfondo = res.data.url;

            if (this.versioneMyPage == VersioneMyPage.Versione1) {
              this.utility.setBackgroundImage(!!this.preventivo.urlImmagineSfondo ? this.preventivo.urlImmagineSfondo : this.portale.urlImmagineSfondo).then((image: string) => {
                this.backgroundImage = image;
                this.setIsLoading(false);
              });
            } else {
              this.setIsLoading(false);
            }
          } else {
            this.setIsLoading(false);
          }
        });
      }
    });
  }

  editArticoli() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(MypageArticoliEditComponent, dialogOptions);
    let component = <MypageArticoliEditComponent>ref.componentInstance;
    component.getMyPageRQ = this.getMyPageRQ;
    component.oidAlbergo = this.albergo.oid;
    component.oidPreventivo = this.preventivo.oid;
    component.dialogRef = ref;

    ref.afterClosed().subscribe((res: GetMyPageRS) => {
      if (res != null) {
        this.articoli = res.data.articoli;
        this.articoliVisible = this.articoli.slice(0, 3);

        let extraSelected: MyPageExtra[][] = [];

        if (this.camere != null && this.camere.length > 0) {
          for (let camera of this.camere) {
            if (this.articoli != null && this.articoli.length > 0) {
              let extras: MyPageExtra[] = [];

              for (let articolo of this.articoli) {
                let extra: MyPageExtra = new MyPageExtra();
                extra.oid = articolo.oid;
                extra.nome = articolo.nome;
                extra.quantita = 0;
                extras.push(extra);
              }

              extraSelected.push(extras);
            }
          }
        }

        this.extraSelected = extraSelected;
      }
    });
  }

  editContenuti() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(MypageContenutiEditComponent, dialogOptions);
    let component = <MypageContenutiEditComponent>ref.componentInstance;
    component.getMyPageRQ = this.getMyPageRQ;
    component.oidAlbergo = this.albergo.oid;
    component.oidPreventivo = this.preventivo.oid;
    component.dialogRef = ref;

    ref.afterClosed().subscribe((res: GetMyPageRS) => {
      if (res != null) {
        this.contenuti = res.data.contenuti;

        let imagePosition: 'left' | 'right' = 'left';

        for (let contenuto of this.contenuti) {
          if (!!contenuto.urlImmagine) {
            contenuto.imagePosition = imagePosition;
            imagePosition = imagePosition == 'left' ? 'right' : 'left';
          }
        }
      }
    });
  }

  editContenuto(oidContenuto: number) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'mypage-dialog';
    dialogOptions.disableClose = true;

    let ref = this.showModalDialog(MypageContenutoEditComponent, dialogOptions);
    let component = <MypageContenutoEditComponent>ref.componentInstance;
    component.getMyPageRQ = this.getMyPageRQ;
    component.oidContenuto = oidContenuto;
    component.v2 = (this.versioneMyPage == VersioneMyPage.Versione2);
    component.dialogRef = ref;

    ref.afterClosed().subscribe((res: GetMyPageRS) => {
      if (res != null) {
        this.contenuti = res.data.contenuti;

        let imagePosition: 'left' | 'right' = 'left';

        for (let contenuto of this.contenuti) {
          if (!!contenuto.urlImmagine) {
            contenuto.imagePosition = imagePosition;
            imagePosition = imagePosition == 'left' ? 'right' : 'left';
          }
        }
      }
    });
  }

  openChat() {
    if (this.chatDialog == null) {
      let dialogOptions = new DialogOptions();
      dialogOptions.width = '370px';
      dialogOptions.panelClass = 'mypage-dialog-no-padding';
      dialogOptions.disableClose = true;
      dialogOptions.hasBackdrop = false;
      dialogOptions.dialogPosition = { bottom: '80px', right: '30px' };

      this.chatDialog = this.showModalDialog(MessaggiSendComponent, dialogOptions);
      let component = <MessaggiSendComponent>this.chatDialog.componentInstance;
      component._preventivo = this.preventivo;
      component.messaggi = this.messaggi;
      component.mittente = MittenteMessaggio.Cliente;
      component.nomeAlbergo = this.albergo.nome;
      component.nomePortale = this.portale.nomeStruttura;
      component.maxHeight = 500;
      component.booking = true;

      this.chatDialog.afterClosed().subscribe(() => {
        this.chatDialog = null;
      });
    } else {
      this.chatDialog.close();
      this.chatDialog = null;
    }
  }

  onTagCrmChange() {
    this.setIsLoading(true);

    this.apiService.getPreventivo(this.preventivo.oid).subscribe((res: WebApiResponse) => {
      res.data.tagCrm = this.tag;

      this.apiService.setPreventivo(res.data).subscribe(() => {
        window.location.reload();
      });
    });
  }

  onColoreChange() {
    this.setIsLoading(true);

    this.apiService.getTagCrm(this.preventivo.oidTagCrm).subscribe((res: WebApiResponse) => {
      if (res.data != null) {
        if (!!this.preventivo.coloreMyPage) {
          try {
            res.data.coloreMyPage = Color(this.preventivo.coloreMyPage).rgbNumber();
          } catch (e) { }
        } else {
          res.data.coloreMyPage = null;
        }

        this.apiService.updateTagCrm(res.data).subscribe(() => {
          this.cssLoaderService.setColor(this.preventivo.coloreMyPage, this.preventivo.coloreSecondarioMyPage);
          this.setIsLoading(false);
        });
      } else {
        this.setIsLoading(false);
      }
    });
  }

  onColoreSecondarioChange() {
    this.setIsLoading(true);

    this.apiService.getTagCrm(this.preventivo.oidTagCrm).subscribe((res: WebApiResponse) => {
      if (res.data != null) {
        if (!!this.preventivo.coloreSecondarioMyPage) {
          try {
            res.data.coloreSecondarioMyPage = Color(this.preventivo.coloreSecondarioMyPage).rgbNumber();
          } catch (e) { }
        } else {
          res.data.coloreSecondarioMyPage = null;
        }

        this.apiService.updateTagCrm(res.data).subscribe(() => {
          this.cssLoaderService.setColor(this.preventivo.coloreMyPage, this.preventivo.coloreSecondarioMyPage);
          this.setIsLoading(false);
        });
      } else {
        this.setIsLoading(false);
      }
    });
  }

  setColor() {
    if (this.setColorTimeout == null) {
      this.setColorTimeout = setTimeout(() => {
        this.cssLoaderService.setColor(this.preventivo.coloreMyPage, this.preventivo.coloreSecondarioMyPage);
        this.setColorTimeout = null;
      }, 50);
    }
  }

  scrollToCamere() {
    this.camereContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  rinnovaRichiesta() {
    this.setIsLoading(true);

    let rq = new RinnovaRichiestaRQ();
    rq.oidPreventivo = this.preventivo.oid;

    this.apiService.rinnovaRichiesta(rq).subscribe(rs => {
      this.setIsLoading(false);

      if (rs.data) {
        this.showInfo('booking.my-page.emailInviata');
      } else {
        this.showError('interfaces.email-send.erroreInvioEmail');
      }
    });
  }

  richiediNuovoPreventivo() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = '80vw';
    } else {
      dialogOptions.width = '95vw';
    }

    dialogOptions.minHeight = '80vh';
    dialogOptions.maxHeight = '95vh';
    dialogOptions.panelClass = 'mypage-dialog-no-padding';

    let ref = this.showModalDialog(RichiesteAddComponent, dialogOptions);
    let component = <RichiesteAddComponent>ref.componentInstance;
    component.dialogRef = ref;
    component.oidAlbergo = this.albergo.oid;
    component.oidPortale = this.portale != null ? this.portale.oid : -1;
    component.language = this.preventivo.lingua;
    component.booking = true;

    if (this.preventivo.cliente != null) {
      let cliente = new Cliente();
      cliente.nome = this.preventivo.cliente.nome;
      cliente.cognome = this.preventivo.cliente.cognome;
      cliente.email = this.preventivo.cliente.email;
      cliente.cellulare = this.preventivo.cliente.cellulare;
      component.cliente = cliente;
    }
  }

  rifiutaPreventivo() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '50vw';
    dialogOptions.panelClass = 'mypage-dialog';

    let ref = this.showModalDialog(MypageRifiutaPreventivoComponent, dialogOptions);
    let component = <MypageRifiutaPreventivoComponent>ref.componentInstance;
    component.oidPreventivo = this.preventivo.oid;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(x => {
      if (x) {
        window.location.reload();
      }
    });
  }

  showMoreArticoli() {
    this.articoliVisible = this.articoli.slice(0, this.articoliVisible.length + 3);
  }

  setImmaginiHeader() {
    let immaginiHeader: string[] = [];

    if (this.versioneMyPage == VersioneMyPage.Versione1) {
      if (!!this.preventivo.urlHeaderMyPage) {
        immaginiHeader.push(this.preventivo.urlHeaderMyPage);
      }

      if (this.preventivo.immaginiTagCrm != null && this.preventivo.immaginiTagCrm.length > 0) {
        immaginiHeader = immaginiHeader.concat(this.preventivo.immaginiTagCrm.filter(x => x.isAttiva && !!x.urlImmagine).sort((a, b) => a.ordinamento < b.ordinamento ? -1 : b.ordinamento < a.ordinamento ? 1 : 0).map(x => x.urlImmagine));
      }
    } else if (this.preventivo.immaginiTagCrm != null && this.preventivo.immaginiTagCrm.length > 0) {
      if (!!this.preventivo.urlImmagineSfondo) {
        immaginiHeader.push(this.preventivo.urlImmagineSfondo);
      } else if (!!this.portale.urlImmagineSfondo) {
        immaginiHeader.push(this.portale.urlImmagineSfondo);
      }

      immaginiHeader = immaginiHeader.concat(this.preventivo.immaginiTagCrm.filter(x => x.isAttiva && !!x.urlImmagine).sort((a, b) => a.ordinamento < b.ordinamento ? -1 : b.ordinamento < a.ordinamento ? 1 : 0).map(x => x.urlImmagine));
    }

    this.immaginiHeader = immaginiHeader;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();

    this.startSubject.complete();
    this.updateImageSize.complete();

    this.meta.updateTag({ property: 'og:site_name', content: null });
    this.meta.updateTag({ property: 'og:title', content: null });
    this.meta.updateTag({ property: 'og:description', content: null });
    this.meta.updateTag({ property: 'og:image', content: null });
  }
}