import { Component } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { ModalitaPagamento, Transazione } from 'src/app/shared/models/bo.model';
import { PaymentRQ, PaymentRS } from 'src/app/shared/models/dto';
import { TipoModalitaPagamento } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { PagamentoFormResultComponent } from '../pagamento-form-result/pagamento-form-result.component';

@Component({
  selector: 'app-pagamento-form-success',
  templateUrl: './pagamento-form-success.component.html',
  styleUrls: ['./pagamento-form-success.component.scss']
})
export class PagamentoFormSuccessComponent extends PagamentoFormResultComponent {

  constructor(protected apiService: ApiService, protected route: ActivatedRoute) { super(apiService, route); }

  checkPagamento(modalitaPagamento: ModalitaPagamento, oidTransazione: number, params: ParamMap) {
    if (((modalitaPagamento.tipo == TipoModalitaPagamento.PayWay || modalitaPagamento.tipo == TipoModalitaPagamento.ScalaPay) && !modalitaPagamento.isUsePost) || modalitaPagamento.tipo == TipoModalitaPagamento.Unicredit || modalitaPagamento.tipo == TipoModalitaPagamento.Hobex || (modalitaPagamento.tipo == TipoModalitaPagamento.XPay && params.get('3DS') == 'true')) {
      this.getTransaction(+oidTransazione).then((transazione: Transazione) => {
        if (transazione != null) {
          if (modalitaPagamento.tipo == TipoModalitaPagamento.PayWay) {
            this.verifyPayWayOrder(transazione, modalitaPagamento).then(() => {
              this.confirmTransaction(+oidTransazione);
            }, () => {
              this.errorTransaction(+oidTransazione);
            });
          } else if (modalitaPagamento.tipo == TipoModalitaPagamento.Unicredit) {
            this.verifyUnicreditOrder(transazione, modalitaPagamento).then(() => {
              this.confirmTransaction(+oidTransazione);
            }, () => {
              this.errorTransaction(+oidTransazione);
            });
          } else if (modalitaPagamento.tipo == TipoModalitaPagamento.Hobex) {
            this.verifyHobexOrder(transazione, modalitaPagamento).then(() => {
              this.confirmTransaction(+oidTransazione);
            }, () => {
              this.errorTransaction(+oidTransazione);
            });
          } else if (modalitaPagamento.tipo == TipoModalitaPagamento.ScalaPay) {
            this.captureScalaPayOrder(transazione, modalitaPagamento, params.get('orderToken')).then(() => {
              this.confirmTransaction(+oidTransazione);
            }, () => {
              this.errorTransaction(+oidTransazione);
            });
          } else if (modalitaPagamento.tipo == TipoModalitaPagamento.XPay) {
            let esito = params.get('esito');
            let nonce = params.get('xpayNonce');

            if (!!esito && esito.toLowerCase() == 'ok' && !!nonce) {
              let pagamento3DSRQ = PaymentRQ.getXPayPagamento3DSRQ(transazione.oidAlbergo, null, modalitaPagamento.oid, transazione.cliente, transazione.cliente?.lingua?.codice, transazione.prenotazione?.arrivo, transazione.prenotazione?.partenza, null, null, transazione.prenotazione?.noteP, transazione.prenotazione?.codicePromozione?.oid, transazione.prenotazione?.motivoViaggio?.oid, transazione.prenotazione?.caparra, null, null, null, modalitaPagamento.xPayAlias, modalitaPagamento.xPaySecretKey, transazione.importo, transazione.idTransazione, nonce, modalitaPagamento.isTest);

              this.apiService.payment(pagamento3DSRQ).subscribe((pagamento3DSRS: PaymentRS) => {
                if (pagamento3DSRS.success) {
                  this.confirmTransaction(+oidTransazione);
                } else {
                  this.errorTransaction(+oidTransazione, pagamento3DSRS.error);
                }
              });
            } else {
              this.errorTransaction(+oidTransazione, params.get('messaggio'));
            }
          }
        }
      });
    } else if (modalitaPagamento.tipo == TipoModalitaPagamento.XPay) {
      if (!modalitaPagamento.isUsePost) {
        let esito = params.get('esito');

        if (!!esito && esito.toLowerCase() == 'ok') {
          this.confirmTransaction(+oidTransazione);
        } else {
          this.errorTransaction(+oidTransazione, params.get('messaggio'));
        }
      }
    } else if (modalitaPagamento.tipo == TipoModalitaPagamento.Siapay) {
      if (!modalitaPagamento.isUsePost) {
        let result = params.get('RESULT');

        if (result == '00') {
          this.confirmTransaction(+oidTransazione);
        } else {
          this.errorTransaction(+oidTransazione);
        }
      }
    } else if (modalitaPagamento.tipo == TipoModalitaPagamento.Holipay || modalitaPagamento.tipo == TipoModalitaPagamento.VirtualPay || modalitaPagamento.tipo == TipoModalitaPagamento.MyPOS) {
      if (!modalitaPagamento.isUsePost) {
        this.confirmTransaction(+oidTransazione);
      }
    } else if (modalitaPagamento.tipo != TipoModalitaPagamento.PayWay) {
      this.confirmTransaction(+oidTransazione);
    }
  }

  verifyPayWayOrder(transazione: Transazione, modalitaPagamento: ModalitaPagamento): Promise<PaymentRS> {
    let promise = new Promise<PaymentRS>((resolve, reject) => {
      let rq = PaymentRQ.getPayWayVerifyRQ(transazione.oidAlbergo, null, modalitaPagamento.oid, transazione.cliente, transazione.cliente?.lingua?.codice, transazione.prenotazione?.arrivo, transazione.prenotazione?.partenza, null, null, transazione.prenotazione?.noteP, transazione.prenotazione?.codicePromozione?.oid, transazione.prenotazione?.motivoViaggio?.oid, transazione.prenotazione?.caparra, null, null, null, modalitaPagamento.payWayTerminalId, modalitaPagamento.payWaySecretKey, transazione.idTransazione, transazione.oid, modalitaPagamento.isTest);

      this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
        if (rs.success) {
          resolve(rs);
        } else {
          reject(rs.error);
        }
      });
    });

    return promise;
  }

  verifyUnicreditOrder(transazione: Transazione, modalitaPagamento: ModalitaPagamento): Promise<PaymentRS> {
    let promise = new Promise<PaymentRS>((resolve, reject) => {
      let rq = PaymentRQ.getUnicreditVerifyRQ(transazione.oidAlbergo, null, modalitaPagamento.oid, transazione.cliente, transazione.cliente?.lingua?.codice, transazione.prenotazione?.arrivo, transazione.prenotazione?.partenza, null, null, transazione.prenotazione?.noteP, transazione.prenotazione?.codicePromozione?.oid, transazione.prenotazione?.motivoViaggio?.oid, transazione.prenotazione?.caparra, null, null, null, modalitaPagamento.unicreditTerminalId, modalitaPagamento.unicreditSecretKey, transazione.idTransazione, transazione.oid, modalitaPagamento.isTest);

      this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
        if (rs.success) {
          resolve(rs);
        } else {
          reject(rs.error);
        }
      });
    });

    return promise;
  }

  verifyHobexOrder(transazione: Transazione, modalitaPagamento: ModalitaPagamento): Promise<PaymentRS> {
    let promise = new Promise<PaymentRS>((resolve, reject) => {
      let rq = PaymentRQ.getHobexVerifyRQ(transazione.oidAlbergo, null, modalitaPagamento.oid, transazione.cliente, transazione.cliente?.lingua?.codice, transazione.prenotazione?.arrivo, transazione.prenotazione?.partenza, null, null, transazione.prenotazione?.noteP, transazione.prenotazione?.codicePromozione?.oid, transazione.prenotazione?.motivoViaggio?.oid, transazione.prenotazione?.caparra, null, null, null, modalitaPagamento.hobexEntityId, modalitaPagamento.hobexAccessToken, transazione.idTransazione, modalitaPagamento.isTest);

      this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
        if (rs.success) {
          resolve(rs);
        } else {
          reject(rs.error);
        }
      });
    });

    return promise;
  }

  captureScalaPayOrder(transazione: Transazione, modalitaPagamento: ModalitaPagamento, token: string): Promise<PaymentRS> {
    let promise = new Promise<PaymentRS>((resolve, reject) => {
      if (!!token) {
        let rq = PaymentRQ.getScalaPayCaptureRQ(transazione.oidAlbergo, null, modalitaPagamento.oid, transazione.cliente, transazione.cliente?.lingua?.codice, transazione.prenotazione?.arrivo, transazione.prenotazione?.partenza, null, null, transazione.prenotazione?.noteP, transazione.prenotazione?.codicePromozione?.oid, transazione.prenotazione?.motivoViaggio?.oid, transazione.prenotazione?.caparra, null, null, null, modalitaPagamento.scalaPayApiKey, token, modalitaPagamento.isTest);

        this.apiService.payment(rq).subscribe((rs: PaymentRS) => {
          if (rs.success) {
            resolve(rs);
          } else {
            reject(rs.error);
          }
        });
      } else {
        reject();
      }
    });

    return promise;
  }
}