import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  BookingInput,
  BookingSolutionCancellationRS,
  BookingSolutionExtraRS,
  DialogOptions,
  ExtraBooking,
  PrenotazioneDetailsBooking,
  PrenotazioneDetailsOutput,
  PrenotazioneDetailsRoom,
} from 'src/app/shared/models/dto';
import { ModalitaCalcoloPrezzo, TipoDisponibilitaArticolo, TipoSupplemento } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { ArticoloDialogComponent } from '../booking-extra-select-summary/articolo-dialog/articolo-dialog.component';
import {
  CondizioniPrenotazioneDialogComponent,
} from '../condizioni-prenotazione-dialog/condizioni-prenotazione-dialog.component';
import {
  PoliticaCancellazioneDialogComponent,
} from './politica-cancellazione-dialog/politica-cancellazione-dialog.component';

@Component({
  selector: 'app-prenotazione-summary',
  templateUrl: './prenotazione-summary.component.html',
  styleUrls: ['./prenotazione-summary.component.scss']
})
export class PrenotazioneSummaryComponent extends BaseComponent {

  @Input() set prenotazioneDetails(value: BookingInput) {
    if (value != null && value.camerePax != null && value.extraSelected != null && value.tariffeTipiCamera != null && value.sistemazioni != null) {
      this._prenotazioneDetails = value;

      let roomCount: number = 0;

      for (let tariffaTipoCamera of value.tariffeTipiCamera) {
        for (let i2 = 0; i2 < tariffaTipoCamera.quantity; i2++) {
          roomCount++;
        }
      }

      if (value.camerePax.length == roomCount && (value.extraSelected.length == roomCount || value.extraSelected.length == 0) && value.sistemazioni.length == roomCount) {
        let oldRooms = this.rooms;

        this.rooms = [];
        this.totale = 0;
        this.totaleScontato = 0;

        for (let tariffaTipoCamera of value.tariffeTipiCamera) {
          if (tariffaTipoCamera.tariffa != null) {
            let totale = tariffaTipoCamera.tariffa.prezzo * tariffaTipoCamera.quantity;

            this.totale += totale;

            if (tariffaTipoCamera.tariffa.prezzoScontato > 0) {
              this.totaleScontato += tariffaTipoCamera.tariffa.prezzoScontato * tariffaTipoCamera.quantity;
            } else {
              this.totaleScontato += totale;
            }
          }
        }

        if (this.totaleScontato == this.totale) {
          this.totaleScontato = 0;
        }

        let i = 0;

        for (let tariffaTipoCamera of value.tariffeTipiCamera) {
          for (let i2 = 0; i2 < tariffaTipoCamera.quantity; i2++) {
            let room = new PrenotazioneDetailsRoom();
            room.tariffa = tariffaTipoCamera.tariffa;
            room.tipoCamera = tariffaTipoCamera.tipoCamera;
            room.oidSistemazione = value.sistemazioni[i];
            room.pax = value.camerePax[i];

            if (oldRooms.length > i && oldRooms[i].cliente != null) {
              room.cliente = oldRooms[i].cliente;
            } else {
              room.cliente = new PrenotazioneDetailsBooking();
            }

            room.extras = [];

            if (value.extraSelected[i] != null) {
              for (let extrasXType of value.extraSelected[i]) {
                if (extrasXType.extras != null) {
                  for (let extra of extrasXType.extras) {
                    if (extra.extra.giorni != null && extra.extra.giorni.length > 0) {
                      extra.extra.totale = extra.extra.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0);
                    } else {
                      //DA CONTROLLARE NIGHTS
                      extra.extra.totale = extra.extra.prezzo * (this.nights != null ? this.nights : value.notti);
                    }

                    extra.extra.totale *= extra.quantita;

                    room.extras.push(extra);

                    this.totale += extra.extra.totale;

                    if (!!this.totaleScontato) {
                      this.totaleScontato += extra.extra.totale;
                    }
                  }
                }
              }
            }

            if (tariffaTipoCamera.tariffa.extras != null) {
              for (let ex of tariffaTipoCamera.tariffa.extras) {
                let extra = new ExtraBooking();
                extra.quantita = ex.quantita;
                extra.extra = new BookingSolutionExtraRS();
                extra.extra.extraId = ex.extraId;
                extra.extra.nome = ex.nome;
                extra.extra.urlImmagine = ex.urlImmagine;
                extra.extra.prezzo = ex.prezzo;
                extra.extra.modalitaCalcoloPrezzo = ex.modalitaCalcoloPrezzo;
                extra.extra.isExtraAutomatico = true;
                extra.extra.isExtraIncluso = ex.isExtraIncluso;
                extra.extra.giorni = ex.giorni;
                extra.extra.isPerPersona = ex.isPerPersona;
                extra.extra.indexComponente = ex.indexComponente;
                extra.extra.tipoProduzione = ex.tipoProduzione;
                extra.extra.tipo = TipoSupplemento.Camera;

                if (ex.giorni != null && ex.giorni.length > 0) {
                  extra.extra.totale = ex.giorni.reduce((sum, current) => sum + current.prezzo, 0) * ex.quantita;
                } else {
                  extra.extra.totale = ex.prezzo * ex.quantita;
                }

                room.extras.push(extra);

                if (ex.tipoDisponibilitaArticolo != TipoDisponibilitaArticolo.DisponibilePrezzoNonCompresoStep1 && ex.modalitaCalcoloPrezzo != ModalitaCalcoloPrezzo.Tassa) {
                  this.totale += extra.extra.totale;

                  if (!!this.totaleScontato) {
                    this.totaleScontato += extra.extra.totale;
                  }
                }
              }
            }

            this.rooms.push(room);

            i++;
          }
        }

        if (value.scontoVoucher != null && value.scontoVoucher > 0) {
          this.totale = Math.max(this.totale - value.scontoVoucher, 0);

          if (!!this.totaleScontato) {
            this.totaleScontato = Math.max(this.totaleScontato - value.scontoVoucher, 0);
          }
        }

        let output = new PrenotazioneDetailsOutput();
        output.totale = this.totale;
        output.totaleScontato = this.totaleScontato;
        output.rooms = this.rooms;
        this.prenotazioneDetailsOutput.emit(output);
      }
    }
  };

  @Input() condizioniPrenotazione: string;
  @Input() from: string;
  @Input() to: string;
  @Input() nights: number;
  @Input() numeroPax: number;
  @Input() dayUse: boolean;
  @Input() mobileSummary: boolean = false;
  @Input() continuaButtonVisible: boolean;
  @Input() disabled: boolean;
  @Input() cancellazioni: BookingSolutionCancellationRS[];
  @Input() bulletPoints: string[];
  @Input() valuta: string;

  @Output() prenotazioneDetailsOutput: EventEmitter<PrenotazioneDetailsOutput> = new EventEmitter();
  @Output() back: EventEmitter<any> = new EventEmitter();
  @Output() continue: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();

  public rooms: PrenotazioneDetailsRoom[] = [];
  public totale: number = 0;
  public totaleScontato: number = 0;
  public enumTipoSupplemento = TipoSupplemento;
  public _prenotazioneDetails: BookingInput;

  constructor(private containerService: ContainerService,
    public utility: UtilityService) {
    super();
  }

  visualizzaCondizioniPrenotazione() {
    let dialogOptions = new DialogOptions();
    dialogOptions.panelClass = 'booking-dialog';

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
      dialogOptions.maxHeight = "50vh";
    } else {
      dialogOptions.width = "80vw";
    }

    let ref = this.showModalDialog(CondizioniPrenotazioneDialogComponent, dialogOptions);
    let component = <CondizioniPrenotazioneDialogComponent>ref.componentInstance;
    component.condizioniPrenotazione = this.condizioniPrenotazione;
  }

  visualizzaPoliticheCancellazione() {
    let dialogOptions = new DialogOptions();
    dialogOptions.panelClass = 'booking-dialog';

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
      dialogOptions.maxHeight = "50vh";
    } else {
      dialogOptions.width = "80vw";
    }

    let ref = this.showModalDialog(PoliticaCancellazioneDialogComponent, dialogOptions);
    let component = <PoliticaCancellazioneDialogComponent>ref.componentInstance;
    component.cancellazioni = this.cancellazioni;
  }

  openArticoloDetails(articolo: ExtraBooking, numeroPax: number) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'booking-dialog-no-padding';

    let ref = this.showModalDialog(ArticoloDialogComponent, dialogOptions);
    let component = <ArticoloDialogComponent>ref.componentInstance;
    component.articolo = articolo;
    component.numeroPax = numeroPax;
    component.valuta = this.valuta;
  }
}