import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import { MatLegacySelect as MatSelect } from '@angular/material/legacy-select';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingSolutionDayRateRS, Dimension, ExtraBooking } from 'src/app/shared/models/dto';
import { TipoApplicazioneSupplemento, TipoSupplemento } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-extra',
  templateUrl: './extra.component.html',
  styleUrls: ['./extra.component.scss']
})
export class ExtraComponent extends BaseComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() extra: ExtraBooking;
  @Input() multipleRooms: boolean;
  @Input() quantityToAdd: number;
  @Input() valuta: string;

  @Output() onExtraAdd: EventEmitter<number> = new EventEmitter();
  @Output() onGiornoSelected: EventEmitter<any> = new EventEmitter();

  @ViewChild('quantitySelect') quantitySelect: MatSelect;

  public hideImage: boolean = false;
  public showImageTop: boolean = false;
  public rowHeight: number = 0;
  public imageWidth: number = 0;
  public sub: Subscription;
  public updateImageSize: Subject<void> = new Subject();
  public initialized: boolean = false;
  public enumTipoSupplemento = TipoSupplemento;
  public showPrezzo: boolean;

  @ViewChild('row') row: ElementRef;
  @ViewChild('imageContainer') imageContainer: ElementRef;

  constructor(private containerService: ContainerService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      this.hideImage = true;

      setTimeout(() => {
        if ((d.width >= 1200 && d.width <= 1550) || d.width < 992) {
          this.showImageTop = true;
        } else {
          this.showImageTop = false;
        }

        setTimeout(() => {
          this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 1);
          this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
          this.hideImage = false;
          this.updateImageSize.next();
        }, 200);
      }, 200);
    });

    this.showPrezzo = this.extra.extra.giorni == null || this.extra.extra.giorni.length == 0 || !this.extra.extra.isVisualizzaGiorniWeb || this.extra.extra.giorni.every(x => x.prezzo == this.extra.extra.prezzo);
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if ((this.containerService.dimension.width >= 1200 && this.containerService.dimension.width <= 1550) || this.containerService.dimension.width < 992) {
        this.showImageTop = true;
      } else {
        this.showImageTop = false;
      }

      this.rowHeight = Math.floor(this.row.nativeElement.offsetHeight + 1);
      this.imageWidth = Math.floor(this.imageContainer.nativeElement.offsetWidth + 1);
      this.initialized = true;
    }, 200);
  }

  checkGiornoDisabled(giorni: BookingSolutionDayRateRS[], selected: boolean): boolean {
    return giorni.filter(x => x.selected).length <= 1 && selected;
  }

  onAggiungiClick() {
    if (this.multipleRooms) {
      this.onExtraAdd.emit();
    } else if (this.extra.extra.isApplicaATuttiComponenti == null) {
      this.onExtraAdd.emit(this.extra.quantita + 1);
    } else if (this.extra.extra.isApplicaATuttiComponenti == true) {
      let quantity = this.extra.quantita + this.quantityToAdd;
      this.onExtraAdd.emit(this.extra.extra.quantitaMassima <= 0 ? quantity : Math.min(quantity, this.extra.extra.quantitaMassima));
    } else if (this.extra.extra.isApplicaATuttiComponenti == false) {
      this.quantitySelect.open();
    }
  }

  getTipoExtra(): string {
    if (this.extra.extra.tipo == TipoSupplemento.Camera) {
      if (this.extra.extra.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.extra.extra.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'booking.perCameraSingoloGiorno';
      } else {
        return 'booking.perCamera';
      }
    } else {
      if (this.extra.extra.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.extra.extra.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'booking.perPersonaSingoloGiorno';
      } else {
        return 'booking.perPersona';
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.updateImageSize.complete();
  }
}