import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { Dimension } from 'src/app/shared/models/dto';
import { TestoMyPage } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MypageCamereComponent } from '../mypage-camere/mypage-camere.component';

@Component({
  selector: 'app-mypage-camere-v2',
  templateUrl: './mypage-camere-v2.component.html',
  styleUrls: ['./mypage-camere-v2.component.scss']
})
export class MypageCamereV2Component extends MypageCamereComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() bulletPoints: string[];
  @Input() config: boolean;

  @Output() onContinuaButtonClick: EventEmitter<any> = new EventEmitter();
  @Output() onEditTestoClick: EventEmitter<TestoMyPage> = new EventEmitter();

  public propostaIndex: number = 0;
  public prenotaButtonEnabled: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public enumTestoMyPage = TestoMyPage;
  public changeEnabled: boolean = true;
  public cursorStartX: number;
  public cursorStartY: number;
  public cursorEndX: number;
  public cursorEndY: number;
  public width: number;
  public startEndScroll: number;
  public roomLabelWidth: number;

  @ViewChild('prenotaButton') prenotaButton: ElementRef;
  @ViewChild('roomLabelsContainer') roomLabelsContainer: ElementRef;

  @ViewChildren('proposteContainer') proposteContainer: QueryList<ElementRef>;

  constructor(public utility: UtilityService, public containerService: ContainerService) {
    super(utility);
  }

  ngOnInit() {
    this.sub = this.continuaButtonEnabled.subscribe(() => {
      this.prenotaButtonEnabled = true;

      setTimeout(() => {
        this.onContinuaButtonClick.emit();
      }, 200);

      /*if (this.dimension.width < 1200) {
        this.prenotaButton.nativeElement.scrollIntoView({ block: 'end' });
      } else {
        this.camereTabGroup.nativeElement.scrollIntoView({ behavior: 'smooth' });
      }*/
    });

    this.sub1 = this.propostaSelected.subscribe(() => {
      if (this.proposteSelected.some(x => x == null)) {
        this.propostaIndex = 0;
      }
    });

    this.sub2 = this.containerService.windowResizeEvent.subscribe((dimension: Dimension) => {
      this.roomLabelWidth = dimension.width / 3.0;

      setTimeout(() => {
        this.calculateWidth();
        this.toIndex(this.propostaIndex);
      }, 500);
    });
  }

  ngAfterViewInit() {
    this.calculateWidth();
  }

  onCameraClick(index: number) {
    if (this.cameraTabIndex != index) {
      this.cameraTabIndex = index;
      this.propostaIndex = 0;
    }
  }

  previous() {
    if (this.propostaIndex > 0) {
      this.propostaIndex--;
    }
  }

  next() {
    if (this.propostaIndex < this.camere[this.cameraTabIndex].proposte.length - 1) {
      this.propostaIndex++;
    }
  }

  previousProposta() {
    if (this.changeEnabled && this.propostaIndex > 0) {
      this.changeEnabled = false;

      let proposteContainer = this.getProposteContainer();

      if (this.propostaIndex == this.camere[this.cameraTabIndex].proposte.length - 1) {
        proposteContainer.scrollTo({ top: 0, left: proposteContainer.scrollLeft - this.startEndScroll, behavior: 'smooth' });
      } else {
        proposteContainer.scrollTo({ top: 0, left: proposteContainer.scrollLeft - this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.propostaIndex--;
        this.changeEnabled = true;
      }, 500);
    }
  }

  nextProposta() {
    if (this.changeEnabled && this.propostaIndex < this.camere[this.cameraTabIndex].proposte.length - 1) {
      this.changeEnabled = false;

      let proposteContainer = this.getProposteContainer();

      if (this.propostaIndex == 0) {
        proposteContainer.scrollTo({ top: 0, left: proposteContainer.scrollLeft + this.startEndScroll, behavior: 'smooth' });
      } else {
        proposteContainer.scrollTo({ top: 0, left: proposteContainer.scrollLeft + this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.propostaIndex++;
        this.changeEnabled = true;
      }, 500);
    }
  }

  toIndex(index: number) {
    let proposteContainer = this.getProposteContainer();

    if (proposteContainer != null && this.changeEnabled) {
      this.changeEnabled = false;
      proposteContainer.scrollTo({ top: 0, left: index >= 1 ? this.startEndScroll + (this.width * (index - 1)) : 0, behavior: 'smooth' });

      setTimeout(() => {
        this.propostaIndex = index;
        this.changeEnabled = true;
      }, 500);
    }
  }

  getProposteContainer() {
    if (this.proposteContainer != null) {
      let element = this.proposteContainer.filter(x => x.nativeElement.classList.contains('proposteContainer' + this.cameraTabIndex));

      if (element.length > 0) {
        return element[0].nativeElement;
      }
    }

    return null;
  }

  checkGesture(iCamera: number) {
    if (this.camere[iCamera].proposte.length > 1) {
      if (Math.abs(this.cursorStartX - this.cursorEndX) > 50 && Math.abs(this.cursorStartY - this.cursorEndY) < 50) {
        if (this.cursorStartX < this.cursorEndX) {
          this.previousProposta();
        } else {
          this.nextProposta();
        }
      }
    }
  }

  calculateWidth() {
    let proposteContainer = this.getProposteContainer();

    if (proposteContainer != null) {
      this.width = (proposteContainer.offsetWidth * 70 / 100) + 16;
      this.startEndScroll = this.width - (proposteContainer.offsetWidth * 15 / 100) + 24;
    }
  }

  scrollToEndRoomLabels() {
    if (this.roomLabelsContainer != null) {
      this.roomLabelsContainer.nativeElement.scrollTo({ top: 0, left: this.roomLabelsContainer.nativeElement.scrollWidth, behavior: 'smooth' });
    }
  }

  scrollToStartRoomLabels() {
    if (this.roomLabelsContainer != null) {
      this.roomLabelsContainer.nativeElement.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    }
  }

  onSelectedTabChange() {
    this.propostaIndex = 0;

    if (this.cameraTabIndex >= 3) {
      this.scrollToEndRoomLabels();
    } else {
      this.scrollToStartRoomLabels();
    }
  }

  checkGestureCamereSelect() {
    if (Math.abs(this.cursorStartX - this.cursorEndX) > 50 && Math.abs(this.cursorStartY - this.cursorEndY) < 50) {
      if (this.cursorStartX < this.cursorEndX) {
        this.scrollToStartRoomLabels();
      } else {
        this.scrollToEndRoomLabels();
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
  }
}