import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { GiorniSelectComponent } from 'src/app/interfaces/giorni-select/giorni-select.component';
import {
  DialogOptions,
  MyPageArticoloGiornoRS,
  MyPageArticoloRS,
  MyPageExtra,
  MyPagePropostaRS,
} from 'src/app/shared/models/dto';
import { TipoApplicazioneSupplemento, TipoSupplemento } from 'src/app/shared/models/enum';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-mypage-extra',
  templateUrl: './mypage-extra.component.html',
  styleUrls: ['./mypage-extra.component.scss']
})
export class MypageExtraComponent extends BaseComponent implements OnInit {

  @Input() articolo: MyPageArticoloRS;

  @Input() set _proposteSelected(value: MyPagePropostaRS[]) {
    if (value != null && value.length > 0) {
      this.proposteSelected = value;

      if (this.initialized) {
        this.init();
      }
    }
  };

  @Input() set _extraSelected(value: MyPageExtra[][]) {
    if (value != null && value.length > 0) {
      this.extraSelected = value;

      if (this.initialized) {
        this.init();
      }
    }
  };

  @Output() onQuantitaGiorniChange: EventEmitter<any> = new EventEmitter();

  public proposteSelected: MyPagePropostaRS[] = [];
  public extraSelected: MyPageExtra[][] = [];
  public quantities: number[][] = [];
  public extras: MyPageExtra[] = [];
  public enumTipoSupplemento = TipoSupplemento;
  public initialized: boolean = false;

  constructor(public utility: UtilityService, public containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    this.initialized = true;
    this.init();
  }

  init() {
    this.extras = [];
    this.quantities = [];

    let cameraIndex = 0;

    for (let camera of this.extraSelected) {
      let extra: MyPageExtra = this.utility.findItemByOid(camera, this.articolo.oid);
      extra.quantita = 0;

      let nights = this.articolo.tipo == null ? 1 : Math.max(moment(this.getDate(this.proposteSelected[cameraIndex].partenza)).diff(moment(this.getDate(this.proposteSelected[cameraIndex].arrivo)), 'days'), 1);

      extra.giorni = [];

      for (let i = 0; i < nights; i++) {
        if (this.articolo.tipoApplicazione == null || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.TuttiIGiorni || (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno && i == 0) || (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno && i == nights - 1)) {
          let giorno = new MyPageArticoloGiornoRS();
          giorno.data = this.getDate(moment(this.getDate(this.proposteSelected[cameraIndex].arrivo)).add(i, 'day'));
          giorno.selected = true;
          giorno.prezzo = this.articolo.prezzo;
          extra.giorni.push(giorno);
        }
      }

      this.extras.push(extra);

      this.quantities.push([]);

      let quantitaMassima = this.getQuantitaMassima(cameraIndex);
      let numeroPax = this.proposteSelected[cameraIndex].numeroAdulti + this.proposteSelected[cameraIndex].numeroBambini;

      for (let i = 0; i <= (this.articolo.isApplicaATuttiComponenti == true ? Math.ceil(quantitaMassima / numeroPax) : quantitaMassima); i++) {
        this.quantities[cameraIndex].push((this.articolo.isApplicaATuttiComponenti == true ? i * numeroPax : i) * extra.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0));
      }

      cameraIndex++;
    }
  }

  openGiornoSelect(iCamera: number) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width < 992) {
      dialogOptions.width = "80vw";
    } else {
      dialogOptions.width = "50vw";
    }

    let ref = this.showModalDialog(GiorniSelectComponent, dialogOptions);
    let component = <GiorniSelectComponent>ref.componentInstance;
    component.giorni = JSON.parse(JSON.stringify(this.extras[iCamera].giorni));
    component.dialogRef = ref;

    ref.afterClosed().subscribe((giorni: MyPageArticoloGiornoRS[]) => {
      if (giorni != null) {
        let i = 0;

        for (let giorno of giorni) {
          this.extras[iCamera].giorni[i].selected = giorno.selected;
          i++;
        }

        i = 0;

        for (let prezzo of this.quantities[iCamera]) {
          this.quantities[iCamera][i] = (i * this.extras[iCamera].giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0));
          i++;
        }

        if (this.extras[iCamera].quantita > 0) {
          this.onQuantitaGiorniChange.emit();
        }
      }
    });
  }

  getQuantitaMassima(iCamera: number): number {
    return this.articolo.quantitaMassima > 0 ? this.articolo.quantitaMassima : this.articolo.isApplicaATuttiComponenti == true ? this.getNumeroPax(iCamera) * 10 : 10;
  }

  getNumeroPax(iCamera: number): number {
    return this.proposteSelected[iCamera].numeroAdulti + this.proposteSelected[iCamera].numeroBambini;
  }

  getQuantity(iExtra: number, iQuantity: number): number {
    let isApplicaATuttiComponenti: boolean = this.articolo.isApplicaATuttiComponenti;
    let quantita: number = iQuantity * this.getNumeroPax(iExtra);
    return isApplicaATuttiComponenti == true ? Math.min(quantita, this.getQuantitaMassima(iExtra)) : iQuantity;
  }

  getTipoExtra(): string {
    if (this.articolo.tipo == null) {
      return 'booking.my-page.perCameraExtra';
    } else if (this.articolo.tipo == TipoSupplemento.Camera) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'booking.my-page.perCameraSingoloGiorno';
      } else {
        return 'booking.my-page.perCamera';
      }
    } else if (this.articolo.tipo == TipoSupplemento.Persona) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'booking.my-page.perPersonaSingoloGiorno';
      } else {
        return 'booking.my-page.perPersona';
      }
    } else {
      return '';
    }
  }
}