import { Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { BookingExtraInput, ExtraBooking } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-booking-extra-select',
  templateUrl: './booking-extra-select.component.html',
  styleUrls: ['./booking-extra-select.component.scss']
})
export class BookingExtraSelectComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() scroll: Subject<void>;
  @Input() set _bookingExtraInput(value: BookingExtraInput) {
    if (value != null && value.extras != null && value.extraSelected != null && value.vociAddebito != null) {
      this.bookingExtraInput = value;

      this.checkNoExtrasSelected();

      if (!this.initialized) {
        this.noExtras = true;

        for (let extra of this.bookingExtraInput.extras) {
          if (extra.extras.length > 0) {
            this.noExtras = false;
            break;
          }
        }

        this.setIsLoading(false);

        this.initialized = true;
      }
    }
  }

  @Input() condizioniPrenotazione: string;
  @Input() descrizioneExtra: string;
  @Input() valuta: string;

  @Output() onExtraSelected: EventEmitter<any> = new EventEmitter();
  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() verificaCodicePromozionale: EventEmitter<any> = new EventEmitter();
  @Output() noExtraSelectedEvent: EventEmitter<boolean> = new EventEmitter();

  @ViewChild('container') container: ElementRef;

  public bookingExtraInput: BookingExtraInput = new BookingExtraInput();
  public voceAddebitoOid: number = -1;
  public noExtras: boolean = false;
  public noExtraSelected: boolean = true;
  public initialized: boolean = false;
  public sub: Subscription;

  constructor(public utility: UtilityService,
    public containerService: ContainerService) {
    super();
  }

  ngOnInit() {
    this.sub = this.scroll.subscribe(() => {
      if (this.containerService.dimension.width < 992) {
        setTimeout(() => {
          if (this.container != null) {
            this.container.nativeElement.scrollIntoView({ behavior: 'smooth' });
          }
        }, 500);
      } else {
        this.containerService.scrollToTop();
      }
    });
  }

  onExtraAdd(extra: ExtraBooking) {
    extra.quantita++;
    this.onChange.emit();
    this.verificaCodicePromozionale.emit();
  }

  onFilter() {
    if (this.voceAddebitoOid != -1) {
      let filteredExtras = this.bookingExtraInput.extras.filter(x => x.voceAddebito.debitEntryId == this.voceAddebitoOid);
      this.noExtras = filteredExtras.length == 0 || filteredExtras[0].extras.length == 0;
    } else {
      this.noExtras = true;

      for (let extra of this.bookingExtraInput.extras) {
        if (extra.extras.length > 0) {
          this.noExtras = false;
          break;
        }
      }
    }
  }

  checkNoExtrasSelected() {
    this.noExtraSelected = true;
    this.noExtraSelectedEvent.emit(this.noExtraSelected);

    for (let extra of this.bookingExtraInput.extraSelected) {
      if (extra.quantita > 0) {
        this.noExtraSelected = false;
        this.noExtraSelectedEvent.emit(this.noExtraSelected);
        break;
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}