import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
    BookingSolutionDayRateRS,
    BookingSolutionRoomRQ,
    ExtraBooking,
    TariffaTipoCameraBooking,
} from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-extra-select-modal',
  templateUrl: './extra-select-modal.component.html',
  styleUrls: ['./extra-select-modal.component.scss']
})
export class ExtraSelectModalComponent extends BaseComponent implements OnInit {

  @Input() extras: ExtraBooking[];
  @Input() tariffeTipiCamera: TariffaTipoCameraBooking[];
  @Input() camerePax: BookingSolutionRoomRQ[];
  @Input() nights: number;
  @Input() valuta: string;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public headers: string[] = [];
  public quantities: number[][] = [];

  constructor(public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    this.calculatePrices();
  }

  calculatePrices() {
    if (this.extras != null && this.extras.length > 0) {
      let prezzo = this.extras[0].extra.prezzo;

      let index = 0;
      this.headers = [];
      this.quantities = [];

      for (let tariffaTipoCamera of this.tariffeTipiCamera) {
        for (let i = 0; i < tariffaTipoCamera.quantity; i++) {
          this.headers.push(tariffaTipoCamera.tipoCamera.nome + '-' + tariffaTipoCamera.tariffa.descrizione);
          this.quantities.push([]);

          let quantitaMassima = this.getQuantitaMassima(index);
          let numeroPax = this.camerePax[index].getNumeroPax();

          for (let i1 = 0; i1 <= (this.extras[index].extra.isApplicaATuttiComponenti == true ? Math.ceil(quantitaMassima / numeroPax) : quantitaMassima); i1++) {
            let totale = 0;

            if (this.extras[index] != null && this.extras[index].extra.giorni != null && this.extras[index].extra.giorni.length > 0) {
              totale = this.extras[index].extra.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0);
            } else {
              totale = prezzo * this.nights;
            }

            this.quantities[index].push((this.extras[index].extra.isApplicaATuttiComponenti == true ? i1 * numeroPax : i1) * totale);
          }

          index++;
        }
      }
    }
  }

  checkGiornoDisabled(giorni: BookingSolutionDayRateRS[], selected: boolean): boolean {
    return giorni.filter(x => x.selected).length <= 1 && selected;
  }

  getQuantitaMassima(iCamera: number): number {
    return this.extras[iCamera].extra.quantitaMassima > 0 ? this.extras[iCamera].extra.quantitaMassima : this.extras[iCamera].extra.isApplicaATuttiComponenti == true ? this.camerePax[iCamera].getNumeroPax() * 10 : 10;
  }

  getQuantity(iExtra: number, iQuantity: number): number {
    let isApplicaATuttiComponenti: boolean = this.extras[iExtra].extra.isApplicaATuttiComponenti;
    let quantita: number = iQuantity * this.camerePax[iExtra].getNumeroPax();
    return isApplicaATuttiComponenti == true ? Math.min(quantita, this.getQuantitaMassima(iExtra)) : iQuantity;
  }
}