import { AfterViewInit, Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/core/local-storage.service';

import { BaseComponent } from '../../base-component/base-component';

@Component({
  selector: 'app-payment-form',
  templateUrl: './payment-form.component.html',
  styleUrls: ['./payment-form.component.scss']
})
export class PaymentFormComponent extends BaseComponent implements OnInit, AfterViewInit {

  constructor(private localStorage: LocalStorageService) { super(); }

  ngOnInit() {
    this.setIsLoading(true);
  }

  ngAfterViewInit() {
    setTimeout(() => {
      let paymentForm: string = this.localStorage.get('paymentForm');
      
      this.localStorage.remove('paymentForm');

      if (!!paymentForm) {
        document.open();
        document.write(paymentForm);
        document.close();
      }
    }, 200);
  }
}